import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { FeatureEvent } from '@trident/certified-events';
import {
  DataRequest,
  TwinDetailsClientType,
  TwinRelationship,
  getLogger,
  useDebugLogging,
  useTwinDetailsClient,
} from '@do/core';
import {
  ITwinRelatedTwinsPanelProps,
  ITwinRelatedTwinsPanelState,
} from './TwinRelatedTwinsPanel.types';
import { useTwinsExplorerRouteProps } from '../../../../hooks/routeProps/useTwinsExplorerRouteProps';
import {
  ExploreTwinDetailsActivity,
  TwinVisualizationFeature,
} from '../../../../models/TwinVisualizationCertifiedEvents';
import { useNavigation } from '@do/core';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'TwinRelatedTwinsPanel.state';

export const useTwinRelatedTwinsPanelState = (
  props: ITwinRelatedTwinsPanelProps,
): ITwinRelatedTwinsPanelState => {
  // contexts

  // state
  const [data, setData] = useState<DataRequest<TwinRelationship[]>>({
    state: 'uncalled',
  });

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);
  const [explorerRouteProps] = useState(useTwinsExplorerRouteProps());
  const detailsUrl = useRef(`${explorerRouteProps.twinsPath}/details`);
  const navigation = useNavigation();

  // callbacks
  const client = useTwinDetailsClient();
  const navigateToDetails = useCallback(
    (twinId: string) => {
      navigation?.navigate({ path: `${detailsUrl.current}/${twinId}` });
    },
    [navigation],
  );

  // side effects
  const loadRelationships = useCallback(
    async (
      client: TwinDetailsClientType,
      twinId: string,
      logConfig: Partial<FeatureEvent>,
    ) => {
      if (data.state === 'pending') {
        const relationships = await client.getRelationships(twinId, logConfig);
        setData(relationships);
      }
    },
    [data.state],
  );

  useEffect(() => {
    if (props.twinId) {
      if (client && data.state === 'uncalled') {
        setData({ state: 'pending' });
      }
      if (client && data.state === 'pending') {
        loadRelationships(client, props.twinId, {
          featureName: TwinVisualizationFeature.ExploreTwinDetails,
          activityName: ExploreTwinDetailsActivity.RelatedTwin,
        });
      }
    }
  }, [client, data.state, loadRelationships, props.twinId]);

  // result
  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state: data,
    });

  return useMemo(() => {
    return {
      data,
      navigateToDetails,
    };
  }, [navigateToDetails, data]);
};
