import React, { Suspense, lazy, useCallback, useEffect, useRef } from 'react';
import {
  IGraphRenderProps,
  DefaultNavigationOptions,
  DefaultPanZoomOptions,
  NodeActions,
  ExtendedCytoscape,
} from './Graph.types';
import { Spinner } from '@fluentui/react-components';
import { useId } from '@fluentui/react-hooks';
import navigator from 'cytoscape-navigator'; // TODO: lazy load these extensions too
const panzoom = require('cytoscape-panzoom'); // TODO: instead of using pan zoom extension we can customly implement it using cytoscape ref instance's native .pan, .zoom and .center methods
const contextMenus = require('cytoscape-context-menus'); // TODO: instead of using context menus extension we can customly implement it using node on click callbacks
import 'cytoscape-context-menus/cytoscape-context-menus.css'; // TODO: override styles as needed
import 'cytoscape-panzoom/cytoscape.js-panzoom.css'; // TODO: instead of using their style, we can use fluentui icons with custom implementation of cytoscape-panzoom
import 'cytoscape-panzoom/font-awesome-4.0.3/css/font-awesome.min.css'; // TODO: instead of using font-awesome we can use fluentui icons with custom implementation of cytoscape-panzoom
import './Graph.render.css'; // alternatively you can import from cytoscape-navigator/cytoscape.js-navigator.css.css
import { strings, t } from '@do/localization';

const CytoscapeComponent = lazy(() => import('react-cytoscapejs'));

const setupCytoscape = (cyModuleRef: any) => {
  // register extensions
  if (typeof cyModuleRef.current('core', 'navigator') === 'undefined') {
    navigator(cyModuleRef.current);
  }
  if (typeof cyModuleRef.current('core', 'panzoom') !== 'function') {
    panzoom(cyModuleRef.current);
  }
  if (typeof cyModuleRef.current('core', 'contextMenus') !== 'function') {
    contextMenus(cyModuleRef.current);
  }
};

export const useGraphRender: React.FC<IGraphRenderProps> = (props) => {
  const { state, styles } = props;

  const cyModuleRef = useRef<any>();
  const cyRef = useRef<cytoscape.Core>();
  const navigatorId = useId('navigator');

  const getDefaultContextMenuOptions = useCallback(
    (
      cy: any,
      callback: (selectedTwinId: string, action: NodeActions) => void,
    ) => {
      return {
        evtType: 'cxttap',
        menuItems: [
          {
            id: 'details',
            content: t(strings.explorer.twins.graph.viewTwinDetails),
            tooltipText: t(strings.explorer.twins.graph.viewTwinDetails),
            selector: 'node, edge',
            onClickFunction: function () {
              alert('Show details!'); // TODO: hook twin or relationship details modal call here
            },
            hasTrailingDivider: true,
          },
          {
            id: 'expand',
            content: t(strings.explorer.twins.graph.expandNode),
            selector: 'node',
            onClickFunction: function (event: any) {
              callback(event.target.data('id'), NodeActions.expand);
            },
            hasTrailingDivider: true,
          },
          {
            id: 'collapse',
            content: t(strings.explorer.twins.graph.collapseNode),
            selector: 'node',
            onClickFunction: function (event: any) {
              callback(event.target.data('id'), NodeActions.collapse);
            },
            hasTrailingDivider: true,
          },
        ],
      };
    },
    [],
  );

  useEffect(() => {
    async function fetchCyModule() {
      // lazy load cytoscape module
      await import('cytoscape').then((module) => {
        cyModuleRef.current = module.default;
        setupCytoscape(cyModuleRef);
      });
    }

    if (!cyModuleRef.current) {
      fetchCyModule();
    }
  }, []);

  return (
    <div className={styles.root}>
      <Suspense fallback={<Spinner className={styles.spinner} />}>
        <CytoscapeComponent
          elements={state.elements}
          className={styles.graph}
          layout={state.layout}
          stylesheet={state.cytoscapeStylesheet}
          cy={(cy) => {
            // initialize the Cytoscape instance once
            if (cyRef.current !== cy) {
              cyRef.current = cy;

              cy.navigator({
                ...DefaultNavigationOptions,
                container: `#${navigatorId}`,
              });

              (cy as ExtendedCytoscape).panzoom(DefaultPanZoomOptions);

              cy.contextMenus(
                getDefaultContextMenuOptions(
                  cyRef.current,
                  state.handleExpandCollapse,
                ),
              );

              cy.on('add', 'node', () => {
                cy.layout(state.layout).run(); // make sure that the layout is run after adding a new node
              });
            }
          }}
        />
        <div className={styles.navigatorContainer}>
          <div
            id={navigatorId}
            className={styles.navigator}
            role='presentation'
          />
        </div>
      </Suspense>
    </div>
  );
};
