import React from 'react';
import { ITwinEventsPanelProps } from './TwinEventsPanel.types';
import { useTwinEventsPanelRender } from './TwinEventsPanel.render';
import { useClassNames } from './TwinEventsPanel.styles';
import { useTwinEventsPanelState } from './TwinEventsPanel.state';

const MODULE_NAME = 'TwinEventsPanel';

export const TwinEventsPanel: React.FC<ITwinEventsPanelProps> = (_props) => {
  // get state
  const state = useTwinEventsPanelState(_props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useTwinEventsPanelRender({ state, styles });
};

TwinEventsPanel.displayName = MODULE_NAME;
