import { PageStyles, PageClasses } from './Page.types';
import { makeStyles, shorthands } from '@fluentui/react-components';
import { tokens } from '@fluentui/react-theme';

const footerHeight = 78;
const maxHeight = `100vh`;
const ribbonHeight = 40;

export const useClassNames: () => PageStyles = makeStyles<PageClasses>({
  root: {
    display: 'flex',
    minHeight: `calc(${maxHeight} - ${footerHeight}px)`,
    maxHeight: `calc(${maxHeight} - ${footerHeight}px)`,
    backgroundColor: tokens.colorNeutralBackground3,
    flexDirection: 'column',
    ...shorthands.padding('8px'),
    rowGap: '8px',
  },
  ribbon: {
    height: `${ribbonHeight}px`,
    backgroundColor: tokens.colorNeutralBackground3,
    ...shorthands.borderRadius(tokens.borderRadiusMedium),
    boxShadow: tokens.shadow4,
    ...shorthands.margin(0),
    justifyContent: 'space-between',
  },
  main: {
    backgroundColor: tokens.colorNeutralBackground1,
    ...shorthands.borderRadius(tokens.borderRadiusMedium),
    boxShadow: tokens.shadow4,
    ...shorthands.padding('8px'),
    ...shorthands.overflow('auto'),
    height: `calc(${maxHeight} - ${footerHeight}px)`,
  },
  mainWithRibbon: {
    height: `calc(${maxHeight} - ${footerHeight + ribbonHeight}px)`,
  },
});
