import React from 'react';
import { SteppedWizardStartStepRenderProps } from './SteppedWizardStartStep.types';
import { Checkbox, Image } from '@fluentui/react-components';
import { Step } from '../Step';
import { strings, t } from '@do/localization';
import { ExternalLink } from '../../ExternalLink';

const TestIds = {
  StepRoot: 'stepped-wizard-step',
};

export const useSteppedWizardStartStepRender: React.FC<
  SteppedWizardStartStepRenderProps
> = ({
  styles,
  title,
  subtitle,
  contentTitle,
  content,
  externalLink,
  externalLinkText,
  imageSrc,
  skipIntro,
  onSkipIntroChange,
  dataTestId,
}) => {
  return (
    <Step title={title}>
      <div className={styles.root} data-testid={dataTestId ?? TestIds.StepRoot}>
        <div className={styles.semibold}>
          <p className={styles.subtitle}>{subtitle}</p>
          <h2>{title}</h2>
        </div>
        <div className={styles.paragraphContainer}>
          <div className={styles.contentContainer}>
            <h3>{contentTitle}</h3>
            <p>{content}</p>
            <ExternalLink
              className={styles.semibold}
              akaShortLink={externalLink}
              text={externalLinkText}
            />
          </div>
          <div className={styles.image}>
            <Image src={imageSrc} width='100%'></Image>
          </div>
        </div>
        <Checkbox
          className={styles.checkbox}
          checked={skipIntro}
          label={t(strings.wizard.skip_intro)}
          onChange={onSkipIntroChange}
        />
      </div>
    </Step>
  );
};
