import React from 'react';
import { LineChartControlsRenderProps } from './LineChartControls.types';
import {
  SimpleDropdown,
  SimpleDropdownItem,
  DateTimeRangeInput,
} from '@do/components';
import { strings, t } from '@do/localization';
import {
  AggregationKeys,
  BinIntervalKeys,
} from '../../../../utils/TimeSeriesUtils';
import {
  Button,
  Dropdown,
  Field,
  Label,
  Option,
} from '@fluentui/react-components';
import { StringMap } from 'i18next';

export const getAggregationOptions = (
  t: (key: string, params?: StringMap) => string,
): SimpleDropdownItem[] => [
  {
    key: AggregationKeys.average,
    text: t(strings.explorer.time_series.controls.aggregation.options.average),
  },
  {
    key: AggregationKeys.maximum,
    text: t(strings.explorer.time_series.controls.aggregation.options.maximum),
  },
  {
    key: AggregationKeys.minimum,
    text: t(strings.explorer.time_series.controls.aggregation.options.minimum),
  },
];

export const getIntervalOptions = (
  t: (key: string, params?: StringMap) => string,
) => [
  {
    key: BinIntervalKeys.oneMinute,
    text: t(
      strings.explorer.time_series.controls.bin_intervals.options.one_minute,
    ),
  },
  {
    key: BinIntervalKeys.fiveMinutes,
    text: t(
      strings.explorer.time_series.controls.bin_intervals.options.five_minutes,
    ),
  },
  {
    key: BinIntervalKeys.fifteenMinutes,
    text: t(
      strings.explorer.time_series.controls.bin_intervals.options
        .fifteen_minutes,
    ),
  },
  {
    key: BinIntervalKeys.thirtyMinutes,
    text: t(
      strings.explorer.time_series.controls.bin_intervals.options
        .thirty_minutes,
    ),
  },
  {
    key: BinIntervalKeys.oneHour,
    text: t(
      strings.explorer.time_series.controls.bin_intervals.options.one_hour,
    ),
  },
  {
    key: BinIntervalKeys.oneDay,
    text: t(
      strings.explorer.time_series.controls.bin_intervals.options.one_day,
    ),
  },
];

export const useLineChartControlsRender: React.FC<
  LineChartControlsRenderProps
> = ({ state, styles }) => {
  return (
    <div className={styles.root}>
      <div className={styles.dropdownContainer}>
        <Field
          label={{
            children: (
              <Label weight='semibold'>
                {t(strings.time_range_controls.aggregation)}
              </Label>
            ),
          }}
        >
          <SimpleDropdown
            ariaLabel={t(
              strings.explorer.time_series.controls.aggregation.aria_label,
            )}
            items={getAggregationOptions(t)}
            setValue={state.onAggregationMethodChange}
            value={state.selectedAggregationMethod}
          />
        </Field>
      </div>
      <DateTimeRangeInput {...state.dateTimeRangeProps} />
      <div className={styles.dropdownContainer}>
        <Field
          label={{
            children: (
              <Label weight='semibold'>
                {t(strings.time_range_controls.bin_width)}
              </Label>
            ),
          }}
        >
          <Dropdown
            onOptionSelect={state.onBinIntervalChange}
            value={state.selectedBinInterval}
            selectedOptions={[state.binInterval]}
          >
            {getIntervalOptions(t).map((option) => (
              <Option key={option.key} value={option.key}>
                {option.text}
              </Option>
            ))}
          </Dropdown>
        </Field>
      </div>
      <div>
        <Button
          appearance='primary'
          onClick={state.onApplyClicked}
          disabled={!state.applyEnabled}
        >
          {t(strings.common.apply)}
        </Button>
      </div>
    </div>
  );
};
