import { useMemo } from 'react';
import type { ComponentProps } from '@fluentui/react-utilities';
import { getLogger, useDebugLogging } from '@do/core';
import { PageSlots, PageState } from './Page.types';
import { resolveShorthand } from '@fluentui/react-components';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'Page.state';

export const usePageState = (props: ComponentProps<PageSlots>): PageState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state: PageState = useMemo(() => {
    return {
      components: {
        root: 'div',
        ribbon: 'div',
        main: 'div',
      },
      root: {},
      ribbon: resolveShorthand(props.ribbon),
    };
  }, [props]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
