import React from 'react';
import { ErrorMessageRenderProps } from './ErrorMessage.types';
import DefaultSvg from '../../../assets/Error.svg';
import { t, strings } from '@do/localization';
import { Image } from '@fluentui/react-components';

export enum TestIds {
  Main = 'error-message-main',
}

export const useErrorMessageRender: React.FC<ErrorMessageRenderProps> = ({
  state,
  styles,
}) => {
  return (
    <div
      data-testid={state.testId ?? TestIds.Main}
      role='alert'
      className={styles.root}
    >
      <Image src={DefaultSvg} role='presentation' />
      <h2 className={styles.header}>{t(strings.common.error)}</h2>
      <span>{state.message}</span>
    </div>
  );
};
