import React from 'react';
import { IIconizedDropdownProps } from './IconizedDropdown.types';
import { useIconizedDropdownRender } from './IconizedDropdown.render';
import { useClassNames } from './IconizedDropdown.styles';
import { useIconizedDropdownState as useIconizedDropdownState } from './IconizedDropdown.state';

const MODULE_NAME = 'IconizedDropdown';

export const IconizedDropdown: React.FC<IIconizedDropdownProps> = (props) => {
  // get state
  const state = useIconizedDropdownState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useIconizedDropdownRender({ state, styles });
};

IconizedDropdown.displayName = MODULE_NAME;
