import React from 'react';
import { StepperRenderProps } from './Stepper.types';
import { tokens } from '@fluentui/react-components';
import { t, strings } from '@do/localization';

const iconAriaLabel = (status?: string) => {
  if (status === 'error') return t(strings.wizard.steps.status.error);
  if (status === 'success') return t(strings.wizard.steps.status.success);
  return undefined;
};

export const useStepperRender: React.FC<StepperRenderProps> = ({
  state,
  styles,
}) => {
  return (
    <div className={styles.root} role='list' aria-live='polite'>
      {state.steps.map((step, index) => {
        return (
          <div
            key={step.label}
            className={styles.item}
            role='listitem'
            aria-current={state.stepIndex === index ? 'step' : 'false'}
          >
            <div
              className={styles.icon}
              role='presentation'
              aria-label={iconAriaLabel(step.status)}
            >
              {index === state.stepIndex ? (
                <CurrentIcon />
              ) : step.status === 'error' ? (
                <ErrorIcon />
              ) : step.status === 'success' ? (
                <SuccessIcon />
              ) : (
                <IncompleteIcon />
              )}
              {index + 1 < state.steps.length ? <PipeIcon /> : null}
            </div>
            <div className={styles.label}>{step.label}</div>
          </div>
        );
      })}
    </div>
  );
};

const BaseIcon = (fill?: string, addIn?: React.ReactNode) => {
  return (
    <svg
      aria-hidden='true'
      role='presentation'
      width='16'
      height='16'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
    >
      <circle
        cx='8'
        cy='8'
        r='7.5'
        fill={fill ?? 'transparent'}
        strokeWidth={1}
        stroke={fill ?? tokens.colorNeutralForeground4}
      />
      {addIn}
    </svg>
  );
};
export const IncompleteIcon = () => {
  return BaseIcon();
};
export const CurrentIcon = () => {
  return BaseIcon(tokens.colorBrandStroke2Contrast);
};
export const ErrorIcon = () => {
  return BaseIcon(
    tokens.colorStatusDangerForeground3,
    <path
      d='M5.5 11L11 5.5 M5.5 5.5L11 11'
      strokeLinecap='round'
      strokeLinejoin='round'
      stroke={tokens.colorNeutralBackground1}
    />,
  );
};
export const SuccessIcon = () => {
  return BaseIcon(
    tokens.colorBrandForeground2,
    <path
      d='M5 8.5L7 10.5 M7 10.5L11 6'
      strokeLinecap='round'
      strokeLinejoin='round'
      stroke={tokens.colorNeutralBackground1}
    />,
  );
};
export const PipeIcon = () => {
  return (
    <svg
      aria-hidden='true'
      role='presentation'
      width='16'
      height='40'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
    >
      <rect
        x='7'
        y='0'
        width='2'
        height='40'
        fill={tokens.colorNeutralBackground1Pressed}
      />
    </svg>
  );
};
