import React from 'react';
import {
  ITwinDetailsPanelRenderProps,
  ITwinDetailsPanelStyles,
} from './TwinDetailsPanel.types';
import {
  Card,
  CardHeader,
  DataGrid,
  DataGridBody,
  DataGridCell,
  DataGridHeader,
  DataGridHeaderCell,
  DataGridRow,
  createTableColumn,
} from '@fluentui/react-components';
import { t, strings } from '@do/localization';
import { LabelValue, EmptyContents, ErrorMessage } from '@do/components';
import { Loading } from '@do/components';
import { DataRequest, TwinAttribute } from '@do/core';

export enum TestIds {
  Main = 'twin-details-overview',
}

export const useTwinDetailsPanelRender: React.FC<
  ITwinDetailsPanelRenderProps
> = ({ state, styles }) => {
  if (
    state.properties.state === 'uncalled' ||
    state.properties.state === 'pending'
  ) {
    return <Loading />;
  }
  if (state.properties.state === 'error') {
    return <ErrorMessage message={state.properties.errorMessage} />;
  }
  const properties = state.properties.data;
  return (
    <div data-testid={TestIds.Main} className={styles.root}>
      <Card>
        <CardHeader
          header={
            <h2>{t(strings.explorer.twin_details.details_panel.header)}</h2>
          }
        />
        <div className={styles.panelBody}>
          <div className={styles.column}>
            <LabelValue
              label={t(strings.explorer.twin_details.details_panel.name)}
              value={properties.name}
            />
            <LabelValue
              label={t(strings.explorer.twin_details.details_panel.id)}
              value={properties.id}
            />
            {renderLabel(state.labels)}
            <LabelValue
              label={t(strings.explorer.twin_details.details_panel.description)}
              value={properties.description}
            />
          </div>
          <div className={styles.column}>
            <LabelValue
              label={t(strings.explorer.twin_details.details_panel.created)}
              value={properties.created?.toISOString() ?? ''}
            />
            <LabelValue
              label={t(strings.explorer.twin_details.details_panel.updated)}
              value={properties.updated?.toISOString() ?? ''}
            />
          </div>
        </div>
      </Card>
      <div>{renderAttributes(state.attributes, styles)}</div>
    </div>
  );
};

const renderLabel = (labels: DataRequest<string[]>) => {
  if (labels.state === 'uncalled') {
    return (
      <LabelValue
        label={t(strings.explorer.twin_details.details_panel.label)}
        value=''
      />
    );
  }
  if (labels.state === 'pending') {
    return (
      <LabelValue
        label={t(strings.explorer.twin_details.details_panel.label)}
        loading={true}
        size='extra-tiny'
      />
    );
  }
  if (labels.state === 'error') {
    return <ErrorMessage message={labels.errorMessage} />;
  }
  return (
    <LabelValue
      label={
        labels.data.length === 1
          ? t(strings.explorer.twin_details.details_panel.label)
          : t(strings.explorer.twin_details.details_panel.labels)
      }
      value={labels.data.join(', ')}
    />
  );
};

const renderAttributes = (
  attributes: DataRequest<TwinAttribute[]>,
  styles: ITwinDetailsPanelStyles,
) => {
  if (attributes.state === 'uncalled') {
    return null;
  }
  if (attributes.state === 'error') {
    return <ErrorMessage message={attributes.errorMessage} />;
  }
  if (attributes.state === 'pending') {
    return <Loading />;
  }
  if (attributes.data.length === 0) {
    return (
      <EmptyContents
        header={t(strings.explorer.twin_details.details_panel.attributes.empty)}
      />
    );
  }
  const columns = [
    createTableColumn<TwinAttribute>({
      columnId: 'name',
      compare: (a, b) => {
        return a.name.localeCompare(b.name);
      },
      renderCell: (item: TwinAttribute) => {
        return item.name;
      },
      renderHeaderCell: () => {
        return (
          <span className={styles.attributeHeader}>
            {t(strings.explorer.twin_details.details_panel.attributes.name)}
          </span>
        );
      },
    }),
    createTableColumn<TwinAttribute>({
      columnId: 'value',
      compare: (a, b) => {
        return a.value.toString().localeCompare(b.value.toString());
      },
      renderCell: (item: TwinAttribute) => {
        return item.valueType !== 'Bool' ? item.value : item.value.toString();
      },
      renderHeaderCell: () => {
        return (
          <span className={styles.attributeHeader}>
            {t(strings.explorer.twin_details.details_panel.attributes.value)}
          </span>
        );
      },
    }),
  ];
  return (
    <DataGrid
      items={attributes.data}
      columns={columns}
      sortable
      getRowId={(item) => item.name}
    >
      <DataGridHeader>
        <DataGridRow>
          {({ renderHeaderCell }) => (
            <DataGridHeaderCell>{renderHeaderCell()}</DataGridHeaderCell>
          )}
        </DataGridRow>
      </DataGridHeader>
      <DataGridBody>
        {({ item, rowId }) => (
          <DataGridRow key={rowId}>
            {({ renderCell }) => (
              <DataGridCell>{renderCell(item)}</DataGridCell>
            )}
          </DataGridRow>
        )}
      </DataGridBody>
    </DataGrid>
  );
};
