import { DataRequest, HierarchyNode } from '@do/core';

export { HierarchyClientType } from '@do/core';

export type TreeNode = Pick<
  HierarchyNode,
  | 'childCount'
  | 'continuationToken'
  | 'continuationUri'
  | 'displayName'
  | 'path'
  | 'twin'
> & {
  children: HierarchyRequest | undefined;
  isLoadingMore?: boolean;
  open: boolean;
};

export type HierarchyRequest = DataRequest<TreeNode[]>;

/** input parameters to HierarchyTree */
export type IHierarchyTreeProps = {
  /** A string to use in tests to interact with items. For twins within the tree, use `${dataTestId}_${twin.id}` */
  dataTestId?: string;
  /** a function to call when the user clicks on a hierarchy item */
  onHierarchyItemClick?: (
    item: TreeNode,
    hierarchyItemType: TreeItemType,
  ) => void;
  /** Determines how to render the tree item, enabling things like overflow menus */
  customItemLayout?:
    | ((item: TreeNode, treeItemType: TreeItemType) => JSX.Element)
    | undefined;
  /** Selected TwinId which triggers styling for a row selected state */
  selectedTwinId?: string;
  /** A className to be merged with base styling of the tree*/
  rootClassName?: string;
  /** A className to be merged with base styling of the TreeItemLayout*/
  treeItemClassName?: string;
  /** A className to be merged with base styling of the show more */
  showMoreClassName?: string;
};

export type HierarchyReducerState = {
  state: 'uncalled' | 'pending' | 'success' | 'error';
  errorCode?: string;
  errorMessage?: string;
  nodes: TreeNode[];
  continuationToken?: string;
  continuationUri?: string;
  isLoadingMore?: boolean;
};

/** state values used for rendering the item */
export type IHierarchyTreeState = {
  /** A string to use in tests to interact with items. For twins within the tree, use `${dataTestId}_${twin.id}` */
  dataTestId: string;

  onOpenChange: (twinPath: string) => void;
  onLoadMoreFromPath: (
    path: string,
    continuationToken: string,
    continuationUri: string,
  ) => void;
  onNodeClick?:
    | ((twinPath: string, hierarchyItemType: TreeItemType) => void)
    | undefined;
} & Pick<
  IHierarchyTreeProps,
  | 'customItemLayout'
  | 'selectedTwinId'
  | 'rootClassName'
  | 'treeItemClassName'
  | 'showMoreClassName'
> &
  HierarchyReducerState;

/** the props send to the render function */
export type IHierarchyTreeRenderProps = {
  state: IHierarchyTreeState;
  styles: IHierarchyTreeStyles;
  className?: string;
};

/** the names of styling classes for HierarchyTree */
export type HierarchyTreeClasses =
  | 'root'
  | 'treeItem'
  | 'showMore'
  | 'selectedItem';
/** is this a branch or leaf */
export type TreeItemType = 'branch' | 'leaf';
/** the computed styles object output from the styles hook */
export type IHierarchyTreeStyles = Record<HierarchyTreeClasses, string>;

export enum HierarchyTreeActionType {
  LOADING_ROOTS = 'LOADING_ROOTS',
  LOADED_ROOTS = 'LOADED_ROOTS',
  LOADING_MORE_ROOTS = 'LOADING_MORE_ROOTS',
  LOADED_MORE_ROOTS = 'LOADED_MORE_ROOTS',
  FAILED_TO_LOAD_ROOTS = 'FAILED_TO_LOAD_ROOTS',
  FAILED_TO_LOAD_MORE_ROOTS = 'FAILED_TO_LOAD_MORE_ROOTS',
  LOADING_CHILDREN = 'LOADING_CHILDREN',
  LOADED_CHILDREN = 'LOADED_CHILDREN',
  LOADING_MORE_CHILDREN = 'LOADING_MORE_CHILDREN',
  LOADED_MORE_CHILDREN = 'LOADED_MORE_CHILDREN',
  FAILED_TO_LOAD_CHILDREN = 'FAILED_TO_LOAD_CHILDREN',
  FAILED_TO_LOAD_MORE_CHILDREN = 'FAILED_TO_LOAD_MORE_CHILDREN',
  TOGGLE_NODE = 'TOGGLE_NODE',
}

export type HierarchyTreeAction =
  | {
      type: HierarchyTreeActionType.LOADING_ROOTS;
    }
  | {
      type: HierarchyTreeActionType.LOADED_ROOTS;
      payload: {
        nodes: TreeNode[];
        continuationToken: string;
        continuationUri: string;
      };
    }
  | {
      type: HierarchyTreeActionType.LOADING_MORE_ROOTS;
    }
  | {
      type: HierarchyTreeActionType.LOADED_MORE_ROOTS;
      payload: {
        nodes: TreeNode[];
        continuationToken: string;
        continuationUri: string;
      };
    }
  | {
      type: HierarchyTreeActionType.FAILED_TO_LOAD_ROOTS;
      payload: {
        errorCode: string;
        errorMessage: string;
      };
    }
  | {
      type: HierarchyTreeActionType.FAILED_TO_LOAD_MORE_ROOTS;
      payload: {
        errorCode: string;
        errorMessage: string;
      };
    }
  | {
      type: HierarchyTreeActionType.LOADING_CHILDREN;
      payload: {
        path: string;
      };
    }
  | {
      type: HierarchyTreeActionType.LOADED_CHILDREN;
      payload: {
        path: string;
        nodes: TreeNode[];
        continuationToken: string;
        continuationUri: string;
      };
    }
  | {
      type: HierarchyTreeActionType.LOADING_MORE_CHILDREN;
      payload: {
        path: string;
      };
    }
  | {
      type: HierarchyTreeActionType.FAILED_TO_LOAD_CHILDREN;
      payload: {
        path: string;
        errorCode: string;
        errorMessage: string;
      };
    }
  | {
      type: HierarchyTreeActionType.LOADED_MORE_CHILDREN;
      payload: {
        path: string;
        nodes: TreeNode[];
        continuationToken: string;
        continuationUri: string;
      };
    }
  | {
      type: HierarchyTreeActionType.FAILED_TO_LOAD_MORE_CHILDREN;
      payload: {
        path: string;
        errorCode: string;
        errorMessage: string;
      };
    }
  | {
      type: HierarchyTreeActionType.TOGGLE_NODE;
      payload: {
        path: string;
      };
    };
