import React from 'react';
import { IPageFooterRenderProps } from './PageFooter.types';
import { Button } from '@fluentui/react-components';
import { CaretLeft12Regular, CaretRight12Regular } from '@fluentui/react-icons';
import { strings, t } from '@do/localization';

export const getTestIds = (name?: string) => ({
  PreviousPageButton: `${name ? name : 'footer'}-previous-page-button`,
  NextPageButton: `${name ? name : 'footer'}-next-page-button`,
  PageNumberButton: `${name ? name : 'footer'}-page-number-button`,
  SkipButton: `${name ? name : 'footer'}-skip-button`,
});

export const usePageFooterRender: React.FC<IPageFooterRenderProps> = ({
  state,
  styles,
}) => {
  return (
    <div className={styles.root}>
      <Button
        data-testid={getTestIds(state.testIdPrefix).PreviousPageButton}
        appearance='transparent'
        className={styles.paginationButton}
        icon={<CaretLeft12Regular />}
        disabled={state.currentPage <= 1}
        onClick={() => {
          state.onPageChange(state.currentPage - 1);
        }}
      >
        {t(strings.common.previous)}
      </Button>
      {[...Array(state.pageCount)].map((_, index) => {
        const pageNumber = index + state.startingPage;
        let style = styles.unselectedPage;
        if (pageNumber + 1 === state.currentPage) {
          style = styles.selectedPage;
        }
        if (pageNumber < state.totalPageCount) {
          return (
            <Button
              data-testid={`${
                getTestIds(state.testIdPrefix).PageNumberButton
              }-${pageNumber}`}
              key={pageNumber}
              as='a'
              appearance='transparent'
              className={style}
              onClick={() => {
                if (state.currentPage !== pageNumber + 1) {
                  state.onPageChange(pageNumber + 1);
                }
              }}
            >
              {pageNumber + 1}
            </Button>
          );
        } else {
          return null;
        }
      })}
      {state.totalPageCount > state.pageCount &&
      state.currentPage + 1 < state.totalPageCount ? (
        <Button
          as='a'
          appearance='transparent'
          className={styles.unselectedPage}
          data-testid={getTestIds(state.testIdPrefix).SkipButton}
          onClick={() => {
            const skipTo =
              state.currentPage + 10 > state.totalPageCount
                ? state.totalPageCount
                : state.currentPage + 10;
            state.onPageChange(skipTo);
          }}
        >
          ...
        </Button>
      ) : null}
      <Button
        data-testid={getTestIds(state.testIdPrefix).NextPageButton}
        appearance='transparent'
        className={styles.paginationButton}
        icon={<CaretRight12Regular />}
        iconPosition='after'
        disabled={state.currentPage >= state.totalPageCount}
        onClick={() => {
          state.onPageChange(state.currentPage + 1);
        }}
      >
        {t(strings.common.next)}
      </Button>
    </div>
  );
};
