import * as React from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import {
  SchedulingSectionTimeFieldProps,
  SchedulingSectionTimeFieldRef,
  SchedulingSectionTimeFieldState,
} from './SchedulingSectionTimeField.types';
import { strings, t } from '@do/localization';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'SchedulingSectionTimeField.state';

export const useSchedulingSectionTimeFieldState = (
  {
    required,
    disabled,
    timesOfDay,
    setTimesOfDay,
  }: SchedulingSectionTimeFieldProps,
  ref: React.Ref<SchedulingSectionTimeFieldRef | undefined>,
): SchedulingSectionTimeFieldState => {
  // state
  const times = React.useMemo(() => timesOfDay ?? [], [timesOfDay]);

  const { isValid, validationMessages } = React.useMemo(() => {
    let invalid = false;
    const validationMessages = times.map((a, i) => {
      if (!a) {
        invalid = true;
        return t(strings.scheduling.times_of_day_validation_message_empty);
      } else if (times?.find((b, j) => i !== j && a === b)) {
        invalid = true;
        return t(strings.scheduling.times_of_day_validation_message_duplicate);
      }
      return undefined;
    });

    return {
      validationMessages,
      isValid: !invalid,
    };
  }, [times]);

  // callbacks
  React.useImperativeHandle(ref, () => {
    return {
      getIsValid: () => {
        return isValid;
      },
    };
  });

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // result
  const state: SchedulingSectionTimeFieldState = React.useMemo(
    () => ({
      required,
      disabled,
      timesOfDay: times,
      validationMessages,
      onAddTime() {
        setTimesOfDay([...times, '']);
      },
      onRemoveTime(i) {
        const newTimesOfDay = [...times];
        newTimesOfDay.splice(i, 1);
        setTimesOfDay(newTimesOfDay);
      },
      onUpdateTime(i, time) {
        const newTimesOfDay = [...times];
        newTimesOfDay[i] = time;
        setTimesOfDay(newTimesOfDay);
      },
    }),
    [required, disabled, times, setTimesOfDay, validationMessages],
  );

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
