import { useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import {
  ISimpleDropdownProps,
  ISimpleDropdownState,
} from './SimpleDropdown.types';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'SimpleDropDown.state';

export const useSimpleDropdownState = (
  props: ISimpleDropdownProps,
): ISimpleDropdownState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state: ISimpleDropdownState = useMemo(() => {
    const itemValue = props.defaultValue || props.value;
    const item = props.items?.find((item) => item.key === itemValue);
    const selectedValues = item ? [item.key] : [];
    const defaultValue = item ? item.text : itemValue;
    return {
      ...props,
      appearance: props.appearance || 'outline',
      defaultValue,
      selectedValues,
      dataTestId: props.dataTestid || 'simpleDropDown',
    };
  }, [props]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state;
};
