import React, { Suspense, lazy } from 'react';
import { Spinner } from '@fluentui/react-components';
import { createRoot } from 'react-dom/client';
import { createExtensionClient, InitParams } from '@trident/extension-client';
import {
  EXTENSION_NAMES,
  customLightTheme,
  extensionVersion,
  getLogger,
  initLakehouseLogger,
  initializeLogger,
  publishDate,
} from '@do/core';
const FluentProvider = lazy(() =>
  import('@fluentui/react-components').then((module) => ({
    default: module.FluentProvider,
  })),
);
const LocalizationContextProvider = lazy(() =>
  import('@do/localization').then((module) => ({
    default: module.LocalizationContextProvider,
  })),
);
const EnvironmentContextProvider = lazy(() =>
  import('@do/core').then((module) => ({
    default: module.EnvironmentContextProvider,
  })),
);
const FeatureSwitchContextProvider = lazy(() =>
  import('@do/core').then((module) => ({
    default: module.FeatureSwitchContextProvider,
  })),
);
const TridentContextProvider = lazy(() =>
  import('@do/core').then((module) => ({
    default: module.TridentContextProvider,
  })),
);
import { createBrowserHistory } from 'history';
import { App } from './components/App/App';
import { Router } from 'react-router-dom';

const MODULE_NAME = 'ExtensionUI';

export async function initialize(params: InitParams): Promise<void> {
  const client = createExtensionClient();
  const history = createBrowserHistory();
  const settings = await client.settings.get();
  initializeLogger(client, params.environmentName);
  getLogger().info(MODULE_NAME, `Starting extension.`, {
    environment: params.environmentName,
    extensionVersion: extensionVersion,
    publishDate: publishDate,
  });

  // have to initialize the logger for the lakehouse package and this is where we know the environment name
  initLakehouseLogger(
    EXTENSION_NAMES.DigitalOperations,
    client,
    params.environmentName,
    '1.0',
  );
  getLogger().info(MODULE_NAME, `Rendering extension.`);

  client.navigation.onNavigate((route) => history.replace(route.targetUrl));

  // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
  const root = createRoot(document.querySelector('#root')!);
  root.render(
    <Suspense fallback={<Spinner style={{ height: '100%' }} />}>
      <EnvironmentContextProvider environmentName={params.environmentName}>
        <LocalizationContextProvider locale={settings.currentLanguageLocale}>
          <FluentProvider theme={customLightTheme}>
            <FeatureSwitchContextProvider settings={settings}>
              <TridentContextProvider extensionClient={client}>
                <Router history={history}>
                  <App />
                </Router>
              </TridentContextProvider>
            </FeatureSwitchContextProvider>
          </FluentProvider>
        </LocalizationContextProvider>
      </EnvironmentContextProvider>
    </Suspense>,
  );
}
