import React from 'react';
import { IMainNavRenderProps } from './MainNav.types';
import {
  Organization24Regular,
  Organization24Filled,
  Library24Regular,
  Library24Filled,
  LightbulbFilament24Regular,
  LightbulbFilament24Filled,
  Cube24Regular,
  Cube24Filled,
  bundleIcon,
} from '@fluentui/react-icons';
import { AppNames } from '@do/core';
import { TabList, Tab } from '@fluentui/react-components';
import { t, strings } from '@do/localization';

export enum TestIds {
  Root = 'main-nav',
  Learn = 'main-nav-learn',
  Prepare = 'main-nav-prepare',
  Explore = 'main-nav-explore',
  Discover = 'main-nav-discover',
}

export const useMainNavRender: React.FC<IMainNavRenderProps> = (props) => {
  const { state, styles } = props;
  const { selectedTab, onTabChange } = state;

  const LearnIcons = bundleIcon(Library24Filled, Library24Regular);
  const PrepareIcons = bundleIcon(Cube24Filled, Cube24Regular);
  const ExploreIcons = bundleIcon(Organization24Filled, Organization24Regular);
  const DiscoverIcons = bundleIcon(
    LightbulbFilament24Filled,
    LightbulbFilament24Regular,
  );

  return (
    <TabList
      selectedValue={selectedTab}
      onTabSelect={onTabChange}
      className={styles.root}
      role='navigation'
      data-testid={TestIds.Root}
    >
      <Tab
        value={AppNames.home}
        icon={<LearnIcons />}
        data-testid={TestIds.Learn}
      >
        {t(strings.main_nav.learn)}
      </Tab>
      <Tab
        value={AppNames.prepare}
        icon={<PrepareIcons />}
        data-testid={TestIds.Prepare}
      >
        {t(strings.main_nav.prepare)}
      </Tab>
      <Tab
        value={AppNames.explorer}
        icon={<ExploreIcons />}
        data-testid={TestIds.Explore}
      >
        {t(strings.main_nav.explore)}
      </Tab>
      <Tab
        value={AppNames.insights}
        icon={<DiscoverIcons />}
        data-testid={TestIds.Discover}
      >
        {t(strings.main_nav.discover)}
      </Tab>
    </TabList>
  );
};
