import { useCallback, useMemo, useState } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { IPageFooterProps, IPageFooterState } from './PageFooter.types';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'PageFooter.state';

export const usePageFooterState = (
  props: IPageFooterProps,
): IPageFooterState => {
  // contexts

  // state
  const [currentPage, setCurrentPage] = useState<number>(
    props.initialPage ? props.initialPage : 1,
  );

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks
  const onInternalOnPageChange = useCallback(
    (newPage: number) => {
      setCurrentPage(newPage);
      props.onPageChange(newPage);
    },
    [props],
  );

  // side effects

  // result
  const state: IPageFooterState = useMemo(() => {
    const filteredPageCount = props.pageCount > 10 ? 10 : props.pageCount;
    const startingPage = Math.max(0, currentPage - 9);

    return {
      pageCount: filteredPageCount,
      totalPageCount: props.pageCount,
      startingPage,
      currentPage,
      onPageChange: onInternalOnPageChange,
      testIdPrefix: props.testIdPrefix,
    };
  }, [
    props.pageCount,
    props.testIdPrefix,
    currentPage,
    onInternalOnPageChange,
  ]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state;
};
