import React from 'react';
import { LabelValueProps } from './LabelValue.types';
import { useLabelValueRender } from './LabelValue.render';
import { useClassNames } from './LabelValue.styles';

const MODULE_NAME = 'LabelValue';

export const LabelValue: React.FC<LabelValueProps> = (props) => {
  // get styles
  const styles = { ...useClassNames(), ...props.styles };

  // render the item
  return useLabelValueRender({ props, styles });
};

LabelValue.displayName = MODULE_NAME;
