import { useCallback, useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import {
  ITwinDetailsNavProps,
  ITwinDetailsNavState,
} from './TwinDetailsNav.types';
import { useTwinsExplorerRouteProps } from '../../../../hooks/routeProps/useTwinsExplorerRouteProps';
import { DetailsPageRouteProps } from '../../TwinDetailsPage';
import { useHistory } from 'react-router-dom';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'TwinDetailsNav.state';

export const useTwinDetailsNavState = (
  _props: ITwinDetailsNavProps,
): ITwinDetailsNavState => {
  // contexts

  // state
  const routeProps = useTwinsExplorerRouteProps() as DetailsPageRouteProps;

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);
  const history = useHistory();

  // callbacks
  const onTabChange = useCallback(
    (value: string) => {
      //we almost always have a history object and Jest hates mocking it (numerous variations tried)
      /* istanbul ignore next */
      history?.replace(`${routeProps.twinDetailsPath}/${value}`);
    },
    [history, routeProps.twinDetailsPath],
  );

  // side effects

  // result
  const state: ITwinDetailsNavState = useMemo(() => {
    return {
      selectedPanel: routeProps.panelName || 'details',
      onTabChange,
    };
  }, [onTabChange, routeProps.panelName]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state;
};
