import React from 'react';
import { ITwinDetailsNavProps } from './TwinDetailsNav.types';
import { useTwinDetailsNavRender } from './TwinDetailsNav.render';
import { useClassNames } from './TwinDetailsNav.styles';
import { useTwinDetailsNavState } from './TwinDetailsNav.state';

const MODULE_NAME = 'TwinDetailsNav';

export const TwinDetailsNav: React.FC<ITwinDetailsNavProps> = (_props) => {
  // get state
  const state = useTwinDetailsNavState(_props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useTwinDetailsNavRender({ state, styles });
};

TwinDetailsNav.displayName = MODULE_NAME;
