import { IGraphStyles, GraphClasses } from './Graph.types';
import { makeStyles, shorthands } from '@fluentui/react-components';
import { tokens } from '@fluentui/react-theme';

export const useClassNames: () => IGraphStyles = makeStyles<GraphClasses>({
  root: {},
  spinner: {
    height: '100%',
  },
  graph: {
    width: '90vw', // TODO: fix this to dynamically size based on container
    height: '90vh',
  },
  navigatorContainer: {
    position: 'absolute',
    bottom: '20px',
    right: '20px',
    backgroundColor: tokens.colorNeutralBackground1,
    height: '120px',
    width: '155px',
    ...shorthands.padding('6px'),
    ...shorthands.borderRadius('5px'),
    ...shorthands.borderColor(tokens.colorNeutralBackground1),
  },
  navigator: {
    position: 'relative',
    height: '100%',
    width: '100%',
    ...shorthands.overflow('hidden'),
    backgroundColor: tokens.colorNeutralBackground2,
    '.cytoscape-navigatorView': {
      backgroundColor: tokens.colorNeutralBackground1Pressed,
    },
  },
});

export const DefaultCytoscapeStylesheet: cytoscape.Stylesheet[] = [
  {
    selector: 'node',
    style: {
      label: 'data(label)',
      'background-color': (node) =>
        node.data('color') ? node.data('color') : 'gray',
    },
  },
  {
    selector: 'edge',
    style: {
      label: 'data(label)',
      width: 3,
      'target-arrow-shape': 'triangle',
      'text-rotation': 'autorotate',
      'curve-style': 'bezier',
      'text-margin-y': -10,
    },
  },
  {
    selector: ':selected',
    css: {
      'background-color': 'SteelBlue', // TODO: themify colors as needed
      'line-color': 'black',
      'target-arrow-color': 'black',
      'source-arrow-color': 'black',
    },
  },
];
