import React from 'react';
import { SteppedWizardProps } from './SteppedWizard.types';
import { useSteppedWizardRender } from './SteppedWizard.render';
import { useClassNames } from './SteppedWizard.styles';
import { useSteppedWizardState } from './SteppedWizard.state';

const MODULE_NAME = 'SteppedWizard';

export const SteppedWizard: React.FC<SteppedWizardProps> = (props) => {
  // get state
  const state = useSteppedWizardState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useSteppedWizardRender({ state, styles });
};

SteppedWizard.displayName = MODULE_NAME;
