import {
  DateTimeRangeInputStyles,
  DateTimeRangeInputClasses,
} from './DateTimeRangeInput.types';
import { makeStyles } from '@fluentui/react-components';

export const useClassNames: () => DateTimeRangeInputStyles =
  makeStyles<DateTimeRangeInputClasses>({
    root: {
      display: 'flex',
      flexDirection: 'column',
      rowGap: '4px',
      minHeight: '80px',
    },
    inputContainer: {
      display: 'flex',
      columnGap: '12px',
    },
    verticalLayout: {
      flexDirection: 'column',
      rowGap: '8px',
    },
    fieldWithoutError: {
      paddingBottom: '16px',
    },
  });
