import { StepperStyles, StepperClasses } from './Stepper.types';
import { makeStyles, shorthands } from '@fluentui/react-components';

export const useClassNames: () => StepperStyles = makeStyles<StepperClasses>({
  root: {
    maxWidth: '20%',
    minWidth: '200px',
    ...shorthands.padding('24px'),
    paddingLeft: '4px',
  },
  label: {},
  item: {
    flexDirection: 'row',
    display: 'flex',
    columnGap: '8px',
  },
  icon: {
    width: '24px',
    display: 'flex',
    flexDirection: 'column',
    rowGap: 0,
    ...shorthands.margin(0),
    ...shorthands.padding(0),
    ...shorthands.overflow('hidden'),
  },
});
