import React from 'react';
import { ICardTitleProps } from './CardTitle.types';
import { useClassNames } from './CardTitle.styles';

const CardTitle: React.FC<ICardTitleProps> = (props) => {
  const { title } = props;
  // contexts

  // state

  // hooks

  // callbacks

  // side effects

  // styles
  const classNames = useClassNames();

  if (typeof title === 'string') {
    return <span className={classNames.root}>{title}</span>;
  } else {
    return (
      <div className={classNames.root}>
        <div className={classNames.titleStart}>
          <span>{title.icon}</span>
          <span>{title.text}</span>
        </div>

        {title.actions?.length && (
          <div className={classNames.titleEnd}>{title.actions}</div>
        )}
      </div>
    );
  }
};

export default CardTitle;
