import {
  DataRequest,
  ErrorDataResponse,
  SuccessDataResponse,
  TwinAttribute,
  TwinDetails,
} from '@do/core';

/** input parameters to TwinDetailsPanel */
export interface ITwinDetailsPanelProps {
  twinId: string;
}

/** the props send to the render function */
export type ITwinDetailsPanelRenderProps = {
  state: TwinDetailsPanelState;
  styles: ITwinDetailsPanelStyles;
};

/** the names of styling classes for TwinDetailsPanel */
export type TwinDetailsPanelClasses =
  | 'root'
  | 'panelBody'
  | 'column'
  | 'attributeHeader';

/** the computed styles object output from the styles hook */
export type ITwinDetailsPanelStyles = Record<TwinDetailsPanelClasses, string>;

export type TwinDetailsPanelState = {
  properties: DataRequest<TwinDetails>;
  labels: DataRequest<string[]>;
  attributes: DataRequest<TwinAttribute[]>;
};

export enum TwinDetailsActionType {
  LOADING_ALL = 'LOADING_ALL',
  LOADING_PROPERTIES = 'LOADING_PROPERTIES',
  LOADED_PROPERTIES = 'LOADED_PROPERTIES',
  FAILED_TO_LOAD_PROPERTIES = 'FAILED_TO_LOAD_PROPERTIES',
  LOADING_LABELS = 'LOADING_LABELS',
  LOADED_LABELS = 'LOADED_LABELS',
  FAILED_TO_LOAD_LABELS = 'FAILED_TO_LOAD_LABELS',
  LOADING_ATTRIBUTES = 'LOADING_ATTRIBUTES',
  LOADED_ATTRIBUTES = 'LOADED_ATTRIBUTES',
  FAILED_TO_LOAD_ATTRIBUTES = 'FAILED_TO_LOAD_ATTRIBUTES',
}

export type TwinDetailsActions =
  | {
      type: TwinDetailsActionType.LOADING_ALL;
    }
  | {
      type: TwinDetailsActionType.LOADING_PROPERTIES;
    }
  | {
      type: TwinDetailsActionType.LOADED_PROPERTIES;
      payload: SuccessDataResponse<TwinDetails>;
    }
  | {
      type: TwinDetailsActionType.FAILED_TO_LOAD_PROPERTIES;
      payload: ErrorDataResponse;
    }
  | {
      type: TwinDetailsActionType.LOADING_LABELS;
    }
  | {
      type: TwinDetailsActionType.LOADED_LABELS;
      payload: SuccessDataResponse<string[]>;
    }
  | {
      type: TwinDetailsActionType.FAILED_TO_LOAD_LABELS;
      payload: ErrorDataResponse;
    }
  | {
      type: TwinDetailsActionType.LOADING_ATTRIBUTES;
    }
  | {
      type: TwinDetailsActionType.LOADED_ATTRIBUTES;
      payload: SuccessDataResponse<TwinAttribute[]>;
    }
  | {
      type: TwinDetailsActionType.FAILED_TO_LOAD_ATTRIBUTES;
      payload: ErrorDataResponse;
    };
