import { useEffect, useMemo, useState } from 'react';
import { TimeSeriesPointData, getLogger, useDebugLogging } from '@do/core';
import { LineChartProps, LineChartState } from './LineChart.types';
import type { Options, SeriesOptionsType } from 'highcharts';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'LineChart.state';

const getColorByIndex = (index: number): string => {
  // TODO: See if these colors can be added to the theme
  const colors = [
    '#12239E',
    '#E044A7',
    '#744EC2',
    '#D9B300',
    '#1AAB40',
    '#D64550',
  ];
  return colors[index % colors.length];
};

export const createSeriesOptions = (
  data: Record<string, TimeSeriesPointData[]>,
): SeriesOptionsType[] => {
  return Object.keys(data).map((key: string, idx: number) => {
    const timeSeriesData = data[key];
    return {
      name: key,
      color: getColorByIndex(idx),
      type: 'line',
      data: timeSeriesData.map((point) => [point.timestamp, point.value]),
      // Explicitly show point if there is only one point, otherwise don't show it
      marker: {
        enabled: timeSeriesData.length === 1,
      },
      tooltip: {
        valueDecimals: 2,
      },
    };
  });
};

export const createInitialOptions = (
  data: Record<string, TimeSeriesPointData[]>,
): Options => {
  return {
    title: undefined,
    xAxis: {
      type: 'datetime',
      labels: {
        // TODO: locale days of week
        format: '{value:%Y-%m-%d}',
      },
    },
    yAxis: {
      opposite: false,
    },
    scrollbar: {
      enabled: false,
    },
    series: createSeriesOptions(data),
    // TODO: Add configuration to be able to manipulate this
    rangeSelector: {
      enabled: false,
    },
    // TODO: Add configuration to be able to manipulate this
    navigator: {
      enabled: false,
    },
    credits: {
      enabled: false,
    },
  };
};

export const useLineChartState = (props: LineChartProps): LineChartState => {
  // contexts

  // state
  const [isHighchartsLoaded, setIsHighchartsLoaded] = useState(false);

  // Options need to come from useState/useContext because they need to be extensible
  const [options, setOptions] = useState(() => {
    if (Object.keys(props.rawTimeSeriesData).length > 0) {
      return createInitialOptions(props.rawTimeSeriesData);
    } else {
      return {};
    }
  });

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects
  useEffect(() => {
    setOptions((prevOptions) => {
      return {
        ...prevOptions,
        series: createSeriesOptions(props.rawTimeSeriesData),
      };
    });
  }, [props.rawTimeSeriesData]);

  // result
  const state: LineChartState = useMemo(() => {
    return {
      options,
      isHighchartsLoaded,
      setIsHighchartsLoaded,
    };
  }, [isHighchartsLoaded, options]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
