import {
  ITwinDetailsPanelStyles,
  TwinDetailsPanelClasses,
} from './TwinDetailsPanel.types';
import { makeStyles, shorthands, tokens } from '@fluentui/react-components';

export const useClassNames: () => ITwinDetailsPanelStyles =
  makeStyles<TwinDetailsPanelClasses>({
    root: {
      display: 'flex',
      flexDirection: 'column',
      rowGap: '16px',
    },
    panelBody: {
      display: 'flex',
      columnGap: '16px',
      flexDirection: 'row',
    },
    column: {
      ...shorthands.flex(1, 0, 'auto'),
      borderTopWidth: '1px',
      borderTopStyle: 'solid',
      borderTopColor: tokens.colorNeutralStroke2,
      width: '50%',
    },
    attributeHeader: {
      fontWeight: tokens.fontWeightSemibold,
    },
  });
