import React from 'react';
import { useSteppedWizardStartStepRender } from './SteppedWizardStartStep.render';
import { useClassNames } from './SteppedWizardStartStep.styles';
import { SteppedWizardStartStepProps } from './SteppedWizardStartStep.types';

const MODULE_NAME = 'SteppedWizardStartStep';

export const SteppedWizardStartStep: React.FC<SteppedWizardStartStepProps> = (
  props,
) => {
  // get styles
  const styles = useClassNames();

  // render the item
  return useSteppedWizardStartStepRender({ ...props, styles });
};

SteppedWizardStartStep.displayName = MODULE_NAME;
