import {
  TimeSeriesChartClasses,
  TimeSeriesChartStyles,
} from './TimeSeriesChart.types';
import { makeStyles } from '@fluentui/react-components';

export const useClassNames: () => TimeSeriesChartStyles =
  makeStyles<TimeSeriesChartClasses>({
    root: {},
    header: {},
    chart: {},
    controls: {
      // Align with the chart
      marginLeft: '44px',
    },
    error: {
      // Align with the chart
      paddingLeft: '44px',
      paddingBottom: '12px',
    },
  });
