import React from 'react';
import { useLineChartControlsRender } from './LineChartControls.render';
import { useClassNames } from './LineChartControls.styles';
import { useLineChartControlsState } from './LineChartControls.state';
import { LineChartControlsProps } from './LineChartControls.types';

const MODULE_NAME = 'LineChartControls';

export const LineChartControls: React.FC<LineChartControlsProps> = (props) => {
  // get state
  const state = useLineChartControlsState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useLineChartControlsRender({ state, styles });
};

LineChartControls.displayName = MODULE_NAME;
