import { ExternalLinkStyles, ExternalLinkClasses } from './ExternalLink.types';
import { makeStyles, tokens } from '@fluentui/react-components';

export const useClassNames: () => ExternalLinkStyles =
  makeStyles<ExternalLinkClasses>({
    root: {
      display: 'contents',
    },
    text: {
      marginTop: '8px',
      marginRight: '4px',
      fontWeight: tokens.fontWeightBold,
    },
    marginBefore: {
      marginRight: '4px',
    },
    marginAfter: {
      marginLeft: '4px',
    },
  });
