import {
  SchedulingSectionStyles,
  SchedulingSectionClasses,
} from './SchedulingSection.types';
import { makeStyles, shorthands } from '@fluentui/react-components';

export const useClassNames: () => SchedulingSectionStyles =
  makeStyles<SchedulingSectionClasses>({
    root: {},
    frequency_fields: {
      display: 'flex',
      flexDirection: 'column',
      ...shorthands.gap('8px'),
    },
    input_field: {
      display: 'flex',
      rowGap: '4px',
      flexDirection: 'column',
    },
    scheduled_run_field: {
      marginBottom: '8px',
    },
    date_time_fields: {
      marginTop: '16px',
    },
    days_of_week_container: {
      display: 'flex',
      flexDirection: 'row',
    },
    days_of_week_field: {
      display: 'flex',
      flexDirection: 'column',
    },
  });
