import { useCallback, useEffect, useMemo, useState } from 'react';
import { FeatureEvent } from '@trident/certified-events';
import {
  DataRequest,
  DataResponse,
  TimeSeries,
  WithContinuationToken,
  getLogger,
  getNestedPage,
  useAppRouteProps,
  useDebugLogging,
  useNavigation,
  useTimeSeriesClient,
} from '@do/core';
import {
  TwinTimeSeriesListProps,
  TwinTimeSeriesListState,
} from './TwinTimeSeriesList.types';
import { useTimeSeriesContext } from '../../../../../../contexts/TimeSeriesContext';
import {
  DataGridProps,
  OnSelectionChangeData,
  SelectionItemId,
} from '@fluentui/react-components';
import { useTwinsExplorerRouteProps } from '../../../../../../hooks/routeProps/useTwinsExplorerRouteProps';
import { DetailsPageRouteProps } from '../../../../TwinDetailsPage/TwinDetailsPage.types';
import {
  ExploreTwinDetailsActivity,
  TwinVisualizationFeature,
} from '../../../../../../models/TwinVisualizationCertifiedEvents';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'TwinTimeSeriesList.state';
const EXPANDED_PAGE_SIZE = 10;
const COMPACT_PAGE_SIZE = 3;

export const useTwinTimeSeriesListState = (
  _props: TwinTimeSeriesListProps,
): TwinTimeSeriesListState => {
  // contexts
  const { selectedTimeSeries, setSelectedTimeSeries } = useTimeSeriesContext();
  const [explorerRouteProps] = useState(
    useTwinsExplorerRouteProps() as DetailsPageRouteProps,
  );

  // state
  const client = useTimeSeriesClient();
  const [timeseriesDataRequest, setTimeseriesDataRequest] = useState<
    DataRequest<TimeSeries[]>
  >({
    state: 'pending',
  });
  const [isLoadingMore, setIsLoadingMore] = useState(false);
  const [selectedItems, setSelectedItems] = useState<Set<SelectionItemId>>(
    new Set(),
  );

  const [continuationToken, setContinuationToken] = useState<
    string | undefined
  >(undefined);
  const [bufferedItems, setBufferedItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageCount, setPageCount] = useState(1);
  const [pageSize, setPageSize] = useState(
    selectedTimeSeries.length === 0 ? EXPANDED_PAGE_SIZE : COMPACT_PAGE_SIZE,
  );

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);
  const navigation = useNavigation();
  const routeProps = useAppRouteProps();

  // callbacks
  const onTimeSeriesSelected: DataGridProps['onSelectionChange'] = useCallback(
    (_e: any, data: OnSelectionChangeData) => {
      setSelectedTimeSeries(data.selectedItems);
      setSelectedItems(data.selectedItems);
    },
    [setSelectedTimeSeries],
  );

  const fetchTimeSeries = useCallback(
    async (
      twinId: string,
      logConfig: Partial<FeatureEvent>,
      continuationToken?: string,
    ) => {
      return await client?.getTimeSeriesByTwinId(
        twinId,
        logConfig,
        continuationToken,
      );
    },
    [client],
  );

  const parseTimeSeriesResults = useCallback(
    (
      results: DataResponse<WithContinuationToken<TimeSeries[]>>,
      isCanceled: boolean,
    ) => {
      if (isCanceled) {
        return;
      }

      if (results && results.state === 'success') {
        setContinuationToken(results.data.continuationToken);
        setBufferedItems(
          (currentBufferedItems) =>
            currentBufferedItems + results.data.data.length,
        );
        let existingData: TimeSeries[] =
          timeseriesDataRequest.state === 'success'
            ? timeseriesDataRequest.data
            : [];
        setTimeseriesDataRequest(
          (currentDataRequest: DataRequest<TimeSeries[]>) => {
            existingData =
              currentDataRequest.state === 'success'
                ? currentDataRequest.data
                : [];
            return {
              state: 'success',
              // Type assertion is safe because we know the state is 'success'
              data: existingData.concat(results.data.data),
            };
          },
        );
        setPageCount(
          existingData.length
            ? Math.ceil(
                (existingData.length + results.data.data.length) / pageSize,
              )
            : 1,
        );
        debugLogging &&
          getLogger().debug('TimeSeries loaded. {timeSeries}', MODULE_NAME, {
            timeSeries: results,
          });
      } else {
        setTimeseriesDataRequest({
          state: 'error',
          errorCode: results?.errorCode || '500',
          errorMessage: results?.errorMessage || 'Unknown message',
        });
      }
    },
    [debugLogging, pageSize, timeseriesDataRequest],
  );

  const navigateToFormulaList = useCallback(
    async (twinId: string | undefined) => {
      if (!twinId) return;
      const path = getNestedPage('insights', routeProps.artifactId);
      await navigation?.navigate({ path: `${path}/${twinId}/list` });
    },
    [routeProps.artifactId, navigation],
  );

  // side effects
  // Fetch initial data
  useEffect(() => {
    let isCanceled = false;
    if (client && explorerRouteProps.twinId) {
      fetchTimeSeries(explorerRouteProps.twinId, {
        featureName: TwinVisualizationFeature.ExploreTwinDetails,
        activityName: ExploreTwinDetailsActivity.TimeSeriesDataPoints,
      }).then((results) => {
        if (results) {
          parseTimeSeriesResults(results, isCanceled);
        } else {
          // No results
          setTimeseriesDataRequest({
            state: 'error',
            errorCode: '500',
            errorMessage: 'Unknown message',
          });
        }
      });
    }

    return () => {
      // cleanup
      isCanceled = true;
    };
    // This is required to avoid updates due to the fetch and parse callbacks
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [client, debugLogging, explorerRouteProps.twinId]);

  // Fetch more data if needed
  useEffect(() => {
    let isCanceled = false;
    const nearEnd = currentPage * pageSize >= bufferedItems - pageSize;
    if (nearEnd && continuationToken) {
      if (client && explorerRouteProps.twinId) {
        setIsLoadingMore(true);
        fetchTimeSeries(
          explorerRouteProps.twinId,
          {
            featureName: TwinVisualizationFeature.ExploreTwinDetails,
            activityName: ExploreTwinDetailsActivity.TimeSeriesDataPoints,
          },
          continuationToken,
        ).then((results) => {
          setIsLoadingMore(false);
          if (results) {
            parseTimeSeriesResults(results, isCanceled);
          } else {
            // No results
            setTimeseriesDataRequest({
              state: 'error',
              errorCode: '500',
              errorMessage: 'Unknown message',
            });
          }
        });
      }
    }

    return () => {
      // cleanup
      isCanceled = true;
    };
    // This is required to avoid updates due to the fetch and parse callbacks
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [client, currentPage, explorerRouteProps.twinId, pageSize]);

  useEffect(() => {
    if (timeseriesDataRequest.state === 'success') {
      const timeSeries = timeseriesDataRequest.data ?? [];
      if (selectedTimeSeries.length === 0) {
        setPageSize(EXPANDED_PAGE_SIZE);
        const pageCount = Math.ceil(timeSeries.length / EXPANDED_PAGE_SIZE);
        setPageCount(pageCount);
        // Since de-selecting all timeseries will reduce page #, make sure we're not on a page that no longer exists
        if (currentPage > pageCount) {
          setCurrentPage(pageCount);
        }
      } else {
        if (pageSize === EXPANDED_PAGE_SIZE) {
          setPageSize(COMPACT_PAGE_SIZE);
          setPageCount(Math.ceil(timeSeries.length / COMPACT_PAGE_SIZE));
        }
      }
    }
  }, [currentPage, pageSize, selectedTimeSeries.length, timeseriesDataRequest]);

  // result
  const state: TwinTimeSeriesListState = useMemo(() => {
    let shownTimeSeries: TimeSeries[] = [];
    if (timeseriesDataRequest.state === 'success') {
      shownTimeSeries = timeseriesDataRequest.data.slice(
        (currentPage - 1) * pageSize,
        currentPage * pageSize,
      );
    }

    return {
      timeSeries: shownTimeSeries,
      timeSeriesRequestState: timeseriesDataRequest.state,
      page: currentPage,
      setCurrentPage,
      pageCount,
      setPageCount,
      selectedItems,
      setSelectedTimeSeries: onTimeSeriesSelected,
      isLoadingMore,
      navigateToFormulaList,
    };
  }, [
    currentPage,
    isLoadingMore,
    onTimeSeriesSelected,
    pageCount,
    pageSize,
    selectedItems,
    timeseriesDataRequest,
    navigateToFormulaList,
  ]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
