import { useCallback, useEffect, useMemo, useState } from 'react';
import { FormulaElement, getLogger, useDebugLogging } from '@do/core';
import {
  ExpressionEditorContextMenuProps,
  ExpressionEditorContextMenuState,
} from './ExpressionEditorContextMenu.types';
import { useInsightsPageContext } from '../../../../../../contexts/InsightsPageContext/InsightsPageContext';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'ExpressionEditorContextMenu.state';

const getInputPropertiesFromState = (formulaElements: FormulaElement[]) => {
  return formulaElements.filter((element) => element.type === 'InputProperty');
};
const getFunctionsFromState = (formulaElements: FormulaElement[]) => {
  return formulaElements.filter((element) => element.type === 'Function');
};
const getExpressionsFromState = (
  formulaElements: FormulaElement[],
  excludeExpressionId?: string,
) => {
  return formulaElements.filter(
    (element) =>
      element.type === 'Expression' && element.id !== excludeExpressionId,
  );
};

export const useExpressionEditorContextMenuState = (
  props: ExpressionEditorContextMenuProps,
): ExpressionEditorContextMenuState => {
  // contexts
  const { pageState } = useInsightsPageContext();
  // state
  const formulaElements = pageState.currentFormula.elements;
  const selectedFormulaElementId = pageState.currentFormula.selectedElementId
    ? pageState.currentFormula.selectedElementId
    : undefined;
  const [inputProperties, setInputProperties] = useState(
    getInputPropertiesFromState(formulaElements || []),
  );
  const [functions, setFunctions] = useState(
    getFunctionsFromState(formulaElements || []),
  );
  const [expressions, setExpressions] = useState(
    getExpressionsFromState(formulaElements || [], selectedFormulaElementId),
  );
  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks
  const onMenuItemSelect = useCallback(
    (
      selectedElement: FormulaElement,
      event?: React.KeyboardEvent | React.MouseEvent,
    ) => {
      // Check if the event is a real mouse click
      if (event?.detail === 0) {
        // This is a click triggered by a keyboard press
        // Prevent the function from executing further
        return;
      }

      if (selectedElement) {
        props.handleMenuItemSelect(selectedElement.displayName);
      }
    },
    [props],
  );

  // side effects
  // update input properties and functions when formula elements change
  useEffect(() => {
    if (formulaElements) {
      const inputProperties = getInputPropertiesFromState(formulaElements);
      const functions = getFunctionsFromState(formulaElements);
      const expressions = getExpressionsFromState(
        formulaElements,
        selectedFormulaElementId,
      );
      setInputProperties(inputProperties);
      setFunctions(functions);
      setExpressions(expressions);
    }
  }, [formulaElements, selectedFormulaElementId]);

  // result
  const state: ExpressionEditorContextMenuState = useMemo(() => {
    return {
      inputProperties,
      functions,
      expressions,
      onMenuItemSelect,
    };
  }, [inputProperties, functions, expressions, onMenuItemSelect]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
