import React from 'react';
import { ITwinDetailsPageRenderProps } from './TwinDetailsPage.types';
import { ErrorBoundary, Page } from '@do/components';
import { t, strings } from '@do/localization';
import { Button } from '@fluentui/react-components';
import { ArrowLeft24Regular, Open20Regular } from '@fluentui/react-icons';
import { Route, Switch } from 'react-router-dom';
import {
  TwinEventsPanel,
  TwinTimeSeriesPanel,
  TwinDetailsPanel,
  TwinRelatedTwinsPanel,
  TwinDetailsNav,
} from '../details';
import { TimeSeriesContextProvider } from '../../../contexts/TimeSeriesContext';

export enum TestIds {
  Main = 'twin-details-main',
  Back = 'twin-details-back',
}

export const useTwinDetailsPageRender: React.FC<ITwinDetailsPageRenderProps> = (
  props,
) => {
  const { state, styles } = props;
  return (
    <Page testid={TestIds.Main}>
      <div className={styles.buttonRow} role='navigation'>
        <Button
          appearance='transparent'
          icon={<ArrowLeft24Regular />}
          className={styles.button}
          onClick={state.onBackClick}
          data-testid={TestIds.Back}
        >
          {t(strings.common.back)}
        </Button>
        <Button
          appearance='transparent'
          icon={<Open20Regular />}
          className={styles.button}
          onClick={state.openInNewTab}
        >
          {t(strings.common.open_in_new_tab)}
        </Button>
      </div>
      <div className={styles.main}>
        <h1>{state.twinId}</h1>
        <TwinDetailsNav />
        <Switch>
          <Route path={`${state.twinDetailsPath}/timeSeries`}>
            <TimeSeriesContextProvider>
              <TwinTimeSeriesPanel />
            </TimeSeriesContextProvider>
          </Route>
          <Route path={`${state.twinDetailsPath}/events`}>
            <TwinEventsPanel />
          </Route>
          <Route path={`${state.twinDetailsPath}/relatedTwins`}>
            <TwinRelatedTwinsPanel twinId={state.twinId} />
          </Route>
          <Route path='*'>
            <ErrorBoundary>
              <TwinDetailsPanel twinId={state.twinId} />
            </ErrorBoundary>
          </Route>
        </Switch>
      </div>
    </Page>
  );
};
