import { DataRequest } from '@do/core';
import { FormulaElement, FormulaData } from '@do/core';
import { InsightsPages } from '../../pages';

export type IInsightsPageContext = {
  pageState: InsightsPageContextState;
  pageDispatch: React.Dispatch<InsightsPageContextAction>;
};

export interface InsightsPageContextProviderProps {
  initialState?: Partial<InsightsPageContextState>;
}

export type FormulaContextData = {
  selectedElementId: string | null;
  selectedElement: FormulaElement | null;
} & FormulaData;

export type InsightsPageContextState = {
  currentFormula: FormulaContextData;
  selectedFormulaId: string | null | undefined; // TODO: put selectedFormulaID in FormulaContextData
  formulaList: DataRequest<FormulaData[]>;
  selectedTwinId: string | undefined;
  selectedTwinDisplayName: string | null | undefined;
  page: InsightsPages;
};

// Default empty formula to be used as currentFormula in the context
export const EMPTY_FORMULA: FormulaContextData = {
  // FormulaData properties
  id: '',
  displayName: '',
  elements: [],
  edges: [],
  scheduleRuns: undefined,
  // For formula builder editing interactions
  selectedElementId: null,
  selectedElement: null,
};

/**
 * The action names the Wizard reducer can handle
 * NOTE: be sure to add the name to one of the collections of actions so they get routed to the right reducer
 */

export enum InsightsPageContextActionType {
  /** selects a node from the current formula */
  SELECTED_NODE_SET = 'SELECTED_NODE_SET',
  /** marks the data call for loading the formula list for a given twin as complete and stores the data */
  FORMULA_LIST_LOADED = 'FORMULA_LIST_LOADED',
  /** marks the data call to fetch the list of formulas for a particular twin as in-progress */
  FORMULA_LIST_LOADING = 'FORMULA_LIST_LOADING',
  /** marks the data call for loading a specific formula as completed and stores the data */
  CURRENT_FORMULA_SET = 'CURRENT_FORMULA_SET',
  /** adds a new element to the graph with the provided data */
  FORMULA_ADD_ELEMENT = 'FORMULA_ADD_ELEMENT',
  /** removes all elements from the graph with the provided ids */
  FORMULA_REMOVE_ELEMENTS = 'FORMULA_REMOVE_ELEMENTS',
  /** updates the selected element with the provided details */
  FORMULA_UPDATE_ELEMENT = 'FORMULA_UPDATE_ELEMENT',
  /** replaces existing relationships between the provided source nodes and the currently selected element */
  SELECTED_NODE_UPDATE_SOURCES = 'SELECTED_NODE_UPDATE_SOURCES',
  /** will show the list page with whatever data is already stored via `FORMULA_LIST_LOADED` */
  NAVIGATE_TO_LIST = 'NAVIGATE_TO_LIST',
  /** will show the flow editor page for the given formula id. Expects that formula to exist in the FORMULA_LIST via `FORMULA_LIST_LOADED` */
  NAVIGATE_TO_BUILDER = 'NAVIGATE_TO_BUILDER',
  /** will show the empty state page */
  NAVIGATE_TO_EMPTY_STATE = 'NAVIGATE_TO_EMPTY_STATE',
  /** set the currentFormula's displayName */
  FORMULA_SET_DISPLAY_NAME = 'FORMULA_SET_DISPLAY_NAME',
}

/** The actions to update the state */
export type InsightsPageContextAction =
  | {
      type: InsightsPageContextActionType.SELECTED_NODE_SET;
      payload: { value: string };
    }
  | {
      type: InsightsPageContextActionType.NAVIGATE_TO_BUILDER;
      payload: {
        twinId: string | undefined;
        formulaId: string | undefined;
        twinDisplayName: string | undefined;
      };
    }
  | {
      type: InsightsPageContextActionType.NAVIGATE_TO_LIST;
      payload: {
        twinId: string;
        twinDisplayName: string;
      };
    }
  | {
      type: InsightsPageContextActionType.FORMULA_LIST_LOADING;
      payload: { twinId: string };
    }
  | {
      type: InsightsPageContextActionType.FORMULA_LIST_LOADED;
      payload: { data: FormulaData[] };
    }
  | {
      type: InsightsPageContextActionType.FORMULA_ADD_ELEMENT;
      payload: { element: FormulaElement };
    }
  | {
      type: InsightsPageContextActionType.FORMULA_REMOVE_ELEMENTS;
      payload: { ids: string[] };
    }
  | {
      type: InsightsPageContextActionType.FORMULA_UPDATE_ELEMENT;
      payload: { element: FormulaElement };
    }
  | {
      type: InsightsPageContextActionType.SELECTED_NODE_UPDATE_SOURCES;
      payload: { newSourceNodeIds: string[] };
    }
  | {
      type: InsightsPageContextActionType.CURRENT_FORMULA_SET;
      payload: { formula: FormulaData | undefined };
    }
  | {
      type: InsightsPageContextActionType.NAVIGATE_TO_EMPTY_STATE;
      payload: { twinId: string };
    }
  | {
      type: InsightsPageContextActionType.FORMULA_SET_DISPLAY_NAME;
      payload: { displayName: string };
    };
