import { LineChartStyles, LineChartClasses } from './LineChart.types';
import { makeStyles } from '@fluentui/react-components';

export const useClassNames: () => LineChartStyles =
  makeStyles<LineChartClasses>({
    root: {},
    spinner: {
      height: '100%',
    },
    chartContainer: {
      display: 'flex',
      flexDirection: 'column',
      maxWidth: '100%',
    },
  });
