import React from 'react';
import { IEventDetailsRenderProps } from './EventDetails.types';
import {
  Drawer,
  DrawerBody,
  DrawerHeader,
  DrawerHeaderTitle,
} from '@fluentui/react-components/unstable';
import { Button } from '@fluentui/react-components';
import {
  ArrowDownload16Regular,
  Copy16Regular,
  Dismiss16Regular,
} from '@fluentui/react-icons';
import { strings, t } from '@do/localization';
import { Editor, ErrorMessage, Loading } from '@do/components';

export enum EventPanelTestIds {
  CloseButton = 'events-details-panel-close',
  CopyButton = 'events-details-panel-copy',
  DownloadButton = 'events-details-panel-download',
}

export const useEventDetailsRender: React.FC<IEventDetailsRenderProps> = ({
  state,
  styles,
}) => {
  return (
    <Drawer
      className={styles.root}
      position='end'
      open={true}
      modalType='non-modal'
      size='medium'
      onOpenChange={state.onClose}
    >
      <DrawerHeader>
        <DrawerHeaderTitle
          action={
            <Button
              data-testid={EventPanelTestIds.CloseButton}
              appearance='subtle'
              aria-label='Close'
              icon={<Dismiss16Regular />}
              onClick={state.onClose}
            />
          }
        >
          {`${
            state.eventId
              ? state.eventId
              : t(strings.explorer.twins.events.no_name)
          }`}
        </DrawerHeaderTitle>
      </DrawerHeader>
      <DrawerBody className={styles.content}>
        {state.event.state === 'uncalled' || state.event.state === 'pending' ? (
          <Loading />
        ) : state.event.state === 'error' ? (
          <ErrorMessage message={state.event.errorMessage} />
        ) : (
          <>
            <div className={styles.buttonContainer}>
              <Button
                icon={<Copy16Regular />}
                onClick={state.onCopyClick}
                data-testid={EventPanelTestIds.CopyButton}
              >
                {t(strings.common.copy)}
              </Button>
              <Button
                onClick={state.onDownloadClick}
                icon={<ArrowDownload16Regular />}
                data-testid={EventPanelTestIds.DownloadButton}
              >
                {t(strings.common.download)}
              </Button>
            </div>
            <div className={styles.eventDetails}>
              <Editor
                options={{
                  readOnly: true,
                  language: 'json',
                  value: JSON.stringify(state.event.data ?? '', null, 4),
                }}
              />
            </div>
          </>
        )}
      </DrawerBody>
    </Drawer>
  );
};
