import {
  IIconizedDropdownStyles,
  IconizedDropdownClasses,
} from './IconizedDropdown.types';
import { makeStyles } from '@fluentui/react-components';

export const useClassNames: () => IIconizedDropdownStyles =
  makeStyles<IconizedDropdownClasses>({
    option: {
      display: 'flex',
      alignItems: 'justify',
      flexDirection: 'row',
      columnGap: '4px',
    },
    button: {
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'justify',
      columnGap: '4px',
    },
  });
