import React from 'react';
import { ExpressionEditorContextMenuProps } from './ExpressionEditorContextMenu.types';
import { useExpressionEditorContextMenuRender } from './ExpressionEditorContextMenu.render';
import { useClassNames } from './ExpressionEditorContextMenu.styles';
import { useExpressionEditorContextMenuState } from './ExpressionEditorContextMenu.state';

const MODULE_NAME = 'ExpressionEditorContextMenu';

export const ExpressionEditorContextMenu: React.FC<
  ExpressionEditorContextMenuProps
> = (props) => {
  // get state
  const state = useExpressionEditorContextMenuState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useExpressionEditorContextMenuRender({ state, styles });
};

ExpressionEditorContextMenu.displayName = MODULE_NAME;
