import React, { Suspense, lazy } from 'react';
import { Route, Switch, useRouteMatch } from 'react-router-dom';
import { Spinner } from '@fluentui/react-components';
import {
  ExtensionRoutes,
  AppNames,
  SearchParams,
  SharedStateContextProvider,
  getLogger,
  operationalInsightsRootPath,
} from '@do/core';
import { useClassNames } from './App.styles';
import { MainNav } from '../MainNav';
import { TwinDetailsPage } from '@do/explorer/components/twins/TwinDetailsPage';

const ExplorerPage = lazy(() =>
  import('@do/explorer').then((module) => ({ default: module.ExplorerPage })),
);
const PreparePage = lazy(() =>
  import('@do/prepare').then((module) => ({ default: module.PreparePage })),
);
const InsightsPage = lazy(() =>
  import('@do/insights').then((module) => ({ default: module.InsightsPage })),
);

// I would really like to be able to combine these two lazy imports to one deconstructed return, doesn't seem possible
const CreateDialog = lazy(() =>
  import('@do/home').then((module) => ({
    default: module.CreateDialog,
  })),
);
const HomePage = lazy(() =>
  import('@do/home').then((module) => ({
    default: module.HomePage,
  })),
);

const MODULE_NAME = 'App';

export function App() {
  const classNames = useClassNames();
  return (
    <Switch>
      {/* Create is outside the ArtifactId route since it won't have artifact id */}
      <Route path={ExtensionRoutes.create} exact>
        <Suspense fallback={<Spinner className={classNames.spinner} />}>
          <CreateDialog isOpen={true} />
        </Suspense>
      </Route>
      {/* Inside existing artifact */}
      <Route
        path={operationalInsightsRootPath(
          `:${SearchParams.ARTIFACT_OBJECT_ID}`,
        )}
      >
        <SharedStateContextProvider>
          <div className={classNames.root}>
            {/* Content */}
            <ArtifactEditors classNames={classNames} />
            {/* Nav */}
            <MainNav />
          </div>
        </SharedStateContextProvider>
      </Route>
      <Route
        path='*'
        render={(routeProps) => {
          getLogger().warn(MODULE_NAME, `Route not found. {Location}`, {
            route: routeProps.location,
          });
          return (
            <div>
              <div>Route not found</div>
              <div>Location: {JSON.stringify(routeProps.location)}</div>
            </div>
          );
        }}
      />
    </Switch>
  );
}

const ArtifactEditors: React.FC<{ classNames: Record<'spinner', string> }> = ({
  classNames,
}) => {
  const { path } = useRouteMatch();
  return (
    <Switch>
      {/* Content section */}
      <Route path={`${path}/${AppNames.explorer}/twins/details/:twinId`}>
        <TwinDetailsPage />
      </Route>
      <Route path={`${path}/${AppNames.explorer}`}>
        <Suspense fallback={<Spinner className={classNames.spinner} />}>
          <ExplorerPage />
        </Suspense>
      </Route>
      <Route path={`${path}/${AppNames.prepare}`}>
        <Suspense fallback={<Spinner className={classNames.spinner} />}>
          <PreparePage />
        </Suspense>
      </Route>
      <Route path={`${path}/${AppNames.insights}`}>
        <Suspense fallback={<Spinner className={classNames.spinner} />}>
          <InsightsPage />
        </Suspense>
      </Route>
      <Route path={[`${path}/`, `${path}/${AppNames.home}`]}>
        <Suspense fallback={<Spinner className={classNames.spinner} />}>
          <HomePage />
        </Suspense>
      </Route>
      <Route
        path='*'
        render={(routeProps) => {
          getLogger().warn(MODULE_NAME, `Inner Route not found. {Location}`, {
            route: routeProps.location,
          });
          return (
            <div>
              <div>Inner Route not found</div>
              <div>Location: {JSON.stringify(routeProps.location)}</div>
            </div>
          );
        }}
      />
    </Switch>
  );
};
