import React from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { useMainNavRender } from './MainNav.render';
import { useClassNames } from './MainNav.styles';
import { useMainNavState } from './MainNav.state';

const MODULE_NAME = 'MainNav';

export const MainNav: React.FC = () => {
  // get state
  const state = useMainNavState();
  const debugLogging = useDebugLogging();

  // get styles
  const styles = useClassNames();

  debugLogging &&
    getLogger().debug('Render. {state, styles}', MODULE_NAME, {
      state,
      styles,
    });

  // render the component
  return useMainNavRender({ state, styles });
};

MainNav.displayName = MODULE_NAME;
