import React from 'react';
import { LoadingProps } from './Loading.types';
import { useLoadingRender } from './Loading.render';
import { useClassNames } from './Loading.styles';
import { useLoadingState } from './Loading.state';

const MODULE_NAME = 'Loading';

export const Loading: React.FC<LoadingProps> = (props) => {
  // get state
  const state = useLoadingState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useLoadingRender({ state, styles });
};

Loading.displayName = MODULE_NAME;
