import { useMemo } from 'react';
import { IMainNavState } from './MainNav.types';
import React from 'react';
import { SelectTabEvent, SelectTabData } from '@fluentui/react-components';
import { getNestedPage, useAppRouteProps, useNavigation } from '@do/core';

export const useMainNavState = (): IMainNavState => {
  // contexts

  // state
  const routeProps = useAppRouteProps();
  const [artifactId] = React.useState<string>(routeProps.artifactId);

  // hooks
  const navigation = useNavigation();

  // callbacks

  // side effects

  // result
  const state: IMainNavState = useMemo(() => {
    return {
      artifactId,
      selectedTab: routeProps.app,
      onTabChange: (_event: SelectTabEvent, data: SelectTabData) => {
        const path = getNestedPage(data.value as string, routeProps.artifactId);
        navigation?.navigate({ path });
      },
    };
  }, [artifactId, navigation, routeProps.app, routeProps.artifactId]);

  return state;
};
