import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { SteppedWizardProps, SteppedWizardState } from './SteppedWizard.types';
import { Step } from './Stepper/Stepper.types';
import { StepProps } from './Step/Step.types';
import { usePrevious } from '@fluentui/react-hooks';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'SteppedWizard.state';

export const useSteppedWizardState = ({
  open,
  children,
  className,
  startIndex,
  submitText,
  submitDisabled,
  onOpenChange,
  onSubmit,
  onPreviousStep,
  onNextStep,
  onStepChange,
}: SteppedWizardProps): SteppedWizardState => {
  // contexts

  // state
  const [stepIndex, setStepIndex] = useState(startIndex ?? 0);

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks
  const calculateStepStatus = useCallback(
    (childIndex: number, onValidate?: () => boolean) => {
      return (
        // if we haven't reached that step, undefined
        childIndex > stepIndex
          ? undefined
          : // if that step has a validation, run it for success/error
            onValidate
            ? onValidate()
              ? 'success'
              : 'error'
            : // otherwise, success
              'success'
      );
    },
    [stepIndex],
  );

  // memos
  const childItems = useMemo(() => {
    return React.Children.map(children, (child, childIndex) => {
      if (child === null || child === undefined) return null; // if a step is conditionally rendered
      const stepProps = child as React.ReactElement<StepProps>;
      const componentDisplayName = (stepProps.type as any).displayName;
      //Does not handle a React Fragment and children. Multiple <Step> within <></> will not work.
      if (
        stepProps &&
        componentDisplayName !== 'Step' &&
        componentDisplayName !== 'SteppedWizardStartStep'
      ) {
        throw new Error(
          'SteppedWizard only accepts children of type `Step` or `SteppedWizardStartStep`.',
        );
      } else {
        return {
          step: <Step>{
            label: stepProps.props.title,
            status: calculateStepStatus(childIndex, stepProps.props.onValidate),
          },
          content: child,
        };
      }
    });
  }, [calculateStepStatus, children]);

  const isValid = useMemo(() => {
    return React.Children.toArray(children).every((child) => {
      const stepProps = child as React.ReactElement<StepProps>;
      return stepProps.props.onValidate ? stepProps.props.onValidate() : true;
    });
  }, [children]);

  const canProceed = useMemo(() => {
    const stepProps = React.Children.toArray(children)[
      stepIndex
    ] as React.ReactElement<StepProps>;
    return stepProps?.props.onValidate?.() ?? true;
  }, [children, stepIndex]);

  // side effects
  const previousOpen = usePrevious(open);
  useEffect(() => {
    // reset the wizard when it is opened
    if (!previousOpen && open) {
      setStepIndex(startIndex ?? 0);
    }
  }, [previousOpen, open, startIndex]);
  // notify parent of step change
  useEffect(() => {
    onStepChange?.(stepIndex);
  }, [stepIndex, onStepChange]);

  const updateStepIndex = React.useMemo(() => {
    return {
      onNextStep: () => {
        setStepIndex((i) => {
          const nextStepIndex = i + 1;
          onNextStep?.(nextStepIndex);
          return nextStepIndex;
        });
      },
      onPreviousStep: () => {
        setStepIndex((i) => {
          const previousStepIndex = i - 1;
          onPreviousStep?.(previousStepIndex);
          return previousStepIndex;
        });
      },
    };
  }, [onPreviousStep, onNextStep]);

  // result
  const state: SteppedWizardState = useMemo(() => {
    return {
      submitText,
      submitDisabled,
      className,
      currentStep: stepIndex,
      onSubmit: () => onSubmit?.(),
      steps: childItems?.map((child) => child.step) ?? [],
      contentPanels: childItems?.map((child) => child.content) ?? [],
      onNextStep: updateStepIndex.onNextStep,
      onPreviousStep: updateStepIndex.onPreviousStep,
      isValid,
      canProceed,
      open: open,
      onOpenChange: onOpenChange,
    };
  }, [
    submitText,
    submitDisabled,
    className,
    stepIndex,
    childItems,
    updateStepIndex.onNextStep,
    updateStepIndex.onPreviousStep,
    isValid,
    canProceed,
    open,
    onOpenChange,
    onSubmit,
  ]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
