import { FluentIcon } from '@fluentui/react-icons/lib/utils/createFluentIcon';

export interface SimpleDropdownItem {
  /** The underlying value of the item */
  key: string;
  /** The user-facing string of the item */
  text: string;
  /** Render the component as disabled */
}

export interface IconizedDropdownItem extends SimpleDropdownItem {
  /** A Fluent-compliant icon */
  icon: FluentIcon;
  iconPosition: 'leading' | 'trailing';
}

export type ItemGroup = {
  /** A user-facing string for the item grouping */
  label: string;
  /** Simple or iconized items in the group */
  items: (SimpleDropdownItem | IconizedDropdownItem)[];
};
/**
 * Controls the colors and borders of the combobox trigger.
 * @default 'outline'
 */

export type AppearanceType =
  | 'filled-darker'
  | 'filled-lighter'
  | 'outline'
  | 'underline';

export interface DropdownProps {
  /**
   * Controls the colors and borders of the combobox trigger.
   * @default 'outline'
   */
  appearance?: AppearanceType;
  /** Since you may be providing a label externally or not at all, we need the localized ariaLabel string for accessibility */
  ariaLabel: string;
  /** The value that is selected, often updated in consumer's state */
  value: string | undefined;
  /** The callback to set value in consuming component's state */
  setValue: (value: string) => void;
  /** Render the dropdown as disabled */
  disabled?: boolean;
  dataTestid?: string;
  /** The default value displayed in the trigger input or button when the combobox's value is uncontrolled */
  placeholder?: string;
  /** The class name */
  className?: string;
  /** Show the loading spinner in place of expanded icon if true */
  loading?: boolean;
  /** Show the clear button to allow a user to clear their selection */
  clearable?: boolean;
}

export interface DropdownState {
  appearance: AppearanceType;
  ariaLabel: string;
  value: string | undefined;
  setValue: (value: string) => void;
  selectedValues?: string[] | undefined;
  disabled?: boolean;
  dataTestId: string;
  defaultValue?: string | undefined;
  placeholder?: string;
  loading?: boolean;
  clearable?: boolean;
}
