import { PositioningVirtualElement } from '@fluentui/react-components';

export function triggerContextMenu( // position and open the context menu
  editor: any,
  event: any,
  setTrue: () => void,
  menuPositioningRef: React.MutableRefObject<any>,
) {
  const position = event.target.position;
  if (position) {
    const top = editor.getTopForLineNumber(position.lineNumber);
    const left = editor.getOffsetForColumn(
      position.lineNumber,
      position.column,
    );
    const editorRect = editor.getDomNode().getBoundingClientRect();
    const viewportTop = top + editorRect.top + 190;
    const viewportLeft = left + editorRect.left;

    const virtualElement: PositioningVirtualElement = {
      getBoundingClientRect: () => ({
        width: 0,
        height: 0,
        x: viewportLeft,
        y: viewportTop,
        top: viewportTop,
        right: viewportLeft,
        left: viewportLeft,
        bottom: viewportTop,
      }),
    };

    menuPositioningRef.current?.setTarget(virtualElement);
    setTrue(); // Open the context menu
  }
}

export function handleExpressionErrors( // highlight syntax errors in the editor
  editor: any,
  monaco: any,
  expressionValidator: any,
) {
  const expression = editor.getValue();
  const syntaxErrors =
    expressionValidator.validateCalculationExpression(expression);
  const monacoErrors = [];
  for (const e of syntaxErrors) {
    monacoErrors.push({
      startLineNumber: e.startLine,
      startColumn: e.startCol,
      endLineNumber: e.endLine,
      endColumn: e.endCol,
      message: e.message,
      severity: monaco.MarkerSeverity.Error,
    });
  }

  const models = monaco.editor.getModels();
  const model = models[models.length - 1]; // TODO: Implement model reuse to avoid performance issues
  monaco.editor.setModelMarkers(
    // TODO: Update Error Highlighting to align with Figma
    model,
    'errors',
    monacoErrors,
  );
}

export function handleValueSelected( // insert the selected value from context menu into the editor
  selectedItemName: string,
  editorRef: React.MutableRefObject<any>,
  monaco: any,
  setFalse: () => void,
) {
  const position = editorRef.current.getPosition();
  const range = new monaco.Range(
    position.lineNumber,
    position.column,
    position.lineNumber,
    position.column,
  );
  const id = { major: 1, minor: 1 }; //operation Id
  const text = selectedItemName;
  const op = { identifier: id, range, text, forceMoveMarkers: true };
  editorRef.current.executeEdits('my-source', [op]);
  setFalse();
  // Refocus the editor
  editorRef.current.focus();
  // Move cursor to the end of the inserted value
  const newPosition = position.delta(0, selectedItemName.length);
  editorRef.current.setPosition(newPosition);
}
