import { useCallback, useMemo, useState } from 'react';
import {
  GraphEdge,
  GraphLayouts,
  GraphNode,
  IGraphProps,
  IGraphState,
  NodeActions,
} from './Graph.types';
import { DefaultCytoscapeStylesheet } from './Graph.styles';

export const useGraphState = (props: IGraphProps): IGraphState => {
  // contexts

  // state
  const [elements, setElements] = useState(() => {
    if (!props.data) {
      return [];
    } else {
      const elements: { data: GraphNode | GraphEdge }[] = [];
      if (props.data.nodes) {
        const nodes = props.data.nodes.map((e: GraphNode) => ({
          data: e,
        }));
        elements.push(...nodes);
      }
      if (props.data.edges) {
        const edges = props.data.edges.map((e: GraphEdge) => ({
          data: e,
        }));
        elements.push(...edges);
      }
      return elements;
    }
  });

  // hooks

  // callbacks
  const getAllChildrenTwinIds = useCallback(
    (twinId: string) => {
      const directChildrenTwinIds = props.data.edges
        .filter((e: any) => e.source === twinId)
        .map((e: any) => e.target);
      const recursiveChildrenTwinIds: string[] = [];
      directChildrenTwinIds.forEach((childId) => {
        // TODO: make this iterative
        recursiveChildrenTwinIds.push(childId);
        recursiveChildrenTwinIds.push(...getAllChildrenTwinIds(childId));
      });
      return recursiveChildrenTwinIds;
    },
    [props.data?.edges],
  );

  const handleExpandCollapse = useCallback(
    (twinId: string, action: NodeActions) => {
      const directChildrenTwinIds = props.data.edges
        .filter((e: any) => e.source === twinId)
        .map((e: any) => e.target);

      if (action === NodeActions.expand) {
        setElements((prevElements) => {
          const newElements = Object.assign([], prevElements);
          directChildrenTwinIds.forEach((childId) => {
            // add direct children nodes to graph elements
            if (prevElements.findIndex((e) => e.data.id === childId) === -1) {
              const childNode = props.data.nodes.find(
                (n: GraphNode) => n.id === childId,
              );
              if (childNode) {
                newElements.push({
                  data: childNode,
                });
              }
            }

            // add direct children edges to graph elements
            if (
              prevElements.findIndex(
                (e) =>
                  (e.data as GraphEdge).source === twinId &&
                  (e.data as GraphEdge).target === childId,
              ) === -1
            ) {
              const relationEdge = props.data.edges.find(
                (n: GraphEdge) => n.source === twinId && n.target === childId,
              );
              if (relationEdge) {
                newElements.push({
                  data: relationEdge,
                });
              }
            }
          });
          return newElements;
        });
      } else {
        // collapse node by shaking the subtree for all descendents nodes and edges
        const allChildrenTwinwIds = getAllChildrenTwinIds(twinId);
        setElements((prevElements) => {
          return prevElements.filter(
            (e) =>
              allChildrenTwinwIds.indexOf(e.data.id) === -1 &&
              !(
                (e.data as GraphEdge).source === twinId ||
                allChildrenTwinwIds.includes((e.data as GraphEdge).source)
              ) /** also remove edges that are connected to the all the descendent nodes that are being collapsed in subtrees, 
              otherwise the nodes will be added when expanded again but no edges will be rendered, 
              so the removal and addition of nodes and edges should be together */,
          );
        });
      }
    },
    [getAllChildrenTwinIds, props.data?.edges, props.data?.nodes],
  );

  // side effects

  // result
  const state: IGraphState = useMemo(() => {
    return {
      elements,
      layout: props.layout ?? GraphLayouts.breadthfirst,
      cytoscapeStylesheet:
        props.cytoscapeStylesheet ?? DefaultCytoscapeStylesheet,
      handleExpandCollapse,
    };
  }, [elements, handleExpandCollapse, props.cytoscapeStylesheet, props.layout]);

  return state;
};
