import React from 'react';
import { IHierarchyTreeProps } from './HierarchyTree.types';
import { useHierarchyTreeRender } from './HierarchyTree.render';
import { useClassNames } from './HierarchyTree.styles';
import { useHierarchyTreeState } from './HierarchyTree.state';

const MODULE_NAME = 'HierarchyTree';

export const HierarchyTree: React.FC<IHierarchyTreeProps> = (props) => {
  // get state
  const state = useHierarchyTreeState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useHierarchyTreeRender({
    state,
    styles,
  });
};

HierarchyTree.displayName = MODULE_NAME;
