import {
  FilterRowType,
  LocalizedOption,
  Property,
  ValueType,
} from '../Filters.types';

/** input parameters to FilterRow */
export interface FilterRowProps {
  rowIndex: number; // Used in the setters to update the correct row
  totalRows: number; // Used to determine if the delete button should be enabled
  propertyOptions: Property[]; // The list of properties to render in the property dropdown
  operatorOptions?: LocalizedOption[]; // The list of operators to render in the operator dropdown
  logicalOperatorOptions?: LocalizedOption[]; // The list of logical operators to render in the logical operator dropdown
  property: string; // The selected property
  propertyType: FilterRowType; // Used to render the correct operators, value control, and validations
  setProperty: (index: number, property: string, valueType: ValueType) => void; // The setter for the property
  operator: string; // The selected operator
  setOperator: (index: number, operator: string) => void; // The setter for the operator
  logicalOperator?: string; // The selected logical operator
  setLogicalOperator?: (index: number, logicalOperator: string) => void; // The setter for the logical operator
  value: string; // The value entered by the user
  setValue: (index: number, value: string) => void; // The setter for the value
  timeSeriesStartTimeUTC?: string;
  setTimeSeriesStartTimeUTC?: (index: number, time: string) => void;
  timeSeriesEndTimeUTC?: string;
  setTimeSeriesEndTimeUTC?: (index: number, time: string) => void;
  deleteFilterRule: (index: number) => void; // The callback to delete the row
  allowDeleteFinalRow: boolean; // If true, the delete button will be enabled for the last row
  setFilterValid?: (index: number, isValid: boolean) => void;
}

/** state values used for rendering the item */
export type FilterRowState = {
  isDeleteRowEnabled: boolean;
  propertyValidationMessage?: string;
  operatorValidationMessage?: string;
  valueValidationMessage?: string;
} & Pick<
  FilterRowProps,
  | 'rowIndex'
  | 'propertyOptions'
  | 'operatorOptions'
  | 'propertyType'
  | 'property'
  | 'setProperty'
  | 'operator'
  | 'setOperator'
  | 'value'
  | 'setValue'
  | 'logicalOperator'
  | 'setLogicalOperator'
  | 'deleteFilterRule'
  | 'allowDeleteFinalRow'
  | 'timeSeriesStartTimeUTC'
  | 'setTimeSeriesStartTimeUTC'
  | 'timeSeriesEndTimeUTC'
  | 'setTimeSeriesEndTimeUTC'
>;

/** the props send to the render function */
export type FilterRowRenderProps = {
  state: FilterRowState;
  styles: FilterRowStyles;
};

/** the names of styling classes for FilterRow */
export type FilterRowClasses =
  | 'root'
  | 'propertyOption'
  | 'propertyType'
  | 'firstFilterColumn'
  | 'dropdownListbox'
  | 'timeSeriesRow'
  | 'newLine'
  | 'timeSeriesDateTime';
/** the computed styles object output from the styles hook */
export type FilterRowStyles = Record<FilterRowClasses, string>;

export const FilterRowOptionDelimiter = '';
