import React from 'react';
import { SortRowProps } from './SortRow.types';
import { useSortRowRender } from './SortRow.render';
import { useClassNames } from './SortRow.styles';
import { useSortRowState } from './SortRow.state';

const MODULE_NAME = 'SortRow';

export const SortRow: React.FC<SortRowProps> = (props) => {
  // get state
  const state = useSortRowState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useSortRowRender({ state, styles });
};

SortRow.displayName = MODULE_NAME;
