import { useCallback, useEffect } from 'react';
import {
  TwinDetailsClientType,
  getLogger,
  useDebugLogging,
  useTwinDetailsClient,
} from '@do/core';
import { FeatureEvent } from '@trident/certified-events';
import {
  ITwinDetailsPanelProps,
  TwinDetailsActionType,
  TwinDetailsPanelState,
} from './TwinDetailsPanel.types';
import { useTwinDetailsPanelReducer } from './TwinDetailsPanel.reducer';
import {
  ExploreTwinDetailsActivity,
  TwinVisualizationFeature,
} from '../../../../models/TwinVisualizationCertifiedEvents';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'TwinDetailsPanel.state';

export const useTwinDetailsPanelState = (
  props: ITwinDetailsPanelProps,
): TwinDetailsPanelState => {
  // contexts

  // state
  const client = useTwinDetailsClient();
  const [state, dispatch] = useTwinDetailsPanelReducer();

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks
  const loadProperties = useCallback(
    async (
      client: TwinDetailsClientType,
      twinId: string,
      logConfig: Partial<FeatureEvent>,
    ) => {
      if (state.properties.state !== 'pending') return;
      try {
        const payload = await client.getDetails(twinId, logConfig);
        if (payload?.state === 'success') {
          dispatch({
            type: TwinDetailsActionType.LOADED_PROPERTIES,
            payload,
          });
        } else {
          dispatch({
            type: TwinDetailsActionType.FAILED_TO_LOAD_PROPERTIES,
            payload,
          });
        }
      } catch (error) {
        dispatch({
          type: TwinDetailsActionType.FAILED_TO_LOAD_PROPERTIES,
          payload: {
            state: 'error',
            errorMessage: error.message,
            errorCode: '',
          },
        });
      }
    },
    [dispatch, state.properties.state],
  );

  const loadAttributes = useCallback(
    async (
      client: TwinDetailsClientType,
      twinId: string,
      logConfig: Partial<FeatureEvent>,
    ) => {
      if (state.attributes.state !== 'pending') return;
      try {
        const payload = await client.getAttributes(twinId, logConfig);
        if (payload?.state === 'success') {
          dispatch({
            type: TwinDetailsActionType.LOADED_ATTRIBUTES,
            payload,
          });
        } else {
          dispatch({
            type: TwinDetailsActionType.FAILED_TO_LOAD_ATTRIBUTES,
            payload,
          });
        }
      } catch (error) {
        dispatch({
          type: TwinDetailsActionType.FAILED_TO_LOAD_ATTRIBUTES,
          payload: {
            state: 'error',
            errorMessage: error.message,
            errorCode: '',
          },
        });
      }
    },
    [dispatch, state.attributes.state],
  );

  const loadLabels = useCallback(
    async (
      client: TwinDetailsClientType,
      twinId: string,
      logConfig: Partial<FeatureEvent>,
    ) => {
      if (state.labels.state !== 'pending') return;
      try {
        const payload = await client.getLabels(twinId, logConfig);
        if (payload?.state === 'success') {
          dispatch({
            type: TwinDetailsActionType.LOADED_LABELS,
            payload,
          });
        } else {
          dispatch({
            type: TwinDetailsActionType.FAILED_TO_LOAD_LABELS,
            payload,
          });
        }
      } catch (error) {
        dispatch({
          type: TwinDetailsActionType.FAILED_TO_LOAD_LABELS,
          payload: {
            state: 'error',
            errorMessage: error.message,
            errorCode: '',
          },
        });
      }
    },
    [dispatch, state.labels.state],
  );

  // side effects
  useEffect(() => {
    if (!props.twinId) {
      return; // can't do anything without a twinId
    }
    if (props.twinId && state.properties.state === 'uncalled') {
      // when properties haven't been fetched, everything needs fetched
      dispatch({
        type: TwinDetailsActionType.LOADING_ALL,
      });
    }
    if (client && state.properties.state === 'pending') {
      //go fetch that data
      loadProperties(client, props.twinId, {
        featureName: TwinVisualizationFeature.ExploreTwinDetails,
        activityName: ExploreTwinDetailsActivity.TwinDetailsCalled,
      });
    }
    if (client && state.attributes.state === 'pending') {
      loadAttributes(client, props.twinId, {
        featureName: TwinVisualizationFeature.ExploreTwinDetails,
        activityName: ExploreTwinDetailsActivity.TwinAttributesCalled,
      });
    }
    if (client && state.labels.state === 'pending') {
      loadLabels(client, props.twinId, {
        featureName: TwinVisualizationFeature.ExploreTwinDetails,
        activityName: ExploreTwinDetailsActivity.TwinLabelsCalled,
      });
    }
  }, [
    state,
    props.twinId,
    client,
    dispatch,
    loadProperties,
    loadAttributes,
    loadLabels,
  ]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state;
};
