import React from 'react';
import { Dropdown, Field, Option } from '@fluentui/react-components';
import { strings, t } from '@do/localization';
import { SortRowRenderProps } from './SortRow.types';
import { LocalizedOption } from '../Filters.types';

export enum TestIds {
  PropertyDropdown = 'sort-row-property-picker',
  OrderDropdown = 'sort-row-order-picker',
}

export const useSortRowRender: React.FC<SortRowRenderProps> = ({
  state,
  styles,
}) => {
  const orderOptions: LocalizedOption[] = [
    {
      name: 'ASC',
      localizedString: t(strings.explorer.query_panel.order_ascending),
    },
    {
      name: 'DESC',
      localizedString: t(strings.explorer.query_panel.order_descending),
    },
  ];

  return (
    <div className={styles.root}>
      <Field
        aria-label={t(strings.explorer.query_panel.property_picker_placeholder)}
        validationMessage={state.propertyValidationMessage}
      >
        <Dropdown
          placeholder={t(
            strings.explorer.query_panel.property_picker_placeholder,
          )}
          value={state.property}
          onOptionSelect={(_ev, selection) =>
            state.setProperty(`${selection.optionValue}`)
          }
          data-testid={TestIds.PropertyDropdown}
        >
          {state.propertyOptions.map((option) => (
            <Option key={option.name} text={option.name}>
              <div className={styles.propertyOption}>
                <div>{option.name}</div>
                <div className={styles.propertyType}>
                  {option.valueType ?? option.type}
                </div>
              </div>
            </Option>
          ))}
        </Dropdown>
      </Field>
      <Field
        aria-label={t(strings.explorer.query_panel.order_picker_placeholder)}
        validationMessage={state.orderValidationMessage}
      >
        <Dropdown
          placeholder={t(strings.explorer.query_panel.order_picker_placeholder)}
          value={
            orderOptions.find((x) => x.name === state.order)?.localizedString ??
            state.order
          }
          onOptionSelect={(_ev, selection) =>
            state.setOrder(`${selection.optionValue}`)
          }
          data-testid={TestIds.OrderDropdown}
        >
          {orderOptions.map((option) => (
            <Option key={option.name} text={option.name}>
              {option.localizedString}
            </Option>
          ))}
        </Dropdown>
      </Field>
    </div>
  );
};
