/**
 * This file is a slightly modified version of the monaco-editor sample for webpack in github.
 * (https://github.com/microsoft/monaco-editor/blob/main/samples/browser-esm-webpack-typescript-react/src/components/Editor.tsx)
 *  */
import React, {
  useRef,
  useEffect,
  useImperativeHandle,
  forwardRef,
  useCallback,
} from 'react';
import { EditorProps, EditorRef } from './Editor.types';
import {
  Popover,
  PopoverSurface,
  PositioningImperativeRef,
  mergeClasses,
} from '@fluentui/react-components';
import { useClassNames } from './Editor.styles';
import * as expressionValidator from '@microsoft/digital-operations.calculation-validator';
import { ExpressionEditorContextMenu } from '@do/insights/components/InsightsDetailsPanel/components/ExpressionEditor/components/ExpressionEditorContextMenu';
import { useBoolean } from '@fluentui/react-hooks';
import {
  triggerContextMenu,
  handleExpressionErrors,
  handleValueSelected,
} from './Editor.utils';
import { t, strings } from '@do/localization';

// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
self.MonacoEnvironment = {
  getWorkerUrl: function (_moduleId: any, label: string) {
    if (label === 'json') {
      return `${window.location.origin}/json.worker.bundle.js`;
    }
    return `${window.location.origin}/json.worker.bundle.js`;
  },
};

export const Editor = forwardRef<EditorRef | undefined, EditorProps>(
  (props, ref) => {
    const monaco = useRef<any>(null); // Would need to import monaco-editor namespace for type
    const editorRef = useRef<any>(null); // Would need to import monaco-editor namespace for type
    const divEl = useRef<HTMLDivElement>(null);
    const styles = useClassNames();
    const [isContextMenuOpen, { setTrue, setFalse, toggle }] =
      useBoolean(false);
    const menuPositioningRef = useRef<PositioningImperativeRef>(null);

    useEffect(() => {
      async function createEditor() {
        if (!monaco.current) {
          const monacoImport = await import('monaco-editor');
          monaco.current = monacoImport;

          // Register the language here
          const language =
            props.language ?? props.options.language ?? 'plaintext';
          if (
            !monaco.current.languages
              .getLanguages()
              .some((lang: { id: string }) => lang.id === language)
          ) {
            monaco.current.languages.register({
              id: language,
            });
          }

          if (divEl.current) {
            editorRef.current = monaco.current.editor.create(
              divEl.current,
              props.options,
            );
          }
          if (props.isExpressionEditor) {
            const editor = editorRef.current;

            // Disable the browser's default context menu
            editor
              .getDomNode()
              .addEventListener('contextmenu', (event: any) => {
                event.preventDefault();
              });

            // Listen for right-clicks in the editor
            editor.onContextMenu((event: any) => {
              triggerContextMenu(editor, event, setTrue, menuPositioningRef);
            });

            editor.onDidChangeModelContent(() => {
              handleExpressionErrors(
                editor,
                monaco.current,
                expressionValidator,
              );
            });

            editor.onDidFocusEditorText(() => {
              if (
                editor.getValue() ===
                t(strings.insights.expression_editor.placeholder)
              ) {
                // clear the placeholder text when the editor is focused
                editor.setValue('');
              }
            });

            // editor.onDidBlurEditorText(() => {
            //   //TODO: fix conflict with context menu
            //   if (editor.getValue().trim() === '') {
            //     // set the placeholder text when the editor is blurred and the value is empty
            //     editor.setValue(
            //       t(strings.insights.expression_editor.placeholder),
            //     );
            //   }
            // });
          }
        } else {
          editorRef.current.setValue(props.options.value);
        }
      }

      createEditor();
    }, [
      props.options,
      props.isExpressionEditor,
      props.language,
      setTrue,
      setFalse,
    ]);

    useImperativeHandle(
      ref,
      () => {
        return {
          getValue: () => {
            return editorRef.current.getValue();
          },
          setValue: (value: string) => {
            editorRef.current.setValue(value);
          },
          insertValueAtCursor: (value: string) => {
            // handles adding the values selected from the context menu into the editor.
            handleValueSelected(
              value,
              editorRef,
              monaco.current,
              // eslint-disable-next-line @typescript-eslint/no-empty-function
              () => {}, // no-op, setFalse
            );
          },
        };
      },
      [],
    );
    const onChangeContextMenuVisibility = useCallback(
      (value?: boolean) => {
        if (value === undefined) {
          toggle();
        } else {
          value ? setTrue() : setFalse();
        }
      },
      [setFalse, setTrue, toggle],
    );

    const onValueSelected = (value: string) => {
      // handles adding values selected from the context menu into the editor
      handleValueSelected(value, editorRef, monaco.current, setFalse);
    };

    return (
      <div className={mergeClasses('Editor', styles.root)} ref={divEl}>
        {props.isExpressionEditor && (
          <Popover
            open={isContextMenuOpen}
            onOpenChange={
              /* istanbul ignore next */
              (e, data) =>
                // handle the popover dismissing itself when you click away
                onChangeContextMenuVisibility(data.open)
            }
            positioning={{ positioningRef: menuPositioningRef }}
          >
            <PopoverSurface>
              <ExpressionEditorContextMenu
                handleMenuItemSelect={onValueSelected}
              />
            </PopoverSurface>
          </Popover>
        )}
      </div>
    );
  },
);
