import { produce } from 'immer';
import {
  HierarchyTreeActionType,
  HierarchyTreeAction,
  TreeNode,
  HierarchyReducerState,
} from './HierarchyTree.types';
import { SuccessDataResponse, getLogger } from '@do/core';
import { hierarchyDeepFindByPath } from './HierarchyTree.utils';
import React from 'react';

export const debugLogging = true;
export const MODULE_NAME = 'HierarchyTreeReducer';

const INITIAL_TREE: HierarchyReducerState = {
  state: 'uncalled',
  nodes: [] as TreeNode[],
};
export const HierarchyTreeReducer: (
  draft: HierarchyReducerState,
  action: any,
) => HierarchyReducerState = produce(
  (draft: HierarchyReducerState, action: HierarchyTreeAction) => {
    debugLogging &&
      getLogger().info(
        MODULE_NAME,
        `Updating HierarchyTreeReducer context ${action.type} with payload: `,
        (action as any).payload,
      );

    switch (action.type) {
      case HierarchyTreeActionType.TOGGLE_NODE: {
        const node = hierarchyDeepFindByPath(action.payload.path, draft.nodes);
        if (node !== undefined) {
          node.open = !node.open;
        }
        break;
      }
      case HierarchyTreeActionType.LOADING_ROOTS: {
        draft.state = 'pending';
        break;
      }
      case HierarchyTreeActionType.FAILED_TO_LOAD_ROOTS: {
        draft.state = 'error';
        draft.errorCode = action.payload.errorCode;
        draft.errorMessage = action.payload.errorMessage;
        break;
      }
      case HierarchyTreeActionType.LOADED_ROOTS: {
        draft.state = 'success';
        draft.nodes = action.payload.nodes;
        draft.continuationToken = action.payload.continuationToken;
        draft.continuationUri = action.payload.continuationUri;
        break;
      }
      case HierarchyTreeActionType.LOADING_MORE_ROOTS: {
        draft.isLoadingMore = true;
        break;
      }
      case HierarchyTreeActionType.LOADED_MORE_ROOTS: {
        draft.isLoadingMore = false;
        draft.state = 'success';
        draft.nodes = [...draft.nodes, ...action.payload.nodes];
        draft.continuationToken = action.payload.continuationToken;
        draft.continuationUri = action.payload.continuationUri;
        break;
      }
      case HierarchyTreeActionType.LOADING_CHILDREN: {
        const node = hierarchyDeepFindByPath(action.payload.path, draft.nodes);
        if (node !== undefined) {
          if (node.children === undefined) {
            node.children = {
              state: 'pending',
            };
          } else {
            node.children.state = 'pending';
          }
        }
        break;
      }
      case HierarchyTreeActionType.LOADED_CHILDREN: {
        const node = hierarchyDeepFindByPath(action.payload.path, draft.nodes);
        if (node !== undefined) {
          if (node.children === undefined) {
            node.children = {
              state: 'success',
              data: action.payload.nodes || [],
            };
          } else {
            node.children.state = 'success';
            if (action.payload.nodes) {
              (node.children as SuccessDataResponse<TreeNode[]>).data =
                action.payload.nodes;
            }
          }
          node.continuationToken = action.payload.continuationToken;
          node.continuationUri = action.payload.continuationUri;
        }
        break;
      }
      case HierarchyTreeActionType.LOADING_MORE_CHILDREN: {
        const node = hierarchyDeepFindByPath(action.payload.path, draft.nodes);
        if (node !== undefined) {
          node.isLoadingMore = true;
        }
        break;
      }
      case HierarchyTreeActionType.LOADED_MORE_CHILDREN: {
        const node = hierarchyDeepFindByPath(action.payload.path, draft.nodes);
        if (node !== undefined) {
          node.isLoadingMore = false;
          if (node.children === undefined) {
            node.children = {
              state: 'success',
              data: [],
            };
          }
          node.children.state = 'success';
          (node.children as SuccessDataResponse<TreeNode[]>).data = [
            ...(node.children as SuccessDataResponse<TreeNode[]>).data,
            ...action.payload.nodes,
          ];
          node.continuationToken = action.payload.continuationToken;
          node.continuationUri = action.payload.continuationUri;
          //be sure to set node open
          node.open = true;
        }
        break;
      }
      case HierarchyTreeActionType.FAILED_TO_LOAD_CHILDREN: {
        const node = hierarchyDeepFindByPath(action.payload.path, draft.nodes);
        if (node !== undefined) {
          node.children = {
            state: 'error',
            errorCode: action.payload.errorCode,
            errorMessage: action.payload.errorMessage,
          };
        }
        break;
      }
      case HierarchyTreeActionType.FAILED_TO_LOAD_MORE_CHILDREN: {
        const node = hierarchyDeepFindByPath(action.payload.path, draft.nodes);
        if (node !== undefined) {
          node.isLoadingMore = false;
        }
        break;
      }
    }
    return draft;
  },
);

export const useHierarchyTreeReducer = () => {
  const [treeState, treeDispatch] = React.useReducer(
    HierarchyTreeReducer,
    INITIAL_TREE,
  );
  return { treeState, treeDispatch };
};
