import React from 'react';
import { SteppedWizardRenderProps } from './SteppedWizard.types';
import { Stepper } from './Stepper';
import {
  Button,
  Dialog,
  DialogBody,
  DialogContent,
  DialogSurface,
  DialogTitle,
  DialogTrigger,
  mergeClasses,
} from '@fluentui/react-components';
import { t, strings } from '@do/localization';
import { Dismiss12Regular } from '@fluentui/react-icons';

export const TestIds = {
  DialogRoot: 'stepped-wizard-dialog-root',
  NextButton: 'stepped-wizard-next-button',
  BackButton: 'stepped-wizard-back-button',
  SubmitButton: 'stepped-wizard-submit-button',
  CloseButton: 'stepped-wizard-close-button',
};

export const useSteppedWizardRender: React.FC<SteppedWizardRenderProps> = ({
  state,
  styles,
}) => {
  return (
    <Dialog
      modalType='alert'
      open={state.open}
      onOpenChange={state.onOpenChange}
    >
      <DialogSurface
        className={styles.dialogSurface}
        data-testid={TestIds.DialogRoot}
      >
        <DialogBody className={styles.dialogBody}>
          <DialogContent
            className={mergeClasses(styles.dialogContent, state.className)}
          >
            <div className={styles.stepper}>
              <Stepper stepIndex={state.currentStep} steps={state.steps} />
            </div>
            <div className={styles.rightPane}>
              <DialogTitle className={styles.dialogTitle}>
                <DialogTrigger action='close'>
                  <Button
                    appearance='subtle'
                    aria-label={t(strings.common.close)}
                    icon={<Dismiss12Regular />}
                    data-testid={TestIds.CloseButton}
                  />
                </DialogTrigger>
              </DialogTitle>
              <div className={styles.content}>
                {state.contentPanels[state.currentStep]}
              </div>
              <div className={styles.footer}>
                <Button
                  onClick={state.onPreviousStep}
                  disabled={state.currentStep === 0}
                  data-testid={TestIds.BackButton}
                >
                  {t(strings.common.back)}
                </Button>
                {
                  // if we're on the last step, show submit button, otherwise show next button
                  state.currentStep + 1 === state.steps.length ? (
                    <Button
                      appearance='primary'
                      onClick={state.onSubmit}
                      disabled={false === state.isValid || state.submitDisabled}
                      data-testid={TestIds.SubmitButton}
                    >
                      {state.submitText ?? t(strings.common.submit)}
                    </Button>
                  ) : (
                    <Button
                      appearance='primary'
                      onClick={state.onNextStep}
                      disabled={!state.canProceed}
                      data-testid={TestIds.NextButton}
                    >
                      {t(strings.common.next)}
                    </Button>
                  )
                }
              </div>
            </div>
          </DialogContent>
        </DialogBody>
      </DialogSurface>
    </Dialog>
  );
};
