import React from 'react';
import { IEventDetailsProps } from './EventDetails.types';
import { useEventDetailsRender } from './EventDetails.render';
import { useClassNames } from './EventDetails.styles';
import { useEventDetailsState } from './EventDetails.state';

const MODULE_NAME = 'EventDetails';

export const EventDetails: React.FC<IEventDetailsProps> = (_props) => {
  // get state
  const state = useEventDetailsState(_props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useEventDetailsRender({ state, styles });
};

EventDetails.displayName = MODULE_NAME;
