import React from 'react';
import { IIconizedDropdownRenderProps } from './IconizedDropdown.types';
import {
  Dropdown,
  Option,
  OptionGroup,
  Spinner,
} from '@fluentui/react-components';
import {
  IconizedDropdownItem,
  ItemGroup,
  SimpleDropdownItem,
} from '../DropdownTypes';

export const useIconizedDropdownRender: React.FC<
  IIconizedDropdownRenderProps
> = ({ state, styles }) => {
  const generateGroup = (group: ItemGroup, index: number) => (
    <OptionGroup label={group.label} key={`optiongroup_${index}`}>
      {group.items.map((item) => generateOption(item))}
    </OptionGroup>
  );

  const generateOption = (item: SimpleDropdownItem | IconizedDropdownItem) => {
    //icon exists and has a value
    if ('icon' in item && item.icon) {
      return (
        <Option key={item.key} value={item.key} text={item.text}>
          <div className={styles.option}>
            {/* Not trailing, default to leading */}
            {item.iconPosition !== 'trailing' && <item.icon role='img' />}
            <div>{item.text}</div>
            {item.iconPosition === 'trailing' && <item.icon role='img' />}
          </div>
        </Option>
      );
    } else {
      return (
        <Option key={item.key} value={item.key}>
          {item.text}
        </Option>
      );
    }
  };

  const getDropdownItemButton = () => {
    const item = state.internalSelectedItem;
    if (!item) {
      return state.value;
    }
    //icon exists and has a value
    if ('icon' in item && item.icon) {
      return (
        <div className={styles.button}>
          {item.iconPosition !== 'trailing' && <item.icon role='img' />}
          <div>{item.text}</div>
          {item.iconPosition === 'trailing' && <item.icon role='img' />}
        </div>
      );
    } else {
      return item.text;
    }
  };

  return (
    <Dropdown
      clearable={state.clearable}
      className={state.className}
      placeholder={state.placeholder}
      appearance={state.appearance}
      aria-label={state.ariaLabel}
      selectedOptions={state.selectedValues}
      value={state.defaultValue}
      onOptionSelect={(_, value) => {
        state.setValue(value.optionValue as string);
      }}
      data-testid={state.dataTestId}
      disabled={state.disabled}
      expandIcon={state.loading ? <Spinner size='tiny' /> : undefined}
      button={{
        disabled: state.disabled,
        children: getDropdownItemButton(),
      }}
    >
      {state.items?.map((item, index) => {
        if ('items' in item) {
          return generateGroup(item, index);
        } else {
          return generateOption(item);
        }
      })}
    </Dropdown>
  );
};
