import React from 'react';
import { TwinTimeSeriesListRenderProps } from './TwinTimeSeriesList.types';
import {
  DataGrid,
  DataGridBody,
  DataGridCell,
  DataGridHeader,
  DataGridHeaderCell,
  DataGridRow,
  Link,
  TableColumnDefinition,
  createTableColumn,
} from '@fluentui/react-components';
import { t, strings } from '@do/localization';
import {
  EmptyContents,
  ErrorMessage,
  Loading,
  PageFooter,
} from '@do/components';
import { TimeSeries, TimeSeriesSourceType } from '@do/core';
import { TIME_SERIES_DELIMETER } from '../../../../../../contexts/TimeSeriesContext';

export enum TimeSeriesListTestIds {
  Row = 'time-series-row',
  List = 'time-series-list',
}

export const useTwinTimeSeriesListRender: React.FC<
  TwinTimeSeriesListRenderProps
> = ({ state, styles }) => {
  // Column definitions
  const columns: TableColumnDefinition<TimeSeries>[] = [
    createTableColumn({
      columnId: 'timeSeriesName',
      compare(a, b) {
        return a.displayName?.localeCompare(b.displayName as string) ?? -1;
      },
      renderCell: (timeSeries: TimeSeries) => {
        return timeSeries.displayName;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader}>
            {t(strings.explorer.twins.time_series.name)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'timeSeriesId',
      compare(a, b) {
        return (
          a.timeSeriesDataPointId?.localeCompare(
            b.timeSeriesDataPointId as string,
          ) ?? -1
        );
      },
      renderCell: (timeSeries: TimeSeries) => {
        return timeSeries.timeSeriesDataPointId;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader}>
            {t(strings.explorer.twins.time_series.id)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'twinId',
      compare(a, b) {
        return a.twinId?.localeCompare(b.twinId as string) ?? -1;
      },
      renderCell: (timeSeries: TimeSeries) => {
        return timeSeries.twinId;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader}>
            {t(strings.explorer.twins.time_series.twin_id)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'formulas',
      compare(a, b) {
        return a.source?.localeCompare(b.source) ?? -1;
      },
      renderCell: (timeSeries: TimeSeries) => {
        return timeSeries.source === TimeSeriesSourceType.Calculated ? (
          <Link
            as='a'
            onClick={(event) => {
              event.stopPropagation();
              state.navigateToFormulaList(timeSeries.twinId);
            }}
          >
            {t(strings.insights.formulas.list.formulas_link_placeholder)}
          </Link>
        ) : (
          '-'
        );
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader}>
            {t(strings.explorer.twins.time_series.formula)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'description',
      compare(a, b) {
        return a.description?.localeCompare(b.description as string) ?? -1;
      },
      renderCell: (timeSeries: TimeSeries) => {
        return timeSeries.description;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader}>
            {t(strings.explorer.twins.time_series.description)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'unitOfMeasure',
      compare(a, b) {
        return a.uom?.localeCompare(b.uom as string) ?? -1;
      },
      renderCell: (timeSeries: TimeSeries) => {
        return timeSeries.uom;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader}>
            {t(strings.explorer.twins.time_series.unit_of_measure)}
          </span>
        );
      },
    }),
  ];

  if (
    state.timeSeriesRequestState === 'pending' ||
    state.timeSeriesRequestState === 'uncalled' ||
    state.isLoadingMore
  ) {
    return (
      <div className={styles.root}>
        <Loading />
      </div>
    );
  }

  if (state.timeSeriesRequestState === 'success') {
    if (!state.timeSeries.length) {
      return (
        <div className={styles.root}>
          <EmptyContents
            header={t(
              strings.explorer.time_series.time_series_list.empty_state,
            )}
          />
        </div>
      );
    }

    return (
      <div className={styles.root}>
        {state.timeSeries.length > 0 ? (
          <div data-testid={TimeSeriesListTestIds.List}>
            <DataGrid
              items={state.timeSeries}
              columns={columns}
              sortable
              selectionMode={'multiselect'}
              selectedItems={state.selectedItems}
              onSelectionChange={state.setSelectedTimeSeries}
              getRowId={
                (timeSeries: TimeSeries) =>
                  `${timeSeries.timeSeriesDataPointId}${TIME_SERIES_DELIMETER}${timeSeries.twinId}` // Emoji delimeter to avoid other common GUID formats
              }
              className={styles.dataGrid}
            >
              <DataGridHeader>
                <DataGridRow>
                  {({ renderHeaderCell }) => (
                    <DataGridHeaderCell>
                      {renderHeaderCell()}
                    </DataGridHeaderCell>
                  )}
                </DataGridRow>
              </DataGridHeader>
              <DataGridBody<TimeSeries>>
                {({ item, rowId }) => (
                  <DataGridRow<TimeSeries>
                    data-testid={TimeSeriesListTestIds.Row}
                    key={rowId}
                  >
                    {({ renderCell }) => (
                      <DataGridCell>{renderCell(item)}</DataGridCell>
                    )}
                  </DataGridRow>
                )}
              </DataGridBody>
            </DataGrid>
            <div className={styles.pageFooter}>
              <PageFooter
                initialPage={state.page} // Needs to keep track of state page because it wil be re-rendered when loading more pages
                pageCount={state.pageCount}
                onPageChange={(page) => state.setCurrentPage(page)}
              />
            </div>
          </div>
        ) : (
          <EmptyContents
            header={t(
              strings.explorer.time_series.time_series_list.empty_state,
            )}
          />
        )}
      </div>
    );
  }

  return (
    <div className={styles.root}>
      <ErrorMessage message={t(strings.common.error)} />
    </div>
  );
};
