import React from 'react';
import { ITwinTimeSeriesPanelRenderProps } from './TwinTimeSeriesPanel.types';
import { TwinTimeSeriesList } from './components/TwinTimeSeriesList';
import { TimeSeriesChart } from '../../../TimeSeriesChart';

export enum TestIds {
  Main = 'twin-details-time-series-panel-main',
}

export const useTwinTimeSeriesPanelRender: React.FC<
  ITwinTimeSeriesPanelRenderProps
> = ({ state, styles }) => {
  return (
    <div data-testid={TestIds.Main} className={styles.root}>
      <div className={styles.list}>
        <TwinTimeSeriesList />
      </div>
      {state.selectedTimeSeries.length != 0 ? (
        <div className={styles.chartContainer}>
          <TimeSeriesChart />
        </div>
      ) : null}
    </div>
  );
};
