import React from 'react';
import {
  SchedulingSectionProps,
  SchedulingSectionRef,
} from './SchedulingSection.types';
import { useSchedulingSectionRender } from './SchedulingSection.render';
import { useClassNames } from './SchedulingSection.styles';

const MODULE_NAME = 'SchedulingSection';

export const SchedulingSection = React.forwardRef<
  SchedulingSectionRef | undefined,
  SchedulingSectionProps
>(({ className, ...state }, ref) => {
  // get styles
  const styles = useClassNames();

  // render the item
  return useSchedulingSectionRender({ className, state, styles }, ref);
});

SchedulingSection.displayName = MODULE_NAME;
