import React, { useCallback, useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import {
  IIconizedDropdownProps,
  IIconizedDropdownState,
} from './IconizedDropdown.types';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'IconizedDropDown.state';

export const useIconizedDropdownState = (
  props: IIconizedDropdownProps,
): IIconizedDropdownState => {
  // contexts

  // state
  const [localValue, setLocalValue] = React.useState<string | undefined>(
    props.value,
  );

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks
  const setValue = useCallback(
    (value: string) => {
      setLocalValue(value);
      props.setValue(value);
    },
    [props],
  );

  // memoized
  const item = useMemo(() => {
    const value = localValue;

    for (const dropdownItem of props.items) {
      // is it a group?
      if ('items' in dropdownItem) {
        const found = dropdownItem.items.find(
          (subItem: any) => subItem.key === value,
        );
        if (found) {
          return found;
        }
      } else if ('key' in dropdownItem && dropdownItem.key === value) {
        return dropdownItem;
      }
    }
    return undefined;
  }, [localValue, props.items]);

  const { selectedValues, defaultValue } = useMemo(() => {
    if (item) {
      return {
        selectedValues: [item.key],
        defaultValue: item.text,
      };
    } else {
      return {
        selectedValues: [],
        defaultValue: localValue,
      };
    }
  }, [item, localValue]);

  // result
  const state: IIconizedDropdownState = useMemo(() => {
    return {
      ...props,
      appearance: props.appearance || 'outline',
      value: localValue,
      setValue,
      defaultValue,
      selectedValues,
      internalSelectedItem: item,
      dataTestId: props.dataTestid || 'iconizedDropDown',
    };
  }, [props, localValue, setValue, defaultValue, selectedValues, item]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state;
};
