import { useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { SortRowProps, SortRowState } from './SortRow.types';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'SortRow.state';

export const useSortRowState = (props: SortRowProps): SortRowState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state: SortRowState = useMemo(() => {
    return {
      // TODO: Temporary hack to filter out attributes, timeseries, and label until sort is supported for these
      propertyOptions: props.propertyOptions.filter(
        (option) =>
          option.source !== 'Attribute' &&
          option.source !== 'TimeSeries' &&
          option.source !== 'Label',
      ),
      property: props.property,
      setProperty: props.setProperty,
      order: props.order,
      setOrder: props.setOrder,
      propertyValidationMessage: props.propertyValidationMessage,
      orderValidationMessage: props.orderValidationMessage,
    };
  }, [
    props.propertyOptions,
    props.property,
    props.setProperty,
    props.order,
    props.setOrder,
    props.propertyValidationMessage,
    props.orderValidationMessage,
  ]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
