import { useCallback, useEffect, useMemo } from 'react';
import {
  HierarchyClientType,
  getLogger,
  useDebugLogging,
  useHierarchyClient,
} from '@do/core';
import {
  IHierarchyTreeProps,
  IHierarchyTreeState,
  TreeItemType,
  HierarchyRequest,
  HierarchyTreeActionType,
  TreeNode,
} from './HierarchyTree.types';
import { TestIds } from './HierarchyTree.render';
import { useHierarchyTreeReducer } from './HierarchyTree.reducer';
import { hierarchyDeepFindByPath } from './HierarchyTree.utils';
import {
  TwinVisualizationFeature,
  ExploreHierarchyActivity,
} from '../../explorer/models/TwinVisualizationCertifiedEvents';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'HierarchyTree.state';

export const useHierarchyTreeState = (
  props: IHierarchyTreeProps,
): IHierarchyTreeState => {
  // contexts

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);
  const client: HierarchyClientType | undefined = useHierarchyClient();

  // state
  const { treeState, treeDispatch } = useHierarchyTreeReducer();

  // callbacks
  const onLoadMoreFromPath = useCallback(
    async (
      path: string,
      continuationToken: string,
      continuationUri: string,
    ): Promise<HierarchyRequest | undefined> => {
      if (client && treeState.state === 'success') {
        //we've loaded stuff, we can do things
        getLogger().logStandardizedCertifiedEvent({
          featureName: TwinVisualizationFeature.ExploreHierarchy,
          activityName: ExploreHierarchyActivity.ShowMoreClicked,
        });
        if (path === '') {
          treeDispatch({
            type: HierarchyTreeActionType.LOADING_MORE_ROOTS,
          });
          //we're loading more roots
          const newRoots = await client.getRoots(
            {
              featureName: TwinVisualizationFeature.ExploreHierarchy,
              activityName: ExploreHierarchyActivity.RootsCalled,
            },
            continuationToken,
            continuationUri,
          );

          if (newRoots.state === 'error') {
            treeDispatch({
              type: HierarchyTreeActionType.FAILED_TO_LOAD_ROOTS,
              payload: {
                errorCode: newRoots.errorCode,
                errorMessage: newRoots.errorMessage,
              },
            });
          }
          if (newRoots.state === 'success') {
            treeDispatch({
              type: HierarchyTreeActionType.LOADED_MORE_ROOTS,
              payload: {
                nodes: newRoots.data.value,
                continuationToken: newRoots.data.continuationToken,
                continuationUri: newRoots.data.continuationUri,
              },
            });
          }
        } else {
          //we're loading more children nodes
          treeDispatch({
            type: HierarchyTreeActionType.LOADING_MORE_CHILDREN,
            payload: { path },
          });

          const newChildren = await client.getChildren(
            path,
            {
              featureName: TwinVisualizationFeature.ExploreHierarchy,
              activityName: ExploreHierarchyActivity.ChildrenCalled,
            },
            continuationToken,
            continuationUri,
          );
          if (newChildren.state === 'success') {
            treeDispatch({
              type: HierarchyTreeActionType.LOADED_MORE_CHILDREN,
              payload: {
                path,
                nodes: newChildren.data.value,
                continuationToken: newChildren.data.continuationToken,
                continuationUri: newChildren.data.continuationUri,
              },
            });
          }
          if (newChildren.state === 'error') {
            treeDispatch({
              type: HierarchyTreeActionType.FAILED_TO_LOAD_MORE_CHILDREN,
              payload: {
                path,
                errorCode: newChildren.errorCode,
                errorMessage: newChildren.errorMessage,
              },
            });
          }
        }
      }
      return;
    },
    [client, treeDispatch, treeState.state],
  );

  const processChildren = useCallback(
    async (
      hierarchyClient: HierarchyClientType,
      path: string,
      node: TreeNode,
    ) => {
      // if node already has children, we're just done
      if (node.children && node.children.state === 'success') {
        treeDispatch({
          type: HierarchyTreeActionType.LOADED_CHILDREN,
          payload: {
            path: node.path,
            nodes: undefined,
          },
        });
        return;
      }
      // go get the children
      const children = await hierarchyClient.getChildren(
        path,
        {
          featureName: TwinVisualizationFeature.ExploreHierarchy,
          activityName: ExploreHierarchyActivity.ChildrenCalled,
        },
        node.continuationToken,
        node.continuationUri,
      );
      if (children?.state === 'success') {
        treeDispatch({
          type: HierarchyTreeActionType.LOADED_CHILDREN,
          payload: {
            path,
            nodes: children.data.value,
            continuationToken: children.data.continuationToken,
            continuationUri: children.data.continuationUri,
          },
        });
        return;
      }
      if (children?.state === 'error') {
        treeDispatch({
          type: HierarchyTreeActionType.FAILED_TO_LOAD_CHILDREN,
          payload: {
            path,
            errorCode: children.errorCode,
            errorMessage: children.errorMessage,
          },
        });
        return;
      }
    },
    [treeDispatch],
  );

  const onOpenChange = useCallback(
    async (path: string) => {
      if (client) {
        getLogger().logStandardizedCertifiedEvent({
          featureName: TwinVisualizationFeature.ExploreHierarchy,
          activityName: ExploreHierarchyActivity.ExpandClicked,
        });
        //we've loaded stuff, we can do things
        treeDispatch({
          type: HierarchyTreeActionType.LOADING_CHILDREN,
          payload: { path },
        });
        const node = hierarchyDeepFindByPath(path, treeState.nodes);
        if (node) {
          processChildren(client, path, { ...node });
        } else {
          treeDispatch({
            type: HierarchyTreeActionType.FAILED_TO_LOAD_CHILDREN,
            payload: {
              path: path,
              errorCode: 'not found',
              errorMessage: 'node not found',
            },
          });
        }
      }
    },
    [client, processChildren, treeDispatch, treeState.nodes],
  );

  const loadRoots = useCallback(
    async (client: HierarchyClientType) => {
      const roots = await client.getRoots({
        featureName: TwinVisualizationFeature.ExploreHierarchy,
        activityName: ExploreHierarchyActivity.RootsCalled,
      });
      if (roots && roots.state === 'success') {
        treeDispatch({
          type: HierarchyTreeActionType.LOADED_ROOTS,
          payload: {
            nodes: roots.data?.value ?? [],
            continuationToken: roots.data?.continuationToken,
            continuationUri: roots.data?.continuationUri,
          },
        });
      }
      if (roots && roots.state === 'error') {
        treeDispatch({
          type: HierarchyTreeActionType.FAILED_TO_LOAD_ROOTS,
          payload: {
            errorCode: roots.errorCode,
            errorMessage: roots.errorMessage,
          },
        });
      }
    },
    [treeDispatch],
  );

  const onNodeClick = useCallback(
    async (path: string, hierarchyItemType: TreeItemType) => {
      if (props.onHierarchyItemClick) {
        const node = hierarchyDeepFindByPath(path, treeState.nodes);
        if (node) {
          props.onHierarchyItemClick(node, hierarchyItemType);
        }
      }
    },
    [props, treeState.nodes],
  );

  // side effects
  useEffect(() => {
    if (client && treeState.state === 'uncalled') {
      treeDispatch({ type: HierarchyTreeActionType.LOADING_ROOTS });
      loadRoots(client);
    }
  }, [client, loadRoots, treeDispatch, treeState.state]);

  // result
  const state: IHierarchyTreeState = useMemo(() => {
    return {
      dataTestId: props.dataTestId || TestIds.TreeRoot,
      state: treeState.state,
      nodes: treeState.nodes,
      selectedTwinId: props.selectedTwinId,
      continuationToken: treeState.continuationToken,
      continuationUri: treeState.continuationUri,
      onNodeClick: props.onHierarchyItemClick ? onNodeClick : undefined,
      customItemLayout: props.customItemLayout,
      onOpenChange,
      onLoadMoreFromPath,
      rootClassName: props.rootClassName,
      treeItemClassName: props.treeItemClassName,
      showMoreClassName: props.showMoreClassName,
    };
  }, [
    props.dataTestId,
    props.selectedTwinId,
    props.onHierarchyItemClick,
    props.customItemLayout,
    props.rootClassName,
    props.treeItemClassName,
    props.showMoreClassName,
    treeState.state,
    treeState.nodes,
    treeState.continuationToken,
    treeState.continuationUri,
    onNodeClick,
    onOpenChange,
    onLoadMoreFromPath,
  ]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
