import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import {
  DataResponse,
  ErrorDataResponse,
  TimeSeriesClientType,
  TimeSeriesControlsParameters,
  TimeSeriesPointData,
  WithContinuationToken,
  getLogger,
  useDebugLogging,
  useTimeSeriesClient,
  DataRequest,
} from '@do/core';
import {
  TimeSeriesChartProps,
  TimeSeriesChartState,
} from './TimeSeriesChart.types';
import { ChartType } from '../../contexts/TimeSeriesContext.types';
import {
  TwinVisualizationFeature,
  ExploreTimeseriesActivity,
  ExploreTimeSeriesAttribute,
} from '../../models/TwinVisualizationCertifiedEvents';
import { useTimeSeriesContext } from '../../contexts/TimeSeriesContext';
import {
  checkTimeSeriesHasValues,
  getTimeSeriesRangeBucket,
} from './utils/TimeSeriesUtils';
import { produce } from 'immer';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'TimeSeriesChart.state';

export const useTimeSeriesChartState = (
  _props: TimeSeriesChartProps,
): TimeSeriesChartState => {
  // contexts
  const client = useTimeSeriesClient();
  const {
    selectedTimeSeries,
    binInterval,
    aggregationMethod,
    startTime,
    endTime,
  } = useTimeSeriesContext();

  // internal functions
  const getTimeSeriesUniqueId = (
    twinId: string,
    timeSeriesDatapointId: string,
  ) => `${twinId}-${timeSeriesDatapointId}`;

  // state
  const [chartType, setChartType] = useState<ChartType>(ChartType.line);
  const [timeSeriesDataRequests, setTimeSeriesDataRequests] = useState<
    Record<string, DataRequest<TimeSeriesPointData[]>>
  >(() => {
    const initialTimeSeriesDataRequests: Record<
      string,
      DataRequest<TimeSeriesPointData[]>
    > = {};
    selectedTimeSeries.forEach((timeSeries) => {
      initialTimeSeriesDataRequests[
        getTimeSeriesUniqueId(
          timeSeries.twinId,
          timeSeries.timeSeriesDatapointId,
        )
      ] = {
        state: 'uncalled',
      };
    });
    return initialTimeSeriesDataRequests;
  });

  const setSingleRequestState = (
    uniqueId: string,
    state: DataRequest<TimeSeriesPointData[]>,
  ) => {
    setTimeSeriesDataRequests((currentRequests) => {
      const newRequests = produce(currentRequests, (draft) => {
        draft[uniqueId] = state;
      });
      return newRequests;
    });
  };

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);
  const previousOptions = useRef<TimeSeriesControlsParameters>({
    binWidth: Number(binInterval),
    aggType: aggregationMethod,
    dateTimeStart: startTime ?? '',
    dateTimeEnd: endTime ?? '',
  });

  // callbacks
  const fetchTimeSeriesData = useCallback(
    async (
      client: TimeSeriesClientType,
      twinId: string,
      timeSeriesDatapointId: string,
      isCanceled: boolean,
    ) => {
      if (isCanceled) {
        return;
      }

      // Intentionally using previous options to ensure time series are only fetched
      // using parameter values captured when the Apply button is clicked.
      const dateTimeStart = previousOptions.current.dateTimeStart;
      const dateTimeEnd = previousOptions.current.dateTimeEnd;

      const timeSeriesData: Record<string, TimeSeriesPointData[]> = {};
      const uniqueId = getTimeSeriesUniqueId(twinId, timeSeriesDatapointId);

      if (!dateTimeStart || !dateTimeEnd) {
        setSingleRequestState(uniqueId, {
          state: 'error',
          errorCode: '400',
          errorMessage: 'Start time and end time cannot be empty',
        });
        return;
      } else if (
        new Date(dateTimeStart).getTime() >= new Date(dateTimeEnd).getTime()
      ) {
        setSingleRequestState(uniqueId, {
          state: 'error',
          errorCode: '400',
          errorMessage:
            'Start time cannot be greater than or equal to end time',
        });
        return;
      }

      // Iterate through all pages of data
      let hasRequestFailed = false;
      let continuationToken: string | undefined = undefined;
      let requestIndex = 0;
      let hasContinuationToken = false;
      let results: DataResponse<WithContinuationToken<TimeSeriesPointData[]>>;
      const logConfig = {
        featureName: TwinVisualizationFeature.ExploreTimeseries,
        activityName: ExploreTimeseriesActivity.GetDataPointsCalled,
        activityAttributes: {
          [ExploreTimeSeriesAttribute.TimeSeriesRange]:
            getTimeSeriesRangeBucket(dateTimeStart, dateTimeEnd),
        },
      };
      do {
        results = await client.getTimeSeriesPointData(
          twinId,
          timeSeriesDatapointId,
          logConfig,
          {
            // Fetch data won't be called if start time and end time are empty
            dateTimeStart: new Date(dateTimeStart).toISOString(),
            dateTimeEnd: new Date(dateTimeEnd).toISOString(),
            aggType: previousOptions.current.aggType,
            binWidth: Number(previousOptions.current.binWidth), // casting required from dropdown option to number
            continuationToken: hasContinuationToken
              ? continuationToken
              : undefined,
          },
        );

        if (results && results.state === 'success') {
          if (results.data.continuationToken) {
            hasContinuationToken = true;
            continuationToken = results.data.continuationToken;
          } else {
            hasContinuationToken = false;
          }
          if (requestIndex === 0) {
            timeSeriesData[uniqueId] = results.data.data;
          } else {
            timeSeriesData[uniqueId] = timeSeriesData[uniqueId].concat(
              results.data.data,
            );
          }
          setSingleRequestState(uniqueId, {
            state: 'success',
            data: timeSeriesData[uniqueId],
          });
        } else {
          hasContinuationToken = false;
          setSingleRequestState(uniqueId, {
            state: 'error',
            errorCode: results.errorCode ?? '500',
            errorMessage: results.errorMessage ?? 'Unknown error',
          });
          hasRequestFailed = true;
        }
        requestIndex++;
      } while (hasContinuationToken && !hasRequestFailed);
    },
    [],
  );

  const applyParameterChanges = useCallback(() => {
    // If the parameters change then reset the state of all requests
    setTimeSeriesDataRequests((currentRequests) => {
      const newRequests = produce(currentRequests, (draft) => {
        Object.keys(currentRequests).forEach((key) => {
          draft[key] = { state: 'uncalled' };
        });
      });
      return newRequests;
    });
    previousOptions.current = {
      binWidth: Number(binInterval),
      aggType: aggregationMethod,
      dateTimeStart: startTime,
      dateTimeEnd: endTime,
    };
  }, [aggregationMethod, binInterval, endTime, startTime]);

  useEffect(() => {
    let isCanceled = false;
    if (client && selectedTimeSeries.length) {
      getLogger().logStandardizedCertifiedEvent({
        featureName: TwinVisualizationFeature.ExploreTimeseries,
        activityName: ExploreTimeseriesActivity.ChartRendered,
        activityAttributes: {
          [ExploreTimeSeriesAttribute.LinesDrawn]: selectedTimeSeries.length,
        },
      });
      selectedTimeSeries.forEach((timeSeries) => {
        const uniqueId = getTimeSeriesUniqueId(
          timeSeries.twinId,
          timeSeries.timeSeriesDatapointId,
        );
        if (timeSeriesDataRequests[uniqueId]) {
          if (timeSeriesDataRequests[uniqueId].state === 'success') {
            // Don't query for data if it exists
            return;
          }
        }

        if (
          !timeSeriesDataRequests[uniqueId] || // if the request is not in the state
          timeSeriesDataRequests[uniqueId].state === 'uncalled' // if the request is uncalled
        ) {
          setSingleRequestState(uniqueId, {
            state: 'pending',
          });
          fetchTimeSeriesData(
            client,
            timeSeries.twinId,
            timeSeries.timeSeriesDatapointId,
            isCanceled,
          );
        }
      });
    }

    return () => {
      // cleanup, cancel all pending requests if component unmounts
      isCanceled = true;
    };
  }, [client, selectedTimeSeries, timeSeriesDataRequests, fetchTimeSeriesData]);

  // result
  const state: TimeSeriesChartState = useMemo(() => {
    // Filter out the time series data requests that are not selected
    const selectedRequests = Object.fromEntries(
      Object.entries(timeSeriesDataRequests).filter(([key]) =>
        selectedTimeSeries.some(
          (ts) =>
            key === getTimeSeriesUniqueId(ts.twinId, ts.timeSeriesDatapointId),
        ),
      ),
    );

    const successfulDataRequests: Record<string, TimeSeriesPointData[]> = {};
    const failedDataRequests: ErrorDataResponse[] = [];
    let loadingDataRequests = 0;

    Object.keys(selectedRequests).forEach((key) => {
      const dataRequest = selectedRequests[key];
      switch (dataRequest.state) {
        case 'success':
          successfulDataRequests[key] = dataRequest.data;
          break;
        case 'error':
          failedDataRequests.push(dataRequest);
          break;
        // Loading or pending fall under same category
        case 'pending':
          loadingDataRequests++;
          break;
        case 'uncalled':
          loadingDataRequests++;
          break;
      }
    });

    const componentState: 'pending' | 'success' | 'empty' | 'error' =
      loadingDataRequests > 0
        ? 'pending'
        : Object.keys(successfulDataRequests).length > 0
          ? checkTimeSeriesHasValues(successfulDataRequests)
            ? 'success'
            : 'empty'
          : 'error';

    return {
      chartType: chartType,
      setChartType: setChartType,
      successfulDataRequests,
      failedDataRequests,
      componentState,
      onApplyClicked: applyParameterChanges,
    };
  }, [
    chartType,
    selectedTimeSeries,
    timeSeriesDataRequests,
    applyParameterChanges,
  ]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
