import {
  SteppedWizardStyles,
  SteppedWizardClasses,
} from './SteppedWizard.types';
import { makeStyles, shorthands, tokens } from '@fluentui/react-components';

/*
From figma so all these magic number make sense:
Screen size: 1920x1080
Modal size: 1440x864
Left panel: 227x864
Title? area: 1213x50
Upper right panel: 1213x702
Lower right panel: 1213x72
*/

export const useClassNames: () => SteppedWizardStyles =
  makeStyles<SteppedWizardClasses>({
    dialogSurface: {
      maxWidth: 'fit-content',
      ...shorthands.padding(0),
    },
    dialogBody: {
      ...shorthands.padding(0),
      ...shorthands.gap('0px'),
    },
    dialogTitle: {
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'right',
      height: '28px', // 50 - padding(16px) - margin(4px) - border and shadow(2px)
      ...shorthands.padding('4px', '0px', '4px', '4px'),
    },
    dialogContent: {
      display: 'flex',
      flexDirection: 'row',
      height: '80vh', // 864 / 1080
      width: '75vw', // 1440 / 1920
      rowGap: '0px',
      ...shorthands.padding(0),
    },
    stepper: {
      width: '16%', // 227 / 1440
      maxWidth: '230px', // 227 but we don't like odd values; add to make it a more round number
      backgroundColor: tokens.colorNeutralBackground3,
      ...shorthands.padding('24px'), // from Figma
      ...shorthands.borderRight('1px', 'solid', tokens.colorNeutralStroke1),
      borderTopLeftRadius: '8px',
      borderBottomLeftRadius: '8px',
      borderTopWidth: 0,
      marginTop: '1px', // to align with the top of the dialog due to 1px border
      marginBottom: '2px', // to align with the bottom of the dialog
    },
    rightPane: {
      display: 'flex',
      flexDirection: 'column',
      rowGap: 0,
      overflowX: 'auto',
      marginRight: '4px',
      width: '100%',
    },
    content: {
      height: 'calc(100% - 72px)', // 72px is the footer height
      ...shorthands.borderBottom('1px', 'solid', tokens.colorNeutralStroke1),
      ...shorthands.padding('24px'), //from Figma
      ...shorthands.overflow('auto'),
    },
    footer: {
      height: '72px', //from Figma
      display: 'flex',
      flexDirection: 'row',
      columnGap: '8px',
      width: 'calc(100% - 16px)', // padding and gap
      alignItems: 'center',
      paddingBottom: '4px',
      justifyContent: 'right',
    },
  });
