import React from 'react';
import { getSlots } from '@fluentui/react-utilities';
import {
  EmptyContentsSlots,
  IEmptyContentsRenderProps,
} from './EmptyContents.types';
import { mergeClasses } from '@fluentui/react-components';
import DefaultSvg from '../../../assets/Empty.svg';
import { Image } from '@fluentui/react-components';

export enum EmptyContentsTestIds {
  EmptyContents = 'EmptyContents',
}

export const useEmptyContentsRender: React.FC<IEmptyContentsRenderProps> = ({
  state,
  styles,
}) => {
  const { slots, slotProps } = getSlots<EmptyContentsSlots>(state);

  if (!slots.root) {
    slots.root = 'div';
  }

  return (
    <slots.root
      data-testid={state.testId ?? EmptyContentsTestIds.EmptyContents}
      {...slotProps.root}
      className={mergeClasses(styles.root, state.className)}
    >
      {slots.image ? (
        <slots.image {...slotProps.image} />
      ) : (
        <Image src={DefaultSvg} role='presentation' />
      )}
      <slots.header {...slotProps.header} className={styles.header} />
      {slots.subtitle && <slots.subtitle {...slotProps.subtitle} />}
      {slots.button && <slots.button {...slotProps.button} />}
      {slots.footer && <slots.footer {...slotProps.footer} />}
    </slots.root>
  );
};
