import React from 'react';
import { IconCardProps } from './IconCard.types';
import { useIconCardRender } from './IconCard.render';
import { useClassNames } from './IconCard.styles';
import { useIconCardState } from './IconCard.state';

const MODULE_NAME = 'IconCard';

export const IconCard: React.FC<IconCardProps> = (props) => {
  // get state
  const state = useIconCardState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useIconCardRender({ state, styles });
};

IconCard.displayName = MODULE_NAME;
