import { useEffect, useImperativeHandle, useMemo, useState } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import {
  DateTimeRangeInputProps,
  DateTimeRangeInputRef,
  DateTimeRangeInputState,
} from './DateTimeRangeInput.types';
import { strings, t } from '@do/localization';
import { produce } from 'immer';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'Loading.state';

export const useDateTimeRangeInputState = (
  props: DateTimeRangeInputProps,
  ref: React.Ref<DateTimeRangeInputRef | undefined>,
): DateTimeRangeInputState => {
  // state
  const startDate = props.startDateInputProps.value;
  const endDate = props.endDateInputProps.value;
  const [errorMessages, setErrorMessages] = useState({
    start: '',
    end: '',
  });

  // callbacks
  useImperativeHandle(ref, () => ({
    getIsValid: () => validate(props),
  }));

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // side effects
  useEffect(() => {
    if (!startDate || !endDate) {
      setErrorMessages(
        produce((draft) => {
          draft.start =
            // no start date provided and start date is required
            !startDate && props.startDateFieldProps?.required
              ? t(
                  strings.time_range_controls.error_messages
                    .start_date_required,
                )
              : '';
        }),
      );

      setErrorMessages(
        produce((draft) => {
          // no start date provided and start date is required
          draft.end =
            !endDate && props.endDateFieldProps?.required
              ? t(strings.time_range_controls.error_messages.end_date_required)
              : '';
        }),
      );

      // stop here to allow user to provide both dates
      return;
    }

    const numericalStartDate = new Date(startDate as string).getTime();
    const numericalEndDate = new Date(endDate as string).getTime();

    setErrorMessages(
      produce((draft) => {
        draft.start = isNaN(numericalStartDate)
          ? t(strings.time_range_controls.error_messages.date_format_invalid)
          : '';
      }),
    );

    setErrorMessages(
      produce((draft) => {
        draft.end = isNaN(numericalEndDate)
          ? t(strings.time_range_controls.error_messages.date_format_invalid)
          : numericalStartDate > numericalEndDate
            ? t(strings.time_range_controls.error_messages.end_date_after_start)
            : '';
      }),
    );
  }, [endDate, startDate, props.startDateFieldProps, props.endDateFieldProps]);

  // result
  const state: DateTimeRangeInputState = useMemo(() => {
    return {
      ...props,
      errorMessages,
    };
  }, [errorMessages, props]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};

function validate(props: DateTimeRangeInputProps) {
  const startDate = props.startDateInputProps.value;
  const endDate = props.endDateInputProps.value;

  const hasValidStartDate = !props.startDateFieldProps?.required || !!startDate;
  const hasValidEndDate = !props.endDateFieldProps?.required || !!endDate;
  const isEndDateAfterStartDate =
    endDate && startDate
      ? new Date(endDate).getTime() > new Date(startDate).getTime()
      : true;

  return hasValidStartDate && hasValidEndDate && isEndDateAfterStartDate;
}
