import { ExtensionRoutes, useAppRouteProps } from '@do/core';
import { ExplorerPageRouteProps } from '../../components/ExplorerPage/ExplorerPage.types';

export const useExplorerRouteProps = (): ExplorerPageRouteProps => {
  const appRouteProps = useAppRouteProps();

  const props = <ExplorerPageRouteProps>{
    ...appRouteProps,
    explorerPath: ExtensionRoutes.explorer(appRouteProps.segments[1]),
    tabName: appRouteProps.segments[3] || 'twins',
  };
  return props;
};
