import { useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { ExternalLinkProps, ExternalLinkState } from './ExternalLink.types';
import { resolveShorthand } from '@fluentui/react-components';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'ExternalLink.state';

export const useExternalLinkState = (
  props: ExternalLinkProps,
): ExternalLinkState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state: ExternalLinkState = useMemo(() => {
    return {
      components: {
        root: 'div',
        text: 'span',
        contentBefore: 'span',
        contentAfter: 'span',
      },
      root: {},
      className: props.className,
      text: resolveShorthand(props.text),
      href: `https://aka.ms/${props.akaShortLink}`,
      contentAfter: resolveShorthand(props.contentAfter),
      contentBefore: resolveShorthand(props.contentBefore),
    };
  }, [
    props.akaShortLink,
    props.className,
    props.contentAfter,
    props.contentBefore,
    props.text,
  ]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
