import { TimeSeriesControlsParameters, TimeSeriesPointData } from '@do/core';
import { ExploreTimeSeriesTimeRangeBuckets } from '../../../models/TwinVisualizationCertifiedEvents';

export enum AggregationKeys {
  average = 'average',
  maximum = 'maximum',
  minimum = 'minimum',
}

// TODO: Improve this to allow flexibility of picking the bin interval
export enum BinIntervalKeys {
  oneMinute = '60',
  fiveMinutes = '300',
  fifteenMinutes = '900',
  thirtyMinutes = '1800',
  oneHour = '3600',
  oneDay = '86400',
}

export const checkParamsChange = (
  newParams: TimeSeriesControlsParameters,
  prevParams?: TimeSeriesControlsParameters,
) => {
  if (!prevParams) {
    return true;
  }

  return (
    prevParams.dateTimeStart !== newParams.dateTimeStart ||
    prevParams.dateTimeEnd !== newParams.dateTimeEnd ||
    prevParams.binWidth !== Number(newParams.binWidth) ||
    prevParams.aggType !== newParams.aggType
  );
};

export const checkTimeSeriesHasValues = (
  timeSeriesPointData: Record<string, TimeSeriesPointData[]>,
): boolean => {
  // If any point has a value, the time series is not empty
  return Object.values(timeSeriesPointData).some((timeSeriesData) =>
    timeSeriesData.some((point) => point.value !== null),
  );
};

export function getTimeSeriesRangeBucket(
  startTime: string | Date | undefined,
  endTime: string | Date | undefined,
): ExploreTimeSeriesTimeRangeBuckets {
  if (!startTime || !endTime) {
    return ExploreTimeSeriesTimeRangeBuckets.TimeSeriesTimeRangeInvalid;
  }

  const durationToBucketMap = {
    [24 * 60 * 60 * 1000]: ExploreTimeSeriesTimeRangeBuckets.TimeSeriesUnder1d, // 1 day
    [7 * 24 * 60 * 60 * 1000]:
      ExploreTimeSeriesTimeRangeBuckets.TimeSeriesOver1d, // 1 week
    [30 * 24 * 60 * 60 * 1000]:
      ExploreTimeSeriesTimeRangeBuckets.TimeSeriesOver1wk, // 30 days
  };
  const start = new Date(startTime);
  const end = new Date(endTime);
  const duration = Math.abs(end.getTime() - start.getTime());
  const matchingDuration = Object.keys(durationToBucketMap)
    .map(Number)
    .find((key) => duration < key);

  return matchingDuration
    ? durationToBucketMap[matchingDuration]
    : ExploreTimeSeriesTimeRangeBuckets.TimeSeriesOver30d;
}
