import React from 'react';
import { produce } from 'immer';
import {
  TwinDetailsPanelState,
  TwinDetailsActions,
  TwinDetailsActionType,
} from './TwinDetailsPanel.types';
import { getLogger } from '@do/core';

export const debugLogging = true;
export const MODULE_NAME = 'TwinDetailsReducer';

export const INITIAL_STATE: TwinDetailsPanelState = {
  properties: {
    state: 'uncalled',
  },
  labels: {
    state: 'uncalled',
  },
  attributes: {
    state: 'uncalled',
  },
};
export const TwinDetailsPanelReducer: (
  draft: TwinDetailsPanelState,
  action: TwinDetailsActions,
) => TwinDetailsPanelState = produce(
  (draft: TwinDetailsPanelState, action: TwinDetailsActions) => {
    debugLogging &&
      getLogger().info(
        MODULE_NAME,
        `Updating TwinDetailsReducer context ${action.type} with payload: `,
        (action as any).payload,
      );

    switch (action.type) {
      case TwinDetailsActionType.LOADING_ALL: {
        draft.properties = { state: 'pending' };
        draft.labels = { state: 'pending' };
        draft.attributes = { state: 'pending' };
        break;
      }
      case TwinDetailsActionType.LOADING_PROPERTIES: {
        draft.properties = { state: 'pending' };
        break;
      }
      case TwinDetailsActionType.LOADED_PROPERTIES: {
        draft.properties = {
          state: 'success',
          data: { ...action.payload.data },
        };
        break;
      }
      case TwinDetailsActionType.FAILED_TO_LOAD_PROPERTIES: {
        draft.properties = {
          state: 'error',
          errorCode: action.payload.errorCode,
          errorMessage: action.payload.errorMessage,
        };
        break;
      }
      case TwinDetailsActionType.LOADING_LABELS: {
        draft.labels = { state: 'pending' };
        break;
      }
      case TwinDetailsActionType.LOADED_LABELS: {
        draft.labels = {
          state: 'success',
          data: action.payload.data,
        };
        break;
      }
      case TwinDetailsActionType.FAILED_TO_LOAD_LABELS: {
        draft.labels = {
          state: 'error',
          errorCode: action.payload.errorCode,
          errorMessage: action.payload.errorMessage,
        };
        break;
      }
      case TwinDetailsActionType.LOADING_ATTRIBUTES: {
        draft.attributes = { state: 'pending' };
        break;
      }
      case TwinDetailsActionType.LOADED_ATTRIBUTES: {
        draft.attributes = { state: 'success', data: action.payload.data };
        break;
      }
      case TwinDetailsActionType.FAILED_TO_LOAD_ATTRIBUTES: {
        draft.attributes = {
          state: 'error',
          errorCode: action.payload.errorCode,
          errorMessage: action.payload.errorMessage,
        };
        break;
      }
    }
    return draft;
  },
);

export const useTwinDetailsPanelReducer = () => {
  const [state, dispatch] = React.useReducer(
    TwinDetailsPanelReducer,
    INITIAL_STATE,
  );
  return [state, dispatch] as const;
};
