import React from 'react';
import { LineChartProps } from './LineChart.types';
import { useLineChartRender } from './LineChart.render';
import { useClassNames } from './LineChart.styles';
import { useLineChartState } from './LineChart.state';

const MODULE_NAME = 'LineChart';

export const LineChart: React.FC<LineChartProps> = (props) => {
  // get state
  const state = useLineChartState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useLineChartRender({ state, styles });
};

LineChart.displayName = MODULE_NAME;
