import {
  FilterComboboxStyles,
  FilterComboboxClasses,
} from './FilterCombobox.types';
import { makeStyles, shorthands, tokens } from '@fluentui/react-components';

export const useClassNames: () => FilterComboboxStyles =
  makeStyles<FilterComboboxClasses>({
    root: {
      // Stack the label above the field with a gap
      display: 'grid',
      gridTemplateRows: 'repeat(1fr)',
      justifyItems: 'start',
      ...shorthands.gap('2px'),
      '> input': {
        textOverflow: 'ellipsis', // truncate long text
        width: '95%', // not 100% to allow space for the caret icon
      },
    },
    tagsList: {
      listStyleType: 'none',
      marginBottom: tokens.spacingVerticalXXS,
      marginTop: 0,
      paddingLeft: 0,
      display: 'flex',
      gridGap: tokens.spacingHorizontalXXS,
    },
  });
