import { useCallback, useEffect, useMemo, useState } from 'react';
import {
  getLogger,
  useDebugLogging,
  TwinEvent,
  useEventClient,
  DataRequest,
} from '@do/core';
import { IEventDetailsProps, IEventDetailsState } from './EventDetails.types';
const DEBUG_LOGGING = false;
const MODULE_NAME = 'EventDetails.state';

export const useEventDetailsState = (
  props: IEventDetailsProps,
): IEventDetailsState => {
  // contexts

  // state
  const [event, setEvent] = useState<DataRequest<TwinEvent | undefined>>({
    state: 'uncalled',
  });
  const client = useEventClient();

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks
  const onCopyClick = useCallback(() => {
    if (event.state === 'success') {
      const eventString = JSON.stringify(event.data, null, '\t');
      navigator.clipboard.writeText(eventString);
    }
  }, [event]);
  const onDownloadClick = useCallback(() => {
    if (event.state === 'success') {
      const eventString = JSON.stringify(event.data, null, '\t');
      const blob = new Blob([eventString], { type: 'application/json' });
      const url = URL.createObjectURL(blob);

      // Create a temporary anchor element and trigger the download
      const a = document.createElement('a');
      a.href = url;
      a.download = `${props.eventId}.json`;
      a.click();
      URL.revokeObjectURL(url);
      a.remove();
    }
  }, [event, props.eventId]);

  // side effects
  useEffect(() => {
    if (client) {
      client.getEventById(props.eventId).then((event) => {
        setEvent(event);
        if (event.state === 'success') {
          debugLogging &&
            getLogger().debug('Event loaded. {event}', MODULE_NAME, {
              event: event.data,
            });
        }
      });
    }
  }, [client, debugLogging, props.eventId]);

  // result
  const state: IEventDetailsState = useMemo(() => {
    return {
      onClose: props.onClose,
      eventId: props.eventId,
      event: event,
      onCopyClick,
      onDownloadClick,
    };
  }, [event, onCopyClick, onDownloadClick, props.eventId, props.onClose]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state;
};
