import * as React from 'react';
import { Option } from '@fluentui/react-components';
import { strings, t } from '@do/localization';
import { FilterComboboxProps } from './FilterCombobox.types';

export function useComboboxFilterWithFreeform(
  options: FilterComboboxProps['options'],
  query: FilterComboboxProps['query'] = '',
  {
    noOptionsMessage,
    freeform,
  }: { noOptionsMessage?: string; freeform?: boolean } = {},
) {
  return React.useMemo(() => {
    const lowercaseTrimmedQuery = query.toLocaleLowerCase().trim();
    const lowercaseOptions = options.map((option) =>
      typeof option === 'string'
        ? option.toLocaleLowerCase()
        : option?.value?.toLocaleLowerCase(),
    );

    // query is defined and not included in the options
    if (
      lowercaseTrimmedQuery &&
      !lowercaseOptions.find((option) => option.includes(lowercaseTrimmedQuery))
    ) {
      const option = freeform
        ? // if freeform is enabled, return the query as an option
          query
        : // otherwise, return the no options message
          noOptionsMessage ?? t(strings.filter_combobox.no_options_message);
      return [
        <Option
          disabled={!freeform}
          key={option}
          value={freeform ? option : undefined}
          text={option}
        >
          {option}
        </Option>,
      ];
    }

    return options.reduce<JSX.Element[]>((options, option) => {
      const value = typeof option === 'string' ? option : option?.value;

      // query is either not present, or included in the options
      if (!query || value.toLocaleLowerCase().includes(lowercaseTrimmedQuery)) {
        const text = typeof option === 'string' ? option : option?.children;
        if (text) {
          options.push(
            <Option key={value} value={value} text={text}>
              {text}
            </Option>,
          );
        }
      }

      return options;
    }, [] as JSX.Element[]);
  }, [query, options, noOptionsMessage, freeform]);
}
