import * as React from 'react';
import { getTimeZones, getLogger, useDebugLogging } from '@do/core';
import {
  SchedulingSectionConsumerState,
  SchedulingSectionOnChangeFields,
  SchedulingSectionRef,
  SchedulingSectionState,
  UnitOfTime,
} from './SchedulingSection.types';
import { strings, t, useLocalizationContext } from '@do/localization';
import { produce } from 'immer';
import { DateTimeRangeInputProps } from '../DateTimeRangeInput';
import { SchedulingSectionTimeFieldRef } from './components/SchedulingSectionTimeField/SchedulingSectionTimeField.types';
import { DateTimeRangeInputRef } from '../DateTimeRangeInput/DateTimeRangeInput.types';
import { TestIds } from './SchedulingSection.render';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'SchedulingSection.state';

export const enum SchedulingSectionScheduledRunValue {
  on = 'on',
  off = 'off',
}
const SCHEDULING_SECTION_INITIAL_STATE: SchedulingSectionOnChangeFields = {
  isScheduledRunOff: true,
  unitOfTime: UnitOfTime.Minute,
  timeZone: '',
  dateTime: {
    start: '',
    end: '',
  },
};

export const useSchedulingSectionState = (
  {
    hideEndDate,
    initialState,
    onScheduleChange,
  }: SchedulingSectionConsumerState,
  ref: React.Ref<SchedulingSectionRef | undefined>,
): SchedulingSectionState => {
  // contexts
  const {
    i18n: { language },
  } = useLocalizationContext();

  // state
  const timeFieldRef = React.useRef<SchedulingSectionTimeFieldRef>();
  const dateTimeRangeRef = React.useRef<DateTimeRangeInputRef>();
  const [schedulingState, updateSchedulingState] =
    React.useState<SchedulingSectionOnChangeFields>(() => {
      return {
        ...SCHEDULING_SECTION_INITIAL_STATE,
        ...initialState,
      };
    });

  const dateTimeRangeProps: DateTimeRangeInputProps = React.useMemo(
    () => ({
      startDateFieldProps: {
        required: true,
        'aria-disabled': schedulingState.isScheduledRunOff,
      },
      endDateFieldProps: {
        required: true,
        'aria-disabled': schedulingState.isScheduledRunOff,
      },
      startDateInputProps: {
        min: new Date().toISOString().substring(0, 16), // date without seconds
        value: schedulingState.dateTime?.start,
        disabled: schedulingState.isScheduledRunOff,
        'data-testid': TestIds.StartDateInput,
        onChange: (_, { value }) => {
          updateSchedulingState(
            produce(schedulingState, (draft) => {
              onScheduleChange({
                field: 'dateTime',
                value: (draft.dateTime = {
                  start: value,
                  end: draft.dateTime?.end,
                }),
              });
            }),
          );
        },
      },
      endDateInputProps: {
        min: new Date().toISOString().substring(0, 16),
        value: schedulingState.dateTime?.end,
        disabled: schedulingState.isScheduledRunOff,
        'data-testid': TestIds.EndDateInput,
        onChange: (_, { value }) => {
          updateSchedulingState(
            produce(schedulingState, (draft) => {
              onScheduleChange({
                field: 'dateTime',
                value: (draft.dateTime = {
                  start: draft.dateTime?.start,
                  end: value,
                }),
              });
            }),
          );
        },
      },
    }),
    [onScheduleChange, schedulingState],
  );
  const isWeekly = React.useMemo(
    () => schedulingState.unitOfTime === UnitOfTime.Week,
    [schedulingState.unitOfTime],
  );

  // callbacks
  React.useImperativeHandle(ref, () => {
    return {
      getIsValid: () => {
        const isMinutesInvalid =
          !schedulingState.isScheduledRunOff &&
          (schedulingState.unitOfTime === UnitOfTime.Minute ||
            schedulingState.unitOfTime === UnitOfTime.Hour) &&
          !schedulingState.minutes;

        const isDateTimeRangeInvalid =
          !schedulingState.isScheduledRunOff &&
          !dateTimeRangeRef.current?.getIsValid();

        const isTimesOfDayInvalid =
          !schedulingState.isScheduledRunOff &&
          (schedulingState.unitOfTime === UnitOfTime.Day || isWeekly) &&
          !timeFieldRef.current?.getIsValid();

        return (
          !state.daysOfWeekValidationMessage &&
          !isMinutesInvalid &&
          !isDateTimeRangeInvalid &&
          !isTimesOfDayInvalid
        );
      },
    };
  });

  // result
  const state = React.useMemo((): SchedulingSectionState => {
    const daysOfWeekValidationMessage =
      isWeekly &&
      !schedulingState.isScheduledRunOff &&
      !schedulingState.daysOfWeek
        ? t(strings.scheduling.days_of_week_validation_message)
        : undefined;

    return {
      ...schedulingState,
      dateTimeRangeRef,
      timeFieldRef,
      dateTimeRangeProps,
      hideEndDate,
      daysOfWeekValidationMessage,
      unitOfTimeDropdownItems: Object.values(UnitOfTime).map((unitOfTime) => ({
        key: unitOfTime,
        text: t(strings.scheduling.unit_of_time_dropdown_option, {
          context: unitOfTime,
        }),
      })),
      daysOfWeekDropdownItems: getWeekdayShortNames(language).map((day) => ({
        key: day,
        text: day,
      })),
      timeZoneDropdownItems: getTimeZones(),
      onTimesOfDayChange: (timesOfDay) => {
        updateSchedulingState(
          produce(schedulingState, (draft) => {
            onScheduleChange({
              field: 'timesOfDay',
              value: (draft.timesOfDay = timesOfDay),
            });
          }),
        );
      },
      onDaysOfWeekChange: (i, checked) => {
        updateSchedulingState(
          produce(schedulingState, (draft) => {
            const previousDaysOfWeek = draft.daysOfWeek ? draft.daysOfWeek : 0;
            onScheduleChange({
              field: 'daysOfWeek',
              value: (draft.daysOfWeek = checked
                ? previousDaysOfWeek | (1 << i) // set the bit at index `i` to `1`
                : previousDaysOfWeek &
                  // create a number where the bit at index `i` is set to `0`:
                  (~(1 << i) &
                    // and mask it to ensure its length is 7:
                    0b1111111)),
            });
          }),
        );
      },
      onTimeZoneChange: (timeZone) => {
        updateSchedulingState(
          produce(schedulingState, (draft) => {
            onScheduleChange({
              field: 'timeZone',
              value: (draft.timeZone = timeZone),
            });
          }),
        );
      },
      onUnitOfTimeChange: (unitOfTime) => {
        updateSchedulingState(
          produce(schedulingState, (draft) => {
            onScheduleChange({
              field: 'unitOfTime',
              value: (draft.unitOfTime = unitOfTime as UnitOfTime),
            });

            // reset minutes to appropriate value based on the selected unit of time
            if (unitOfTime === UnitOfTime.Minute) {
              onScheduleChange({
                field: 'minutes',
                value: (draft.minutes = 1),
              });
            } else if (unitOfTime === UnitOfTime.Hour) {
              onScheduleChange({
                field: 'minutes',
                value: (draft.minutes = 60),
              });
            }
          }),
        );
      },
      onMinutesChange: (_, { value }) => {
        updateSchedulingState(
          produce(schedulingState, (draft) => {
            const minutes = Number(value);
            onScheduleChange({
              field: 'minutes',
              value: (draft.minutes =
                draft.unitOfTime === UnitOfTime.Hour ? minutes * 60 : minutes),
            });
          }),
        );
      },
      onToggleScheduledRun: (_, { value }) => {
        updateSchedulingState(
          produce(schedulingState, (draft) => {
            onScheduleChange({
              field: 'isScheduledRunOff',
              value: (draft.isScheduledRunOff =
                value === SchedulingSectionScheduledRunValue.off),
            });
          }),
        );
      },
      resetSchedulingState: () => {
        const resetState = {
          ...SCHEDULING_SECTION_INITIAL_STATE,
          ...initialState,
        };

        updateSchedulingState(resetState);
        return resetState;
      },
    };
  }, [
    isWeekly,
    language,
    initialState,
    schedulingState,
    hideEndDate,
    dateTimeRangeProps,
    updateSchedulingState,
    onScheduleChange,
  ]);

  useDebugLogging(DEBUG_LOGGING) &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};

export function getWeekdayShortNames(language: string) {
  return [...Array(7).keys()].map((day) =>
    new Intl.DateTimeFormat(language, {
      weekday: 'short',
      // we pass UTC here to ensure the days are always returned in the same order
      // regardless of the user's timezone (i.e. Sunday to Saturday)
      timeZone: 'UTC',
    }).format(new Date(Date.UTC(2024, 3, day))),
  );
}
