import { useCallback, useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { ErrorBoundaryState } from './ErrorBoundary.types';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'ErrorBoundary.state';

export const useErrorBoundaryState = (): ErrorBoundaryState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);
  const logger = useMemo(
    () => (debugLogging ? getLogger() : undefined),
    [debugLogging],
  );

  // callbacks
  const logError = useCallback(
    (error: Error) => {
      logger?.error(MODULE_NAME, 'Error. {error}', {
        error,
      });
    },
    [logger],
  );

  // side effects

  // result
  const state: ErrorBoundaryState = useMemo(() => {
    return {
      onError: logError,
    };
  }, [logError]);
  return state;
};
