import { SelectionItemId } from '@fluentui/react-components';
import type { DropdownProps } from '@fluentui/react-components';

export enum ChartType {
  line = 'line',
  scatter = 'scatter',
}

export type SelectedTimeSeries = {
  timeSeriesDatapointId: string;
  twinId: string;
};

export type TimeSeriesContextType = {
  selectedTimeSeries: SelectedTimeSeries[];
  setSelectedTimeSeries: (timeSeriesIds: Set<SelectionItemId>) => void;
  aggregationMethod: string;
  setAggregationMethod: (aggregationMethod: string) => void;
  startTime?: string;
  endTime?: string;
  setStartTime: (time?: string) => void;
  setEndTime: (time?: string) => void;
  binSelectedOption: string;
  binInterval: string;
  setBinInterval: DropdownProps['onOptionSelect'];
};

export type TimeSeriesContextProviderProps = {
  selectedTimeSeries?: SelectedTimeSeries[];
};
