import { LoadingStyles, LoadingClasses } from './Loading.types';
import { makeStyles, tokens } from '@fluentui/react-components';

export const useClassNames: () => LoadingStyles = makeStyles<LoadingClasses>({
  root: {
    display: 'flex',
    flexDirection: 'column',
    rowGap: '4px',
    width: '100%',
    minHeight: '120px',
    minWidth: '300px',
    paddingTop: '8px',
  },
  item: {
    width: '90%',
  },
  altItem: {
    width: '50%',
  },
  inverted: {
    backgroundColor: tokens.colorNeutralBackground3,
  },
});
