import React from 'react';
import { ITwinDetailsPanelProps } from './TwinDetailsPanel.types';
import { useTwinDetailsPanelRender } from './TwinDetailsPanel.render';
import { useClassNames } from './TwinDetailsPanel.styles';
import { useTwinDetailsPanelState } from './TwinDetailsPanel.state';

const MODULE_NAME = 'TwinDetailsPanel';

export const TwinDetailsPanel: React.FC<ITwinDetailsPanelProps> = (props) => {
  // get state
  const state = useTwinDetailsPanelState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useTwinDetailsPanelRender({ state, styles });
};

TwinDetailsPanel.displayName = MODULE_NAME;
