import React from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { IGraphProps } from './Graph.types';
import { useGraphRender } from './Graph.render';
import { useClassNames } from './Graph.styles';
import { useGraphState } from './Graph.state';

const MODULE_NAME = 'Graph';

export const Graph: React.FC<IGraphProps> = (props) => {
  // get state
  const state = useGraphState(props);
  const debugLogging = useDebugLogging();

  // get styles
  const styles = useClassNames();

  debugLogging &&
    getLogger().debug('Render. {state, styles}', MODULE_NAME, {
      state,
      styles,
    });

  // render the component
  return useGraphRender({
    state,
    styles,
  });
};

Graph.displayName = MODULE_NAME;
