/* istanbul ignore file */
import React from 'react';
import { IEventsListProps } from './EventsList.types';
import { useEventsListRender } from './EventsList.render';
import { useClassNames } from './EventsList.styles';
import { useEventsListState } from './EventsList.state';

const MODULE_NAME = 'EventsList';

export const EventsList: React.FC<IEventsListProps> = (_props) => {
  // get state
  const state = useEventsListState(_props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useEventsListRender({ state, styles });
};

EventsList.displayName = MODULE_NAME;
