import React from 'react';
import { TimeSeriesChartRenderProps } from './TimeSeriesChart.types';
import { LineChart } from './components/LineChart/LineChart.root';
import {
  MessageBar,
  MessageBarBody,
  MessageBarTitle,
  Spinner,
} from '@fluentui/react-components';
import { EmptyContents, ErrorMessage } from '@do/components';
import { strings, t } from '@do/localization';
import { LineChartControls } from './components/LineChart/components/LineChartControls';

export const useTimeSeriesChartRender: React.FC<TimeSeriesChartRenderProps> = ({
  state,
  styles,
}) => {
  let chartContents: React.ReactNode = null;
  const { successfulDataRequests, failedDataRequests } = state;

  switch (state.componentState) {
    case 'pending':
      chartContents = <Spinner label={t(strings.common.loading)} />;
      break;
    case 'success':
      chartContents = (
        <LineChart
          setChartType={state.setChartType}
          rawTimeSeriesData={successfulDataRequests}
        />
      );
      break;
    case 'empty':
      chartContents = (
        <EmptyContents
          header={t(strings.explorer.time_series.controls.errors.no_data)}
        />
      );
      break;
    case 'error':
      chartContents = <ErrorMessage message={t(strings.common.error)} />;
      break;
    default:
      chartContents = <ErrorMessage message={t(strings.common.error)} />;
  }

  return (
    <div className={styles.root}>
      <div className={styles.chart}>
        <div className={styles.controls}>
          <LineChartControls onApplyClicked={state.onApplyClicked} />
        </div>
        {/* TODO: Allow message bar to expand-collapse to show detailed errors */}
        {failedDataRequests.length > 0 ? (
          <div className={styles.error}>
            <MessageBar intent='error'>
              <MessageBarBody>
                <MessageBarTitle>
                  {t(strings.explorer.time_series.controls.errors.error_title)}
                </MessageBarTitle>
                {t(
                  strings.explorer.time_series.controls.errors.failed_to_load,
                  {
                    count: failedDataRequests.length,
                  },
                )}
              </MessageBarBody>
            </MessageBar>
          </div>
        ) : null}
        {chartContents}
      </div>
    </div>
  );
};
