import React from 'react';
import { ITwinEventsPanelRenderProps } from './TwinEventsPanel.types';
import { EventsList } from './components/EventsList';

export enum TestIds {
  Main = 'twin-details-events-panel-main',
}

export const useTwinEventsPanelRender: React.FC<
  ITwinEventsPanelRenderProps
> = ({ state, styles }) => {
  return (
    <div data-testid={TestIds.Main} className={styles.root}>
      <EventsList twinId={state.twinId} />
    </div>
  );
};
