import React, {
  createContext,
  useContext,
  memo,
  useReducer,
  useMemo,
} from 'react';
import {
  EMPTY_FORMULA,
  IInsightsPageContext,
  InsightsPageContextProviderProps,
  InsightsPageContextState,
} from './InsightsPageContext.types';
import { InsightsPageContextReducer } from './InsightsPageContext.reducer';

const DEFAULT_TRANSFORM_WIZARD_CONTEXT: InsightsPageContextState = {
  currentFormula: {
    ...EMPTY_FORMULA,
  },
  formulaList: {
    state: 'uncalled',
  },
  page: 'empty',
  selectedTwinId: undefined,
  selectedTwinDisplayName: undefined,
  selectedFormulaId: undefined,
};

export const InsightsPageContext = createContext<IInsightsPageContext | null>(
  null,
);
export const useInsightsPageContext = () => {
  const context = useContext(InsightsPageContext);
  // istanbul ignore next
  if (!context) {
    throw new Error(
      'useInsightsPageContext must be used within a InsightsPageContextProvider',
    );
  }
  return context;
};

/**
 * This context is for holding which environment the app is running in (OneBox, INT, DAILY, PROD, etc). Dogfood/EDog is TEST
 */
export const InsightsPageContextProvider = memo(
  ({
    children,
    initialState,
  }: React.PropsWithChildren<InsightsPageContextProviderProps>) => {
    const state: InsightsPageContextState = useMemo(
      () => ({
        ...DEFAULT_TRANSFORM_WIZARD_CONTEXT,
        ...initialState,
      }),
      [initialState],
    );

    const [pageState, pageDispatch] = useReducer(
      InsightsPageContextReducer,
      state,
    );

    return (
      <InsightsPageContext.Provider value={{ pageState, pageDispatch }}>
        {children}
      </InsightsPageContext.Provider>
    );
  },
);
