import {
  SchedulingSectionTimeFieldStyles,
  SchedulingSectionTimeFieldClasses,
} from './SchedulingSectionTimeField.types';
import { makeStyles, shorthands } from '@fluentui/react-components';

export const useClassNames: () => SchedulingSectionTimeFieldStyles =
  makeStyles<SchedulingSectionTimeFieldClasses>({
    root: {
      display: 'flex',
      flexDirection: 'column',
      rowGap: '16px',
    },
    row: {
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center',
      ...shorthands.gap('8px'),
    },
    add_a_time_btn: {
      alignSelf: 'flex-start',
      ...shorthands.padding(0),
    },
    hours_of_day_field: {
      display: 'flex',
      flexDirection: 'column',
      rowGap: '8px',
    },
  });
