import React from 'react';
import { StepperProps } from './Stepper.types';
import { useStepperRender } from './Stepper.render';
import { useClassNames } from './Stepper.styles';

const MODULE_NAME = 'Stepper';

export const Stepper: React.FC<StepperProps> = (props) => {
  // get styles
  const styles = useClassNames();

  // render the item
  return useStepperRender({ state: props, styles });
};

Stepper.displayName = MODULE_NAME;
