import React from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { ITwinDetailsPageProps } from './TwinDetailsPage.types';
import { useTwinDetailsPageRender } from './TwinDetailsPage.render';
import { useClassNames } from './TwinDetailsPage.styles';
import { useTwinDetailsPageState } from './TwinDetailsPage.state';

const MODULE_NAME = 'TwinDetailsPage';

export const TwinDetailsPage: React.FC<ITwinDetailsPageProps> = (_props) => {
  // get state
  const state = useTwinDetailsPageState(_props);
  const debugLogging = useDebugLogging();

  // get styles
  const styles = useClassNames();

  debugLogging &&
    getLogger().debug('Render. {state, styles}', MODULE_NAME, {
      state,
      styles,
    });

  // render the component
  return useTwinDetailsPageRender({ state, styles });
};

TwinDetailsPage.displayName = MODULE_NAME;
