import {
  IEmptyContentsStyles,
  EmptyContentsClasses,
} from './EmptyContents.types';
import { makeStyles, shorthands } from '@fluentui/react-components';

export const useClassNames: () => IEmptyContentsStyles =
  makeStyles<EmptyContentsClasses>({
    root: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      rowGap: '24px',
      ...shorthands.margin('auto'),
      height: '100%',
      justifyContent: 'center',
    },
    header: {
      ...shorthands.margin('0'),
    },
  });
