import React from 'react';
import { TwinTimeSeriesListProps } from './TwinTimeSeriesList.types';
import { useTwinTimeSeriesListRender } from './TwinTimeSeriesList.render';
import { useClassNames } from './TwinTimeSeriesList.styles';
import { useTwinTimeSeriesListState } from './TwinTimeSeriesList.state';

const MODULE_NAME = 'TwinTimeSeriesList';

export const TwinTimeSeriesList: React.FC<TwinTimeSeriesListProps> = (
  props,
) => {
  // get state
  const state = useTwinTimeSeriesListState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useTwinTimeSeriesListRender({ state, styles });
};

TwinTimeSeriesList.displayName = MODULE_NAME;
