import { useMemo, useRef } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import {
  FilterComboboxProps,
  FilterComboboxState,
} from './FilterCombobox.types';
import { useId } from '@fluentui/react-components';
import { useComboboxFilterWithFreeform } from './FilterCombobox.utils';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'FilterCombobox.state';

export const useFilterComboboxState = (
  props: FilterComboboxProps,
): FilterComboboxState => {
  // state
  const comboboxInputRef = useRef<HTMLInputElement>(null);

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);
  const comboId = useId('combo-multi');
  const children = useComboboxFilterWithFreeform(props.options, props.query, {
    noOptionsMessage: props.noOptionsMessage,
    freeform: props.freeform,
  });

  // result
  const state: FilterComboboxState = useMemo(() => {
    const onChange: FilterComboboxState['onChange'] = (event) => {
      props.onQueryChange(event.target.value);
    };
    const onSelect: FilterComboboxState['onSelect'] = (_event, data) => {
      props.onSelectedOptionsChange(data.selectedOptions);
    };
    return {
      dataTestId: props.dataTestId,
      freeform: props.freeform,
      children,
      comboId,
      comboboxInputRef,
      multiselect: props.multiselect,
      onChange,
      onSelect,
      className: props.className,
      placeholder: props.placeholder,
      selectedOptions: props.selectedOptions,
      value: props.value,
    };
  }, [children, comboId, props]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
