import { useMemo, useState } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import {
  ITwinEventsPanelProps,
  ITwinEventsPanelState,
} from './TwinEventsPanel.types';
import { useTwinsExplorerRouteProps } from '../../../../hooks/routeProps/useTwinsExplorerRouteProps';
import { DetailsPageRouteProps } from '../../TwinDetailsPage';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'TwinEventsPanel.state';

export const useTwinEventsPanelState = (
  _props: ITwinEventsPanelProps,
): ITwinEventsPanelState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);
  const [explorerRouteProps] = useState(
    useTwinsExplorerRouteProps() as DetailsPageRouteProps,
  );
  // callbacks

  // side effects

  // result
  const state: ITwinEventsPanelState = useMemo(() => {
    return {
      twinId: explorerRouteProps.twinId,
    };
  }, [explorerRouteProps.twinId]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state;
};
