import React from 'react';
import { RequiredFieldTextRenderProps } from './RequiredFieldText.types';
import { strings, t } from '@do/localization';
import { mergeClasses } from '@fluentui/react-components';

export const useRequiredFieldTextRender: React.FC<
  RequiredFieldTextRenderProps
> = ({ styles, className }) => {
  return (
    <div className={mergeClasses(styles.root, className)}>
      <span className={styles.star}>*</span>
      <span className={styles.text}>{t(strings.form.required)}</span>
    </div>
  );
};
