import { TreeNode } from './HierarchyTree.types';

export const hierarchyDeepFindByPath = (
  path: string,
  hierarchyItems: TreeNode[],
): TreeNode | undefined => {
  for (const item of hierarchyItems) {
    if (item.path === path) {
      return item;
    }
    // TODO: check for hierarchies with cycles
    if (item.childCount > 0) {
      if (item.children?.state === 'success') {
        const found = hierarchyDeepFindByPath(path, item.children.data);
        if (found) {
          return found;
        }
      }
    }
  }
  return undefined;
};
