import React from 'react';
import { LabelValueRenderProps } from './LabelValue.types';
import { Label, Spinner } from '@fluentui/react-components';

export const useLabelValueRender: React.FC<LabelValueRenderProps> = ({
  props,
  styles,
}) => {
  return (
    <div className={styles.root}>
      <Label className={styles.label}>{props.label}:</Label>
      <span className={styles.value}>
        {props.loading ? <Spinner size={props.size} /> : props.value}
      </span>
    </div>
  );
};
