import React from 'react';
import { ErrorBoundaryRenderProps } from './ErrorBoundary.types';
import { ErrorBoundary } from 'react-error-boundary';
import { ErrorMessage } from '../ErrorMessage';

export const useErrorBoundaryRender: React.FC<ErrorBoundaryRenderProps> = ({
  state,
  children,
}) => {
  // we're not testing the code coverage of external components
  /* istanbul ignore next */
  const ErrorFallback = (eProps: any) => {
    state.onError(eProps.error);
    return <ErrorMessage message={eProps.error.message} />;
  };
  return (
    <ErrorBoundary FallbackComponent={ErrorFallback}>{children}</ErrorBoundary>
  );
};
