import * as React from 'react';
import {
  CheckboxOnChangeData,
  CheckboxProps,
} from '@fluentui/react-components';

export type SkipSteppedWizardIntroState = {
  skipIntro: boolean;
  onSkipIntroChange: CheckboxProps['onChange'];
};

export const useSkipSteppedWizardIntro = (localStorageKey: string) => {
  const [skipIntro, setSkipIntro] = React.useState(
    localStorage.getItem(localStorageKey) === 'true',
  );

  const onSkipIntroChange: CheckboxProps['onChange'] = React.useCallback<
    (
      ev: React.ChangeEvent<HTMLInputElement>,
      data: CheckboxOnChangeData,
    ) => void
  >(
    (_, data) => {
      setSkipIntro(data.checked as boolean);
      localStorage.setItem(localStorageKey, data.checked.toString());
    },
    [localStorageKey],
  );

  return {
    skipIntro,
    onSkipIntroChange,
  };
};
