import React from 'react';
import { IPageFooterProps } from './PageFooter.types';
import { usePageFooterRender } from './PageFooter.render';
import { useClassNames } from './PageFooter.styles';
import { usePageFooterState } from './PageFooter.state';

const MODULE_NAME = 'PageFooter';

export const PageFooter: React.FC<IPageFooterProps> = (props) => {
  // get state
  const state = usePageFooterState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return usePageFooterRender({ state, styles });
};

PageFooter.displayName = MODULE_NAME;
