import React, { useCallback, useContext } from 'react';
import {
  SelectedTimeSeries,
  TimeSeriesContextProviderProps,
  TimeSeriesContextType,
} from './TimeSeriesContext.types';
import { SelectionItemId } from '@fluentui/react-components';
import type { DropdownProps } from '@fluentui/react-components';
import { getIntervalOptions } from '../components/TimeSeriesChart/components/LineChart/components/LineChartControls/LineChartControls.render';
import { t } from '@do/localization';

export const TimeSeriesContext =
  React.createContext<TimeSeriesContextType | null>(null);
export const useTimeSeriesContext = () => {
  const context = useContext(TimeSeriesContext);
  // istanbul ignore next
  if (!context) {
    throw new Error(
      'useTimeSeriesContext must be used within a TimeSeriesContextProvider',
    );
  }
  return context;
};

const getSingleDigitFormatted = (date: number): string => {
  return date < 10 ? '0' + date : date.toString();
};

export const getDateTimeFormatted = (date: Date): string => {
  // yyyy-MM-ddThh:mm
  return (
    date.getFullYear() +
    '-' +
    getSingleDigitFormatted(date.getMonth() + 1) +
    '-' +
    getSingleDigitFormatted(date.getDate()) +
    'T' +
    getSingleDigitFormatted(date.getHours()) +
    ':' +
    getSingleDigitFormatted(date.getMinutes())
  );
};

const getDateMinusDays = (days: number): string => {
  const date = new Date(new Date().getTime() - days * 24 * 60 * 60 * 1000);
  // yyyy-MM-ddThh:mm
  const fullDate = getDateTimeFormatted(date);
  return fullDate;
};

export const TIME_SERIES_DELIMETER = '';

export const TimeSeriesContextProvider = React.memo(
  (props: TimeSeriesContextProviderProps & { children?: React.ReactNode }) => {
    const { children, selectedTimeSeries } = props;

    // state
    const [aggregationMethod, setAggregationMethod] =
      React.useState<string>('average');
    // Default to past 7 days
    const [startTime, setStartTime] = React.useState<string | undefined>(() =>
      getDateMinusDays(7),
    );
    const [endTime, setEndTime] = React.useState<string | undefined>(() =>
      getDateMinusDays(0),
    );
    const [binSelectedOption, setBinSelectedOption] = React.useState<string>(
      getIntervalOptions(t)[4].text,
    );
    const [binInterval, setBinInterval] = React.useState<string>(
      getIntervalOptions(t)[4].key, // Default to hourly
    );
    const [stateSelectedTimeSeries, setStateSelectedTimeSeries] =
      React.useState<SelectedTimeSeries[]>(selectedTimeSeries || []);

    // callbacks
    const setIdsFromDataGridSelection = useCallback(
      (timeSeriesDataGridIds: Set<SelectionItemId>) => {
        const datagridSelectedTimeSeries: SelectedTimeSeries[] = [];
        timeSeriesDataGridIds.forEach((ts) => {
          const [timeSeriesId, twinId] = (ts as string).split(
            TIME_SERIES_DELIMETER,
          );
          datagridSelectedTimeSeries.push({
            timeSeriesDatapointId: timeSeriesId,
            twinId: twinId,
          });
        });
        setStateSelectedTimeSeries(datagridSelectedTimeSeries);
      },
      [],
    );

    // Dropdown function types (event: SelectionEvents, data: OptionOnSelectData) are not exposed by Fluent
    const setBinFromDropdown: DropdownProps['onOptionSelect'] = useCallback(
      (_e: any, data: any) => {
        setBinSelectedOption(data.optionText);
        setBinInterval(data.optionValue);
      },
      [],
    );

    const providerProps: TimeSeriesContextType = React.useMemo(() => {
      return {
        selectedTimeSeries: stateSelectedTimeSeries,
        setSelectedTimeSeries: setIdsFromDataGridSelection,
        aggregationMethod,
        setAggregationMethod,
        startTime,
        endTime,
        setStartTime,
        setEndTime,
        binSelectedOption,
        binInterval,
        setBinInterval: setBinFromDropdown,
      };
    }, [
      aggregationMethod,
      binInterval,
      binSelectedOption,
      endTime,
      setBinFromDropdown,
      setIdsFromDataGridSelection,
      startTime,
      stateSelectedTimeSeries,
    ]);

    return (
      <TimeSeriesContext.Provider value={providerProps}>
        {children}
      </TimeSeriesContext.Provider>
    );
  },
);
