import { useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import {
  ITwinTimeSeriesPanelProps,
  ITwinTimeSeriesPanelState,
} from './TwinTimeSeriesPanel.types';
import { useTimeSeriesContext } from '../../../../contexts/TimeSeriesContext';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'TwinTimeSeriesPanel.state';

export const useTwinTimeSeriesPanelState = (
  _props: ITwinTimeSeriesPanelProps,
): ITwinTimeSeriesPanelState => {
  // contexts
  const { selectedTimeSeries } = useTimeSeriesContext();

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state: ITwinTimeSeriesPanelState = useMemo(() => {
    return {
      selectedTimeSeries,
    };
  }, [selectedTimeSeries]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state;
};
