import React from 'react';
import { IEmptyContentsProps } from './EmptyContents.types';
import { useEmptyContentsRender } from './EmptyContents.render';
import { useClassNames } from './EmptyContents.styles';
import { useEmptyContentsState } from './EmptyContents.state';

const MODULE_NAME = 'EmptyContents';

export const EmptyContents: React.FC<IEmptyContentsProps> = (props) => {
  // get state
  const state = useEmptyContentsState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useEmptyContentsRender({
    state,
    styles,
  });
};

EmptyContents.displayName = MODULE_NAME;
