import React from 'react';
import {
  ExternalLinkRenderProps,
  ExternalLinkSlots,
} from './ExternalLink.types';
import { getSlots } from '@fluentui/react-utilities';
import { Link, mergeClasses } from '@fluentui/react-components';
import { WindowNew16Regular } from '@fluentui/react-icons';

export const useExternalLinkRender: React.FC<ExternalLinkRenderProps> = ({
  state,
  styles,
}) => {
  const { slots, slotProps } = getSlots<ExternalLinkSlots>(state);
  return (
    <slots.root
      {...slotProps.root}
      className={mergeClasses(styles.root, state.className)}
    >
      {slots.contentBefore ? (
        <slots.contentBefore
          className={styles.marginBefore}
          {...slotProps.contentBefore}
        />
      ) : null}
      <Link href={state.href} target='_blank'>
        <slots.text {...slotProps.text} className={styles.text} />
        <WindowNew16Regular />
      </Link>
      {slots.contentAfter ? (
        <slots.contentAfter
          className={styles.marginAfter}
          {...slotProps.contentAfter}
        />
      ) : null}
    </slots.root>
  );
};
