import React, { Suspense, lazy, useEffect, useRef } from 'react';
import { LineChartRenderProps } from './LineChart.types';
import type { HighchartsReactRefObject } from 'highcharts-react-official';
import { Spinner } from '@fluentui/react-components';

/* istanbul ignore next */
const HighchartsReact = lazy(() =>
  import('highcharts-react-official').then((module) => ({
    default: module.HighchartsReact,
  })),
);

export const useLineChartRender: React.FC<LineChartRenderProps> = ({
  state,
  styles,
}) => {
  const chartComponentRef = useRef<HighchartsReactRefObject>(null);
  const highchartsModule = useRef<any>(null);

  // TODO: See if we can remove this useEffect in favor of lazy loading the component
  useEffect(() => {
    /* istanbul ignore next */
    async function fetchHighchartsModule() {
      // lazy load highcharts module
      await import('highcharts/highstock').then((module) => {
        highchartsModule.current = module.default;
        state.setIsHighchartsLoaded(true);
      });
    }

    if (!state.isHighchartsLoaded) {
      /* istanbul ignore next */
      fetchHighchartsModule();
    }
  }, [state]);

  return (
    <div className={styles.root}>
      <Suspense fallback={<Spinner className={styles.spinner} />}>
        {/* TODO: ADD SPINNER WHEN HIGHCHARTS NOT LOADED */}
        {state.isHighchartsLoaded ? (
          <div className={styles.chartContainer}>
            <HighchartsReact
              highcharts={highchartsModule.current}
              options={state.options}
              ref={chartComponentRef}
              constructorType='stockChart'
            />
          </div>
        ) : null}
      </Suspense>
    </div>
  );
};
