import React from 'react';
import {
  IHierarchyTreeRenderProps,
  TreeNode,
  TreeItemType,
} from './HierarchyTree.types';
import {
  Tree,
  TreeItem,
  TreeItemLayout,
  TreeOpenChangeData,
  mergeClasses,
  Button,
  Link,
  Spinner,
} from '@fluentui/react-components';
import { EmptyContents } from '../EmptyContents';
import { t, strings } from '@do/localization';
import { Loading } from '../Loading';
import { ErrorMessage } from '../ErrorMessage';

export const enum TestIds {
  TreeRoot = 'hierarchy-tree-root',
  /** suffixed with -<twinId> */
  ChildTreePrefix = 'hierarchy-child-tree',
  TreeNodeButton = 'hierarchy-tree-node-button',
  ShowMoreButton = 'hierarchy-show-more-button',
  Loading = 'hierarchy-tree-loading',
  Error = 'hierarchy-tree-error',
  Empty = 'hierarchy-tree-empty',
}

const renderShowMore = (
  path: string,
  className: string,
  continuationToken: string | undefined,
  continuationUri: string | undefined,
  showMore: (
    path: string,
    continuationToken: string,
    continuationUri: string,
  ) => void,
) => {
  if (
    continuationToken &&
    continuationToken !== '' &&
    continuationUri &&
    continuationUri !== ''
  ) {
    return (
      // Put this in a tree item to get the right indention
      <TreeItem key={`showMore_${path}`} value={path} itemType='leaf'>
        <TreeItemLayout className={className}>
          <Link
            as='button'
            onClick={() => showMore(path, continuationToken, continuationUri)}
            data-testid={TestIds.ShowMoreButton}
          >
            {t(strings.hierarchy_tree.show_more)}
          </Link>
        </TreeItemLayout>
      </TreeItem>
    );
  }
  return null;
};

const renderChildTree = (
  item: TreeNode,
  selectedTwinId: string | undefined,
  selectedItemStyle: string,
  treeItemStyle: string,
  showMoreStyle: string,
  onOpenChange: (_: any, data: TreeOpenChangeData) => void,
  onLoadMoreFromPath: (
    path: string,
    continuationToken: string,
    continuationUri: string,
  ) => void,
  customItemLayout:
    | ((item: TreeNode, itemType: TreeItemType) => JSX.Element)
    | undefined,
  onNodeClick?: ((path: string, itemType: TreeItemType) => void) | undefined,
) => {
  if (!item.children) {
    return null;
  }
  switch (item.children.state) {
    case 'success':
      return (
        <>
          <Tree
            data-testid={`${TestIds.ChildTreePrefix}-${item.path}`}
            key={`childTree_${item.path}`}
            aria-label={item.displayName}
            onOpenChange={onOpenChange}
          >
            {item.children.data.map((child) => {
              return renderTreeItem(
                child,
                selectedTwinId,
                selectedItemStyle,
                treeItemStyle,
                showMoreStyle,
                onOpenChange,
                onLoadMoreFromPath,
                onNodeClick,
                customItemLayout,
              );
            })}
            {renderShowMore(
              item.path,
              showMoreStyle,
              item.continuationToken,
              item.continuationUri,
              onLoadMoreFromPath,
            )}
          </Tree>
        </>
      );
    case 'error':
      return <ErrorMessage message={item.children.errorMessage} />;
  }
  return null;
};

const renderTreeItem = (
  item: TreeNode,
  selectedTwinId: string | undefined,
  selectedItemStyle: string,
  treeItemStyle: string,
  showMoreStyle: string,
  onOpenChange: (_: any, data: TreeOpenChangeData) => void,
  onLoadMoreFromPath: (
    path: string,
    continuationToken: string,
    continuationUri: string,
  ) => void,
  onNodeClick: ((path: string, itemType: TreeItemType) => void) | undefined,
  customItemLayout:
    | ((item: TreeNode, itemType: TreeItemType) => JSX.Element)
    | undefined,
) => {
  const itemType: TreeItemType = item.childCount > 0 ? 'branch' : 'leaf';
  return (
    <TreeItem
      key={`treeItem_${item.path}`}
      value={item.path}
      itemType={itemType}
      data-testId={item.path}
      open={item.open}
    >
      <TreeItemLayout
        className={
          item.twin !== undefined &&
          selectedTwinId !== undefined &&
          item.twin.twinId === selectedTwinId
            ? mergeClasses(treeItemStyle, selectedItemStyle)
            : treeItemStyle
        }
        expandIcon={
          item.children?.state === 'pending' ? (
            <Spinner size='extra-tiny' />
          ) : undefined
        }
      >
        {
          <div key={`treeItemLayout_${item.path}`}>
            {customItemLayout ? (
              customItemLayout(item, itemType)
            ) : onNodeClick ? (
              <Button
                appearance='subtle'
                // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
                onClick={() => onNodeClick!(item.path, itemType)}
                style={{ fontWeight: 'normal' }}
                data-testid={`${TestIds.TreeNodeButton}`}
              >
                {item.displayName}
              </Button>
            ) : (
              item.displayName
            )}
          </div>
        }
      </TreeItemLayout>
      {item.childCount > 0
        ? renderChildTree(
            item,
            selectedTwinId,
            selectedItemStyle,
            treeItemStyle,
            showMoreStyle,
            onOpenChange,
            onLoadMoreFromPath,
            customItemLayout,
            onNodeClick,
          )
        : null}
    </TreeItem>
  );
};

const generateTree = (
  items: TreeNode[],
  selectedTwinId: string | undefined,
  selectedItemStyle: string,
  treeItemStyle: string,
  showMoreStyle: string,
  onOpenChange: (_: any, data: TreeOpenChangeData) => void,
  onLoadMoreFromPath: (
    path: string,
    continuationToken: string,
    continuationUri: string,
  ) => void,
  customItemLayout:
    | ((item: TreeNode, itemType: TreeItemType) => JSX.Element)
    | undefined,
  onNodeClick: ((path: string, itemType: TreeItemType) => void) | undefined,
): JSX.Element | undefined => {
  return (
    <>
      {items.map((item) =>
        renderTreeItem(
          item,
          selectedTwinId,
          selectedItemStyle,
          treeItemStyle,
          showMoreStyle,
          onOpenChange,
          onLoadMoreFromPath,
          onNodeClick,
          customItemLayout,
        ),
      )}
    </>
  );
};

export const useHierarchyTreeRender: React.FC<IHierarchyTreeRenderProps> = ({
  state,
  styles,
}) => {
  const handleOpenChange = async (_: any, data: TreeOpenChangeData) => {
    if (data.open) {
      await state.onOpenChange(data.value as string);
    }
  };
  return state.state === 'uncalled' || state.state === 'pending' ? (
    <Loading data-testid={TestIds.Loading} />
  ) : state.state === 'success' ? (
    state.nodes.length === 0 ? (
      <EmptyContents
        data-testid={TestIds.Empty}
        header={t(strings.hierarchy_tree.empty)}
      />
    ) : (
      <>
        <Tree
          className={mergeClasses(styles.root, state.rootClassName)}
          aria-label={t(strings.hierarchy_tree.aria_label)}
          data-testid={state.dataTestId}
          onOpenChange={handleOpenChange}
        >
          {generateTree(
            state.nodes,
            state.selectedTwinId,
            styles.selectedItem,
            mergeClasses(styles.treeItem, state.treeItemClassName),
            mergeClasses(styles.showMore, state.showMoreClassName),
            handleOpenChange,
            state.onLoadMoreFromPath,
            state.customItemLayout,
            state.onNodeClick,
          )}
          {renderShowMore(
            '',
            mergeClasses(styles.showMore, state.showMoreClassName),
            state.continuationToken,
            state.continuationUri,
            state.onLoadMoreFromPath,
          )}
        </Tree>

        {state.isLoadingMore ? <Loading count={1} /> : null}
      </>
    )
  ) : (
    <ErrorMessage
      data-testid={TestIds.Error}
      message={state.errorMessage ?? ''}
    />
  );
};
