import React from 'react';
import { ErrorMessageProps } from './ErrorMessage.types';
import { useErrorMessageRender } from './ErrorMessage.render';
import { useClassNames } from './ErrorMessage.styles';

const MODULE_NAME = 'ErrorMessage';

export const ErrorMessage: React.FC<ErrorMessageProps> = (props) => {
  // no state, just props

  // get styles
  const styles = useClassNames();

  // render the item
  return useErrorMessageRender({ state: props, styles });
};

ErrorMessage.displayName = MODULE_NAME;
