import { useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import {
  IEmptyContentsProps,
  IEmptyContentsState,
} from './EmptyContents.types';
import { Button, resolveShorthand } from '@fluentui/react-components';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'EmptyContents.state';

export const useEmptyContentsState = (
  props: IEmptyContentsProps,
): IEmptyContentsState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state = useMemo(() => {
    return {
      className: props.className,
      testId: props.testId,
      components: {
        root: 'div',
        image: 'div',
        header: 'h2',
        subtitle: 'span',
        button: Button,
        footer: 'div',
      },
      root: {},
      image: resolveShorthand(props.image),
      header: resolveShorthand(props.header),
      subtitle: resolveShorthand(props.subtitle),
      button: resolveShorthand(props.button, {
        defaultProps: { appearance: 'primary' },
      }),
      footer: resolveShorthand(props.footer),
    };
  }, [props]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state as IEmptyContentsState;
};
