import React from 'react';
import { ITwinRelatedTwinsPanelRenderProps } from './TwinRelatedTwinsPanel.types';
import {
  DataGrid,
  DataGridBody,
  DataGridCell,
  DataGridHeader,
  DataGridHeaderCell,
  DataGridRow,
  Link,
  createTableColumn,
} from '@fluentui/react-components';
import { t, strings } from '@do/localization';
import { EmptyContents, ErrorMessage, Loading } from '@do/components';
import { TwinRelationship } from '@do/core';

export enum TestIds {
  Main = 'twin-details-relatedtwins-main',
  Empty = 'twin-details-relatedtwins-empty',
  Loading = 'twin-details-relatedtwins-loading',
  Error = 'twin-details-relatedtwins-error',
}

export const useTwinRelatedTwinsPanelRender: React.FC<
  ITwinRelatedTwinsPanelRenderProps
> = ({ state, styles }) => {
  const columns = [
    createTableColumn<TwinRelationship>({
      columnId: 'twinName',
      compare: (a, b) => {
        return a.name.localeCompare(b.name);
      },
      renderCell: (item: TwinRelationship) => {
        return (
          <Link as='a' onClick={() => state.navigateToDetails(item.twinId)}>
            {item.name}
          </Link>
        );
      },
      renderHeaderCell: () => {
        return (
          <span className={styles.header}>
            {t(strings.explorer.twin_details.related_twins.twin_name)}
          </span>
        );
      },
    }),
    createTableColumn<TwinRelationship>({
      columnId: 'relationshipName',
      compare: (a, b) => {
        return a.relationshipName.localeCompare(b.relationshipName);
      },
      renderCell: (item: TwinRelationship) => {
        return item.relationshipName;
      },
      renderHeaderCell: () => {
        return (
          <span className={styles.header}>
            {t(strings.explorer.twin_details.related_twins.relationship_name)}
          </span>
        );
      },
    }),
    createTableColumn<TwinRelationship>({
      columnId: 'relationshipId',
      compare: (a, b) => {
        return a.relationshipId.localeCompare(b.relationshipId);
      },
      renderCell: (item: TwinRelationship) => {
        return item.relationshipId;
      },
      renderHeaderCell: () => {
        return (
          <span className={styles.header}>
            {t(strings.explorer.twin_details.related_twins.relationship_id)}
          </span>
        );
      },
    }),
    createTableColumn<TwinRelationship>({
      columnId: 'origin',
      compare: (a, b) => {
        const ta =
          a.originDirection === 'source'
            ? t(strings.explorer.twin_details.related_twins.origin_types.source)
            : t(
                strings.explorer.twin_details.related_twins.origin_types.target,
              );
        const tb =
          b.originDirection === 'source'
            ? t(strings.explorer.twin_details.related_twins.origin_types.source)
            : t(
                strings.explorer.twin_details.related_twins.origin_types.target,
              );
        return ta.localeCompare(tb);
      },
      renderCell: (item: TwinRelationship) => {
        return item.originDirection === 'source'
          ? t(strings.explorer.twin_details.related_twins.origin_types.source)
          : t(strings.explorer.twin_details.related_twins.origin_types.target);
      },
      renderHeaderCell: () => {
        return (
          <span className={styles.header}>
            {t(strings.explorer.twin_details.related_twins.relationship_type)}
          </span>
        );
      },
    }),
  ];
  if (state.data.state === 'error') {
    return (
      <ErrorMessage testId={TestIds.Error} message={state.data.errorMessage} />
    );
  }
  if (state.data.state === 'uncalled' || state.data.state === 'pending') {
    return <Loading testId={TestIds.Loading} />;
  }

  const items = state.data.state === 'success' ? state.data.data : [];

  if (items.length === 0) {
    return (
      <EmptyContents
        testId={TestIds.Empty}
        header={t(strings.explorer.twin_details.related_twins.empty)}
      />
    );
  }

  return (
    <>
      <DataGrid
        data-testid={TestIds.Main}
        items={items}
        columns={columns}
        sortable
        getRowId={(item) => item.id}
        className={styles.root}
      >
        <DataGridHeader>
          <DataGridRow>
            {({ renderHeaderCell }) => (
              <DataGridHeaderCell>{renderHeaderCell()}</DataGridHeaderCell>
            )}
          </DataGridRow>
        </DataGridHeader>
        <DataGridBody>
          {({ item, rowId }) => (
            <DataGridRow key={rowId}>
              {({ renderCell }) => (
                <DataGridCell>{renderCell(item)}</DataGridCell>
              )}
            </DataGridRow>
          )}
        </DataGridBody>
      </DataGrid>
    </>
  );
};
