import React from 'react';
import { FilterComboboxProps } from './FilterCombobox.types';
import { useFilterComboboxRender } from './FilterCombobox.render';
import { useClassNames } from './FilterCombobox.styles';
import { useFilterComboboxState } from './FilterCombobox.state';

const MODULE_NAME = 'FilterCombobox';

export const FilterCombobox: React.FC<FilterComboboxProps> = (props) => {
  // get state
  const state = useFilterComboboxState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useFilterComboboxRender({ state, styles });
};

FilterCombobox.displayName = MODULE_NAME;
