import React from 'react';
import { ExternalLinkProps } from './ExternalLink.types';
import { useExternalLinkRender } from './ExternalLink.render';
import { useClassNames } from './ExternalLink.styles';
import { useExternalLinkState } from './ExternalLink.state';

const MODULE_NAME = 'ExternalLink';

export const ExternalLink: React.FC<ExternalLinkProps> = (props) => {
  // get state
  const state = useExternalLinkState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useExternalLinkRender({ state, styles });
};

ExternalLink.displayName = MODULE_NAME;
