import React from 'react';
import { SchedulingSectionTimeFieldRenderProps } from './SchedulingSectionTimeField.types';
import { Add24Regular, Delete24Regular } from '@fluentui/react-icons';
import { Button, Field, Input, Label } from '@fluentui/react-components';
import { t, strings } from '@do/localization';

export const TestIds = {
  TimeInput: 'scheduling-section-time-input',
  RemoveTime: 'scheduling-section-remove-time-button',
  AddTime: 'scheduling-section-add-time-button',
};

export const useSchedulingSectionTimeFieldRender: React.FC<
  SchedulingSectionTimeFieldRenderProps
> = ({ state, styles }) => {
  return (
    <div className={styles.root}>
      <Label required weight='semibold' disabled={state.disabled}>
        {t(strings.scheduling.time_of_day_label)}
      </Label>
      {state.timesOfDay?.map((hour, i) => (
        <Field
          key={i + hour}
          required={state.required}
          aria-disabled={state.disabled}
          className={styles.row}
          validationMessage={
            state.disabled ? undefined : state.validationMessages[i]
          }
        >
          <Input
            data-testid={`${TestIds.TimeInput}-${i}`}
            type='time'
            value={hour}
            disabled={state.disabled}
            onChange={(_, { value }) => state.onUpdateTime(i, value)}
          />
          <Button
            appearance='transparent'
            onClick={() => state.onRemoveTime(i)}
            disabled={i === 0 || state.disabled}
            icon={<Delete24Regular />}
            data-testId={`${TestIds.RemoveTime}-${i}`}
          />
        </Field>
      ))}
      <Button
        appearance='transparent'
        onClick={state.onAddTime}
        disabled={state.disabled}
        className={styles.add_a_time_btn}
        data-testId={TestIds.AddTime}
      >
        <Add24Regular />
        {t(strings.scheduling.add_a_time_label)}
      </Button>
    </div>
  );
};
