import { InputProps, RadioGroupProps } from '@fluentui/react-components';
import { DropdownProps, SimpleDropdownItem } from '../dropdowns';
import { SchedulingSectionTimeFieldProps } from './components/SchedulingSectionTimeField';
import { DateTimeRangeInputProps } from '../DateTimeRangeInput';
import { SchedulingSectionTimeFieldRef } from './components/SchedulingSectionTimeField/SchedulingSectionTimeField.types';
import { DateTimeRangeInputRef } from '../DateTimeRangeInput/DateTimeRangeInput.types';

export interface SchedulingSectionRef {
  getIsValid(): boolean;
}

export enum UnitOfTime {
  Minute = 'minute',
  Hour = 'hour',
  Day = 'day',
  Week = 'week',
}

export enum DayOfWeek {
  Su = 'Su',
  Mo = 'Mo',
  Tu = 'Tu',
  We = 'We',
  Th = 'Th',
  Fr = 'Fr',
  Sa = 'Sa',
}

export type SchedulingSectionProps = SchedulingSectionState & {
  className?: string;
};

export type SchedulingSectionState = Pick<
  SchedulingSectionConsumerState,
  'hideEndDate'
> & {
  isScheduledRunOff: boolean;
  unitOfTime: UnitOfTime;
  minutes?: number;
  timesOfDay?: string[];
  daysOfWeek?: number;
  timeZone?: string;
  dateTime?: {
    start?: string;
    end?: string;
  };
  dateTimeRangeRef: React.MutableRefObject<DateTimeRangeInputRef | undefined>;
  timeFieldRef: React.MutableRefObject<
    SchedulingSectionTimeFieldRef | undefined
  >;
  daysOfWeekDropdownItems: SimpleDropdownItem[];
  unitOfTimeDropdownItems: SimpleDropdownItem[];
  timeZoneDropdownItems: SimpleDropdownItem[];
  dateTimeRangeProps: DateTimeRangeInputProps;
  daysOfWeekValidationMessage?: string;

  onUnitOfTimeChange: DropdownProps['setValue'];
  onMinutesChange: Required<InputProps>['onChange'];
  onTimesOfDayChange: SchedulingSectionTimeFieldProps['setTimesOfDay'];
  onDaysOfWeekChange: (i: number, checked: boolean) => void;
  onTimeZoneChange: DropdownProps['setValue'];
  onToggleScheduledRun: Required<RadioGroupProps>['onChange'];
  resetSchedulingState: () => SchedulingSectionOnChangeFields;
};

export interface SchedulingSectionConsumerState {
  hideEndDate?: boolean;
  initialState?: Partial<SchedulingSectionOnChangeFields>;
  onScheduleChange: SchedulingSectionOnScheduleChange;
}

export type SchedulingSectionOnScheduleChange<
  T extends SchedulingSectionOnChangeField = SchedulingSectionOnChangeField,
> = (change: SchedulingSectionChange<T>) => void;

type SchedulingSectionOnChangeField = keyof SchedulingSectionOnChangeFields;
export type SchedulingSectionOnChangeFields = Pick<
  SchedulingSectionState,
  | 'unitOfTime'
  | 'minutes'
  | 'timesOfDay'
  | 'daysOfWeek'
  | 'timeZone'
  | 'dateTime'
  | 'isScheduledRunOff'
>;

export type SchedulingSectionChange<
  T extends SchedulingSectionOnChangeField = SchedulingSectionOnChangeField,
> = {
  field: T;
  value: SchedulingSectionState[T];
};

export type SchedulingSectionRenderProps = {
  className?: string;
  state: SchedulingSectionState;
  styles: SchedulingSectionStyles;
};

export type SchedulingSectionClasses =
  | 'root'
  | 'input_field'
  | 'frequency_fields'
  | 'scheduled_run_field'
  | 'date_time_fields'
  | 'days_of_week_container'
  | 'days_of_week_field';
export type SchedulingSectionStyles = Record<SchedulingSectionClasses, string>;
