import { useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { LoadingProps, LoadingState } from './Loading.types';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'Loading.state';

export const useLoadingState = (props: LoadingProps): LoadingState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state: LoadingState = useMemo(() => {
    return {
      count: props.count ?? 5,
      size: props.size ?? 16,
      shape: props.shape ?? 'rectangle',
      invert: props.invert ?? false,
    };
  }, [props]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
