import React from 'react';
import { ISimpleDropdownProps } from './SimpleDropdown.types';
import { useSimpleDropdownRender } from './SimpleDropdown.render';
import { useSimpleDropdownState } from './SimpleDropdown.state';

const MODULE_NAME = 'SimpleDropDown';

export const SimpleDropdown: React.FC<ISimpleDropdownProps> = (props) => {
  // get state
  const state = useSimpleDropdownState(props);

  // render the item
  return useSimpleDropdownRender({ state });
};

SimpleDropdown.displayName = MODULE_NAME;
