import React from 'react';
import {
  DayOfWeek,
  SchedulingSectionRenderProps,
  UnitOfTime,
} from './SchedulingSection.types';
import {
  Field,
  Label,
  RadioGroup,
  Radio,
  Input,
  mergeClasses,
  Checkbox,
} from '@fluentui/react-components';
import { strings, t } from '@do/localization';
import { SimpleDropdown } from '../dropdowns';
import { DateTimeRangeInput } from '../DateTimeRangeInput';
import { SchedulingSectionTimeField } from './components';
import { SchedulingSectionScheduledRunValue } from './SchedulingSection.state';

export const TestIds = {
  EnableRadioButton: 'scheduling-section-enable-radio',
  DisableRadioButton: 'scheduling-section-disable-radio',
  TimeInput: 'scheduling-section-time-input',
  DayFrequencyInput: 'scheduling-section-day-frequency-input',
  WeekDayFrequencyCheckbox: 'scheduling-section-week-day-checkbox',
  FrequencyDropdown: 'scheduling-section-frequency-dropdown',
  MinuteHourFrequencyInput: 'scheduling-section-minute-hour-frequency-input',
  StartDateInput: 'scheduling-section-start-date-input',
  EndDateInput: 'scheduling-section-end-date-input',
};

export const useSchedulingSectionRender: React.FC<
  SchedulingSectionRenderProps
> = ({ className, state, styles }) => {
  return (
    <div className={mergeClasses(styles.root, className)}>
      <Field
        className={styles.scheduled_run_field}
        label={
          <Label weight='semibold'>
            {t(strings.scheduling.scheduled_run_label)}
          </Label>
        }
      >
        <RadioGroup
          value={
            state.isScheduledRunOff
              ? SchedulingSectionScheduledRunValue.off
              : SchedulingSectionScheduledRunValue.on
          }
          layout='horizontal'
          onChange={state.onToggleScheduledRun}
        >
          <Radio
            value='on'
            label={t(strings.common.on)}
            data-testid={TestIds.EnableRadioButton}
          />
          <Radio
            value='off'
            label={t(strings.common.off)}
            data-testid={TestIds.DisableRadioButton}
          />
        </RadioGroup>
      </Field>
      <div className={styles.frequency_fields}>
        <Field
          required
          aria-disabled={state.isScheduledRunOff}
          className={styles.input_field}
          label={
            <Label weight='semibold'>
              {t(strings.scheduling.unit_of_time_label)}
            </Label>
          }
        >
          <SimpleDropdown
            disabled={state.isScheduledRunOff}
            dataTestid={TestIds.FrequencyDropdown}
            ariaLabel={t(strings.scheduling.unit_of_time_dropdown_aria_label)}
            items={state.unitOfTimeDropdownItems}
            setValue={state.onUnitOfTimeChange}
            value={state.unitOfTime}
          />
        </Field>
        {(() => {
          switch (state.unitOfTime) {
            case UnitOfTime.Day:
              return (
                <SchedulingSectionTimeField
                  data-testid={TestIds.DayFrequencyInput}
                  ref={state.timeFieldRef}
                  required
                  disabled={state.isScheduledRunOff}
                  timesOfDay={state.timesOfDay}
                  setTimesOfDay={state.onTimesOfDayChange}
                />
              );
            case UnitOfTime.Week:
              return (
                <>
                  <Field
                    className={styles.days_of_week_field}
                    validationMessage={state.daysOfWeekValidationMessage}
                    label={
                      <Label
                        required
                        weight='semibold'
                        disabled={state.isScheduledRunOff}
                      >
                        {t(strings.scheduling.every)}
                      </Label>
                    }
                  >
                    <div className={styles.days_of_week_container}>
                      {Object.keys(DayOfWeek).map((day, i) => (
                        <Field
                          key={i + day}
                          aria-disabled={state.isScheduledRunOff}
                          className={styles.input_field}
                        >
                          <Checkbox
                            disabled={state.isScheduledRunOff}
                            data-testid={`${TestIds.WeekDayFrequencyCheckbox}-${day}`}
                            label={day}
                            checked={
                              (state.daysOfWeek ?? 0) & (1 << i) ? true : false
                            }
                            onChange={(_, { checked }) =>
                              state.onDaysOfWeekChange(i, Boolean(checked))
                            }
                          />
                        </Field>
                      ))}
                    </div>
                  </Field>
                  <SchedulingSectionTimeField
                    ref={state.timeFieldRef}
                    disabled={state.isScheduledRunOff}
                    timesOfDay={state.timesOfDay}
                    setTimesOfDay={state.onTimesOfDayChange}
                  />
                </>
              );
            default: {
              const minutes = state.minutes ?? 0;
              return (
                <Field
                  required
                  aria-disabled={state.isScheduledRunOff}
                  className={styles.input_field}
                  label={
                    <Label weight='semibold'>
                      {t(strings.scheduling.every)}
                    </Label>
                  }
                  hint={t(strings.scheduling.every_hint, {
                    context: state.unitOfTime,
                  })}
                >
                  <Input
                    data-testid={TestIds.MinuteHourFrequencyInput}
                    disabled={state.isScheduledRunOff}
                    onChange={state.onMinutesChange}
                    value={String(
                      state.unitOfTime === UnitOfTime.Hour && minutes > 0
                        ? minutes / 60
                        : minutes,
                    )}
                    type='number'
                    min={1}
                  />
                </Field>
              );
            }
          }
        })()}
      </div>
      <div className={styles.date_time_fields}>
        {state.hideEndDate ? (
          <Field
            label={t(strings.time_range_controls.start_date)}
            aria-disabled={state.isScheduledRunOff}
            className={styles.input_field}
          >
            <Input {...state.dateTimeRangeProps.startDateInputProps} />
          </Field>
        ) : (
          <DateTimeRangeInput
            ref={state.dateTimeRangeRef}
            {...state.dateTimeRangeProps}
          />
        )}
      </div>
      <Field
        label={
          <Label weight='semibold'>
            {t(strings.scheduling.time_zone_label)}
          </Label>
        }
      >
        <SimpleDropdown
          disabled={state.isScheduledRunOff}
          items={state.timeZoneDropdownItems}
          ariaLabel={t(strings.scheduling.time_zone_dropdown_aria_label)}
          value={state.timeZone}
          setValue={state.onTimeZoneChange}
        />
      </Field>
    </div>
  );
};
