import navigator from 'cytoscape-navigator';

/** input parameters to Graph */
export interface IGraphProps {
  data: GraphData;
  layout?: GraphLayout;
  cytoscapeStylesheet?: cytoscape.Stylesheet[];
}

/** state values used for rendering the component */
export type IGraphState = {
  elements: { data: GraphNode | GraphEdge }[];
  layout: GraphLayout;
  cytoscapeStylesheet: cytoscape.Stylesheet[];
  handleExpandCollapse: (twinId: string, action: NodeActions) => void;
};

/** the props send to the render function */
export type IGraphRenderProps = {
  state: IGraphState;
  styles: IGraphStyles;
};

/** the names of styling classes for Graph */
export type GraphClasses =
  | 'root'
  | 'spinner'
  | 'graph'
  | 'navigatorContainer'
  | 'navigator';
/** the computed styles object output from the styles hook */
export type IGraphStyles = Record<GraphClasses, string>;

export interface GraphNode {
  id: string;
  label: string;
}

export interface GraphEdge {
  id: string;
  label: string;
  source: string;
  target: string;
}

export interface GraphData {
  nodes: GraphNode[];
  edges: GraphEdge[];
}

export interface ExtendedCytoscape extends cytoscape.Core {
  navigator: (options?: navigator.Options | undefined) => navigator.Nav;
  panzoom: (options?: { [key: string]: any }) => any; // TODO: type this
  contextMenus: (
    options: string | contextMenus.MenuOptions,
  ) => contextMenus.ContextMenu;
}

export enum AllowedGraphLayouts {
  'random' = 'random',
  'grid' = 'grid',
  'circle' = 'circle',
  'concentric' = 'concentric',
  'breadthfirst' = 'breadthfirst',
  'dagre' = 'dagre',
  'fcose' = 'fcose',
  'cola' = 'cola',
}

export enum NodeActions {
  expand,
  collapse,
}

export type GraphLayout = { name: AllowedGraphLayouts; [key: string]: any };

export const GraphLayouts: Record<AllowedGraphLayouts, GraphLayout> = {
  random: {
    name: AllowedGraphLayouts.random,
  },
  grid: {
    name: AllowedGraphLayouts.grid,
  },
  circle: {
    name: AllowedGraphLayouts.circle,
  },
  concentric: {
    name: AllowedGraphLayouts.concentric,
    minNodeSpacing: 100,
  },
  breadthfirst: {
    name: AllowedGraphLayouts.breadthfirst,
  },
  dagre: {
    name: AllowedGraphLayouts.dagre,
  },
  fcose: {
    name: AllowedGraphLayouts.fcose,
  },
  cola: {
    name: AllowedGraphLayouts.cola,
  },
};

export const DefaultNavigationOptions: navigator.Options = {
  container: true, // html dom element
  viewLiveFramerate: 2, // set false to update graph pan only on drag end; set 0 to do it instantly; set a number (frames per second) to update not more than N times per second
  dblClickDelay: 200, // milliseconds
  removeCustomContainer: true, // destroy the container specified by user on plugin destroy
  rerenderDelay: 500, // ms to throttle rerender updates to the panzoom for performance
};

export const DefaultPanZoomOptions = {
  zoomFactor: 0.05, // zoom factor per zoom tick
  zoomDelay: 45, // how many ms between zoom ticks
  minZoom: 0.1, // min zoom level
  maxZoom: 10, // max zoom level
  fitPadding: 50, // padding when fitting
  panSpeed: 10, // how many ms in between pan ticks
  panDistance: 10, // max pan distance per tick
  panDragAreaSize: 75, // the length of the pan drag box in which the vector for panning is calculated (bigger = finer control of pan speed and direction)
  panMinPercentSpeed: 0.25, // the slowest speed we can pan by (as a percent of panSpeed)
  panInactiveArea: 8, // radius of inactive area in pan drag box
  panIndicatorMinOpacity: 0.5, // min opacity of pan indicator (the draggable nib); scales from this to 1.0
  zoomOnly: false, // a minimal version of the ui only with zooming (useful on systems with bad mousewheel resolution)
  fitSelector: undefined, // selector of elements to fit
  animateOnFit: function () {
    // whether to animate on fit
    return false;
  },
  fitAnimationDuration: 1000, // duration of animation on fit

  // icon class names
  sliderHandleIcon: 'fa fa-minus',
  zoomInIcon: 'fa fa-plus',
  zoomOutIcon: 'fa fa-minus',
  resetIcon: 'fa fa-expand',
};
