import React from 'react';
import { TimeSeriesChartProps } from './TimeSeriesChart.types';
import { useTimeSeriesChartRender } from './TimeSeriesChart.render';
import { useClassNames } from './TimeSeriesChart.styles';
import { useTimeSeriesChartState } from './TimeSeriesChart.state';

const MODULE_NAME = 'TimeSeriesChart';

export const TimeSeriesChart: React.FC<TimeSeriesChartProps> = (props) => {
  // get state
  const state = useTimeSeriesChartState(props);
  // get styles
  const styles = useClassNames();

  return useTimeSeriesChartRender({
    state,
    styles,
  });
};

TimeSeriesChart.displayName = MODULE_NAME;
