import React from 'react';
import { IEventsListRenderProps, TwinEvent } from './EventsList.types';
import {
  DataGrid,
  DataGridBody,
  DataGridCell,
  DataGridHeader,
  DataGridHeaderCell,
  DataGridRow,
  Image,
  TableColumnDefinition,
  createTableColumn,
} from '@fluentui/react-components';
import { DurationUnits, formatTimeInRelevantUnits } from '@do/core';
import { t, strings } from '@do/localization';
import { EventDetails } from './components/EventDetails';
import { EmptyContents, Loading, PageFooter } from '@do/components';
import DefaultSvg from '../../../../../../../../assets/Error.svg';

export enum EventListTestIds {
  Row = 'event-row',
  List = 'event-list',
}

export const EventTimestampCell: React.FC<{ timestamp: Date }> = ({
  timestamp,
}) => {
  const timeSince =
    timestamp.getTime() > 0 ? Date.now() - timestamp.getTime() : 0;
  const time = formatTimeInRelevantUnits(timeSince, DurationUnits.seconds);
  return (
    <>
      {t(strings.date.ago, {
        timeWithUnits: t(time.displayStringKey, {
          count: Math.round(time.value),
        }),
      })}
    </>
  );
};

export const useEventsListRender: React.FC<IEventsListRenderProps> = ({
  state,
  styles,
}) => {
  const columns: TableColumnDefinition<TwinEvent>[] = [
    createTableColumn({
      columnId: 'eventType',
      compare(a, b) {
        return a.eventType.localeCompare(b.eventType);
      },
      renderCell: (event: TwinEvent) => {
        return event.eventType;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader} data-testid='Event type'>
            {t(strings.explorer.twins.events.eventType)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'eventId',
      compare(a, b) {
        return a.eventId.localeCompare(b.eventId);
      },
      renderCell: (event: TwinEvent) => {
        return event.eventId;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader} data-testid='ID'>
            {t(strings.explorer.twins.events.eventId)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'eventSource',
      compare(a, b) {
        if (!a.source) return -1;
        else if (!b.source) return 1;
        return a.source.localeCompare(b.source);
      },
      renderCell: (event: TwinEvent) => {
        return event.source;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader} data-testid='Source'>
            {t(strings.explorer.twins.events.eventSource)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'eventMessage',
      compare(a, b) {
        if (!a.message) return -1;
        else if (!b.message) return 1;
        return a.message.localeCompare(b.message);
      },
      renderCell: (event: TwinEvent) => {
        return event.message;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader} data-testid='Message'>
            {t(strings.explorer.twins.events.eventMessage)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'eventSeverity',
      compare(a, b) {
        if (!a.severity) return -1;
        else if (!b.severity) return 1;
        return a.severity.localeCompare(b.severity);
      },
      renderCell: (event: TwinEvent) => {
        return event.severity;
      },
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader} data-testid='Severity'>
            {t(strings.explorer.twins.events.eventSeverity)}
          </span>
        );
      },
    }),
    createTableColumn({
      columnId: 'eventTimestamp',
      compare(a, b) {
        if (!a.sourceTimestamp) return -1;
        else if (!b.sourceTimestamp) return 1;
        return a.sourceTimestamp.getTime() - b.sourceTimestamp.getTime();
      },
      renderCell: (event: TwinEvent) => (
        <EventTimestampCell
          timestamp={event.sourceTimestamp}
          data-testid='TimestampAgo'
        />
      ),
      renderHeaderCell() {
        return (
          <span className={styles.columnHeader} data-testid='Timestamp'>
            {t(strings.explorer.twins.events.eventTimestamp)}
          </span>
        );
      },
    }),
  ];

  if (
    state.eventRequestState === 'pending' ||
    state.eventRequestState === 'uncalled'
  ) {
    return (
      <div className={styles.root}>
        <Loading />
      </div>
    );
  }

  if (state.eventRequestState === 'success') {
    if (!state.events.length) {
      return (
        <div className={styles.root}>
          <EmptyContents
            header={t(strings.explorer.twins.events.no_data_message)}
          />
        </div>
      );
    }

    return (
      <>
        {/* Event list */}
        <div className={styles.root} data-testid={EventListTestIds.List}>
          <DataGrid
            items={state.events}
            columns={columns}
            sortable
            getRowId={(event: TwinEvent) => event.eventId}
            className={styles.dataGrid}
          >
            <DataGridHeader>
              <DataGridRow>
                {({ renderHeaderCell }) => (
                  <DataGridHeaderCell>{renderHeaderCell()}</DataGridHeaderCell>
                )}
              </DataGridRow>
            </DataGridHeader>
            <DataGridBody<TwinEvent>>
              {({ item, rowId }) => (
                <DataGridRow<TwinEvent>
                  data-testid={EventListTestIds.Row}
                  key={rowId}
                  onClick={() => {
                    state.setSelectedEvent(item.eventId);
                  }}
                >
                  {({ renderCell }) => (
                    <DataGridCell>{renderCell(item)}</DataGridCell>
                  )}
                </DataGridRow>
              )}
            </DataGridBody>
          </DataGrid>
          <div className={styles.pageFooter}>
            <PageFooter
              initialPage={1}
              pageCount={state.pageCount}
              onPageChange={state.setCurrentPage}
            />
          </div>
        </div>
        {/* Event details */}
        {state.selectedEvent !== undefined ? (
          <EventDetails
            eventId={state.selectedEvent}
            onClose={() => {
              state.setSelectedEvent(undefined);
            }}
          />
        ) : null}
      </>
    );
  }

  return (
    <div className={styles.root}>
      <EmptyContents
        image={<Image src={DefaultSvg} role='presentation' />}
        header={t(strings.common.error)}
      />
    </div>
  );
};
