import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import {
  TimeSeriesControlsParameters,
  getLogger,
  useDebugLogging,
} from '@do/core';
import {
  LineChartControlsProps,
  LineChartControlsState,
} from './LineChartControls.types';
import {
  getDateTimeFormatted,
  useTimeSeriesContext,
} from '../../../../../../contexts/TimeSeriesContext';
import { ChartType } from '../../../../../../contexts/TimeSeriesContext.types';
import { DateTimeRangeInputProps } from '@do/components';
import { checkParamsChange } from '../../../../utils/TimeSeriesUtils';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'LineChartControls.state';

export const useLineChartControlsState = (
  props: LineChartControlsProps,
): LineChartControlsState => {
  // contexts
  const {
    binInterval,
    binSelectedOption,
    aggregationMethod,
    startTime,
    endTime,
    setAggregationMethod,
    setBinInterval,
    setStartTime,
    setEndTime,
  } = useTimeSeriesContext();

  // state
  const dateTimeRangeProps = useMemo<DateTimeRangeInputProps>(
    () => ({
      startDateInputProps: {
        value: startTime,
        onChange(event) {
          if (!event.target.value) {
            setStartTime(undefined);
          } else {
            setStartTime(getDateTimeFormatted(new Date(event.target.value)));
          }
        },
      },
      endDateInputProps: {
        value: endTime,
        onChange(event) {
          if (!event.target.value) {
            setEndTime(undefined);
          } else {
            setEndTime(getDateTimeFormatted(new Date(event.target.value)));
          }
        },
      },
    }),
    [startTime, endTime, setStartTime, setEndTime],
  );
  const [applyEnabled, setApplyEnabled] = useState<boolean>(false);

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);
  const previousOptions = useRef<TimeSeriesControlsParameters>({
    binWidth: Number(binInterval),
    aggType: aggregationMethod,
    dateTimeStart: startTime,
    dateTimeEnd: endTime,
  });

  // callbacks
  const onApplyClicked = useCallback(() => {
    previousOptions.current = {
      binWidth: Number(binInterval),
      aggType: aggregationMethod,
      dateTimeStart: startTime,
      dateTimeEnd: endTime,
    };
    setApplyEnabled(false);
    props.onApplyClicked();
  }, [aggregationMethod, binInterval, startTime, endTime, props]);

  // side effects
  useEffect(() => {
    const paramsChanged = checkParamsChange(
      {
        binWidth: Number(binInterval),
        aggType: aggregationMethod,
        dateTimeStart: startTime,
        dateTimeEnd: endTime,
      },
      previousOptions.current,
    );
    setApplyEnabled(paramsChanged);
  }, [binInterval, aggregationMethod, startTime, endTime]);

  // result
  const state: LineChartControlsState = useMemo(() => {
    return {
      selectedAggregationMethod: aggregationMethod,
      dateTimeRangeProps,
      selectedBinInterval: binSelectedOption,
      binInterval,
      onAggregationMethodChange: setAggregationMethod,
      onBinIntervalChange: setBinInterval,
      chartType: ChartType.line,
      applyEnabled: applyEnabled,
      onApplyClicked: onApplyClicked,
    };
  }, [
    aggregationMethod,
    binInterval,
    binSelectedOption,
    dateTimeRangeProps,
    setAggregationMethod,
    setBinInterval,
    applyEnabled,
    onApplyClicked,
  ]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
