import { useCallback, useMemo } from 'react';
import {
  DetailsPageRouteProps,
  ITwinDetailsPageProps,
  ITwinDetailsPageState,
} from './TwinDetailsPage.types';
import { useTwinsExplorerRouteProps } from '../../../hooks/routeProps/useTwinsExplorerRouteProps';
import { useNavigation } from '@do/core';

export const useTwinDetailsPageState = (
  _props: ITwinDetailsPageProps,
): ITwinDetailsPageState => {
  // contexts

  // state
  const routeProps = useTwinsExplorerRouteProps() as DetailsPageRouteProps;
  const navigation = useNavigation();
  // hooks

  // callbacks
  const onBackClick = useCallback(() => {
    navigation?.navigate({ path: routeProps.twinsPath });
  }, [navigation, routeProps.twinsPath]);

  const openInNewTab = useCallback(async () => {
    return navigation?.duplicateTab();
  }, [navigation]);
  // side effects

  // result
  const state: ITwinDetailsPageState = useMemo(() => {
    return {
      selectedPanel: routeProps.panelName || 'details',
      twinDetailsPath: routeProps.twinDetailsPath,
      twinId: routeProps.twinId,
      onBackClick,
      openInNewTab,
    };
  }, [
    onBackClick,
    openInNewTab,
    routeProps.panelName,
    routeProps.twinDetailsPath,
    routeProps.twinId,
  ]);

  return state;
};
