import React from 'react';
import { PageRenderProps, PageSlots } from './Page.types';
import { Toolbar, getSlots, mergeClasses } from '@fluentui/react-components';
import { ErrorBoundary } from '../ErrorBoundary';

export const usePageRender: React.FC<PageRenderProps> = ({
  state,
  styles,
  className,
  children,
  testId,
}) => {
  const { slots, slotProps } = getSlots<PageSlots>(state);
  return (
    <slots.root
      {...slotProps.root}
      className={mergeClasses(styles.root, className)}
      data-testid={testId ?? ''}
    >
      {slots.ribbon && (
        <Toolbar className={styles.ribbon}>{slotProps.ribbon.children}</Toolbar>
      )}
      <div
        className={mergeClasses(
          styles.main,
          slots.ribbon ? styles.mainWithRibbon : '',
        )}
      >
        <ErrorBoundary>{children}</ErrorBoundary>
      </div>
    </slots.root>
  );
};
