import React from 'react';
import {
  Button,
  Dropdown,
  Field,
  Input,
  Option,
  Tooltip,
} from '@fluentui/react-components';
import { Delete24Regular } from '@fluentui/react-icons';
import { strings, t } from '@do/localization';
import {
  FilterRowOptionDelimiter,
  FilterRowRenderProps,
} from './FilterRow.types';
import { LocalizedOption, ValueType } from '../Filters.types';

export enum TestIds {
  LogicalOperatorDropdown = 'filter-row-logical-operator-picker',
  PropertyDropdown = 'filter-row-property-picker',
  OperatorDropdown = 'filter-row-operator-picker',
  BooleanValueDropdown = 'filter-row-boolean-value-picker',
  ValueField = 'filter-row-value-field',
  DeleteButton = 'filter-row-delete-button',
  DateValueField = 'filter-row-date-value-field',
  TimeSeriesValueField = 'filter-row-time-series-value-field',
  TimeSeriesStartTimeField = 'filter-row-time-series-start-time-field',
  TimeSeriesEndTimeField = 'filter-row-time-series-end-time-field',
}

export const useFilterRowRender: React.FC<FilterRowRenderProps> = ({
  state,
  styles,
}) => {
  const stringOperators: LocalizedOption[] = [
    {
      name: 'eq',
      localizedString: t(strings.explorer.query_panel.operators.equals),
    },
    {
      name: 'contains',
      localizedString: t(strings.explorer.query_panel.operators.contains),
    },
  ];

  const numberOperators: LocalizedOption[] = [
    {
      name: 'eq',
      localizedString: t(strings.explorer.query_panel.operators.equals),
    },
    {
      name: 'gt',
      localizedString: t(strings.explorer.query_panel.operators.greater_than),
    },
    {
      name: 'lt',
      localizedString: t(strings.explorer.query_panel.operators.less_than),
    },
    {
      name: 'ge',
      localizedString: t(
        strings.explorer.query_panel.operators.greater_than_or_equals,
      ),
    },
    {
      name: 'le',
      localizedString: t(
        strings.explorer.query_panel.operators.less_than_or_equals,
      ),
    },
  ];

  const booleanOperators: LocalizedOption[] = [
    {
      name: 'eq',
      localizedString: t(strings.explorer.query_panel.operators.equals),
    },
  ];

  const logicalOperators: LocalizedOption[] = [
    {
      name: 'and',
      localizedString: t(strings.explorer.query_panel.logical_operators.and),
    },
    {
      name: 'or',
      localizedString: t(strings.explorer.query_panel.logical_operators.or),
    },
  ];

  const booleanValueOptions: LocalizedOption[] = [
    {
      name: 'true',
      localizedString: t(strings.explorer.query_panel.operators.true),
    },
    {
      name: 'false',
      localizedString: t(strings.explorer.query_panel.operators.false),
    },
  ];

  const dateOperators: LocalizedOption[] = [
    {
      name: 'gt',
      localizedString: t(strings.explorer.query_panel.operators.is_after),
    },
    {
      name: 'lt',
      localizedString: t(strings.explorer.query_panel.operators.is_before),
    },
  ];

  const valueTypeLocalizedOptionMapping: Record<ValueType, LocalizedOption> = {
    String: {
      name: 'String',
      localizedString: t(strings.explorer.query_panel.value_types.string),
    },
    Int: {
      name: 'Int',
      localizedString: t(strings.explorer.query_panel.value_types.integer),
    },
    Double: {
      name: 'Double',
      localizedString: t(strings.explorer.query_panel.value_types.double),
    },
    DateTime: {
      name: 'DateTime',
      localizedString: t(strings.explorer.query_panel.value_types.date_time),
    },
    Bool: {
      name: 'Bool',
      localizedString: t(strings.explorer.query_panel.value_types.boolean),
    },
  };

  const getOptions = () => {
    if (state.operatorOptions !== undefined) {
      return state.operatorOptions;
    }
    switch (state.propertyType) {
      case 'number':
        return numberOperators;
      case 'boolean':
        return booleanOperators;
      case 'date':
        return dateOperators;
      case 'timeSeries':
        return numberOperators;
      default:
        return stringOperators;
    }
  };

  const renderBooleanValueDropdown = () => {
    return (
      <Field
        aria-label={t(strings.explorer.query_panel.value_field_placeholder)}
        validationMessage={state.valueValidationMessage}
      >
        <Dropdown
          placeholder={t(strings.explorer.query_panel.value_field_placeholder)}
          value={state.value}
          onOptionSelect={(_ev, selection) =>
            state.setValue(state.rowIndex, `${selection.optionValue}`)
          }
          data-testid={TestIds.BooleanValueDropdown}
        >
          {booleanValueOptions.map((option) => (
            <Option key={option.name} value={option.name}>
              {option.localizedString}
            </Option>
          ))}
        </Dropdown>
      </Field>
    );
  };

  const renderDateValueDropdown = () => {
    return (
      <div className={styles.root}>
        <Field validationMessage={state.propertyValidationMessage}>
          <Input
            type='datetime-local'
            onChange={(_, data) => state.setValue(state.rowIndex, data.value)}
            value={state.value}
            data-testid={TestIds.DateValueField}
          />
        </Field>
      </div>
    );
  };

  const renderTimeSeriesValue = () => {
    return (
      <div className={styles.root}>
        <Field
          aria-label={t(strings.explorer.query_panel.value_field_placeholder)}
          validationMessage={state.valueValidationMessage}
        >
          <Input
            placeholder={t(
              strings.explorer.query_panel.value_field_placeholder,
            )}
            value={state.value}
            onChange={(_e, data) => {
              state.setValue(state.rowIndex, data.value);
            }}
            data-testid={TestIds.TimeSeriesValueField}
          />
        </Field>
      </div>
    );
  };

  const renderTimeSeriesDateTime = () => {
    return (
      <div className={styles.timeSeriesDateTime}>
        <div className={styles.newLine} />
        <Field>{t(strings.explorer.query_panel.time_series_between)}</Field>
        <Field>
          <Input
            type='date'
            onChange={(_e, data) => {
              if (state.setTimeSeriesStartTimeUTC) {
                state.setTimeSeriesStartTimeUTC(state.rowIndex, data.value);
              }
            }}
            value={state.timeSeriesStartTimeUTC}
            data-testid={TestIds.TimeSeriesStartTimeField}
          />
        </Field>
        <Field>{t(strings.explorer.query_panel.time_series_and)}</Field>
        <Field>
          <Input
            type='date'
            onChange={(_e, data) => {
              if (state.setTimeSeriesEndTimeUTC) {
                state.setTimeSeriesEndTimeUTC(state.rowIndex, data.value);
              }
            }}
            value={state.timeSeriesEndTimeUTC}
            data-testid={TestIds.TimeSeriesEndTimeField}
          />
        </Field>
      </div>
    );
  };

  return (
    <div
      className={
        state.propertyType === 'timeSeries' ? styles.timeSeriesRow : styles.root
      }
    >
      {state.rowIndex === 0 ? (
        <Field className={styles.firstFilterColumn}>
          {t(strings.explorer.query_panel.first_filter_prefix)}
        </Field>
      ) : (
        <Field aria-label='logical_operator'>
          <Dropdown
            className={styles.firstFilterColumn}
            placeholder={t(strings.explorer.query_panel.logical_operators.and)}
            value={
              logicalOperators.find((x) => x.name === state.logicalOperator)
                ?.localizedString ?? state.logicalOperator
            }
            onOptionSelect={(_, selection) => {
              if (state.setLogicalOperator) {
                state.setLogicalOperator(
                  state.rowIndex,
                  `${selection.optionValue}`,
                );
              }
            }}
            defaultValue='and'
            defaultSelectedOptions={['and']}
            listbox={{ className: styles.dropdownListbox }}
            data-testid={TestIds.LogicalOperatorDropdown}
          >
            {logicalOperators.map((option) => (
              <Option key={option.name} text={option.name}>
                {option.localizedString}
              </Option>
            ))}
          </Dropdown>
        </Field>
      )}

      <Field
        aria-label={t(strings.explorer.query_panel.property_picker_placeholder)}
        validationMessage={state.propertyValidationMessage}
      >
        <Dropdown
          placeholder={t(
            strings.explorer.query_panel.property_picker_placeholder,
          )}
          value={state.property}
          onOptionSelect={(_ev, selection) => {
            if (selection.optionValue) {
              const [name, valueType] = selection.optionValue.split(
                FilterRowOptionDelimiter,
              );
              state.setProperty(state.rowIndex, name, valueType as ValueType);
            }
          }}
          disabled={state.propertyOptions.length < 2}
          data-testid={TestIds.PropertyDropdown}
        >
          {state.propertyOptions.map((option) => (
            <Option
              key={option.name + FilterRowOptionDelimiter + option.valueType}
              text={option.name}
              value={option.name + FilterRowOptionDelimiter + option.valueType}
            >
              <div className={styles.propertyOption}>
                <div>{option.name}</div>
                <div className={styles.propertyType}>
                  {
                    valueTypeLocalizedOptionMapping[option.valueType]
                      .localizedString
                  }
                </div>
              </div>
            </Option>
          ))}
        </Dropdown>
      </Field>
      <Field
        aria-label={t(strings.explorer.query_panel.operator_picker_placeholder)}
        validationMessage={state.operatorValidationMessage}
      >
        <Dropdown
          placeholder={t(
            strings.explorer.query_panel.operator_picker_placeholder,
          )}
          value={
            getOptions().find((x) => x.name === state.operator)
              ?.localizedString ?? state.operator
          }
          onOptionSelect={(_ev, selection) =>
            state.setOperator(state.rowIndex, `${selection.optionValue}`)
          }
          data-testid={TestIds.OperatorDropdown}
        >
          {getOptions().map((option) => (
            <Option key={option.name} value={option.name}>
              {option.localizedString}
            </Option>
          ))}
        </Dropdown>
      </Field>
      {state.propertyType === 'boolean' && renderBooleanValueDropdown()}
      {state.propertyType === 'date' && renderDateValueDropdown()}
      {state.propertyType === 'timeSeries' && renderTimeSeriesValue()}
      {state.propertyType === 'timeSeries' && renderTimeSeriesDateTime()}
      {state.propertyType !== 'boolean' &&
        state.propertyType !== 'date' &&
        state.propertyType !== 'timeSeries' && (
          <Field
            aria-label={t(strings.explorer.query_panel.value_field_placeholder)}
            validationMessage={state.valueValidationMessage}
          >
            <Input
              placeholder={t(
                strings.explorer.query_panel.value_field_placeholder,
              )}
              value={state.value}
              onChange={(_e, data) => {
                state.setValue(state.rowIndex, data.value);
              }}
              data-testid={TestIds.ValueField}
            />
          </Field>
        )}
      <Tooltip
        content={t(strings.explorer.query_panel.delete_filter_row_tooltip)}
        relationship='label'
      >
        <Button
          icon={<Delete24Regular />}
          appearance='transparent'
          data-testid={TestIds.DeleteButton}
          onClick={() => state.deleteFilterRule(state.rowIndex)}
          disabled={!state.isDeleteRowEnabled}
        />
      </Tooltip>
    </div>
  );
};
