import React from 'react';
import { FilterComboboxRenderProps } from './FilterCombobox.types';
import { Combobox, mergeClasses } from '@fluentui/react-components';

export const useFilterComboboxRender: React.FC<FilterComboboxRenderProps> = ({
  state,
  styles,
}) => {
  const selectedListId = `${state.comboId}-selection`;
  const labelledBy =
    state.selectedOptions && state.selectedOptions.length > 0
      ? `${state.comboId} ${selectedListId}`
      : state.comboId;
  return (
    <Combobox
      data-testid={state.dataTestId}
      className={mergeClasses(styles.root, state.className)}
      aria-labelledby={labelledBy}
      multiselect={state.multiselect}
      onChange={state.onChange}
      onOptionSelect={state.onSelect}
      placeholder={state.placeholder}
      ref={state.comboboxInputRef}
      value={state.value}
      selectedOptions={state.selectedOptions}
      disabled={state.disabled}
      title={state.value}
    >
      {state.children}
    </Combobox>
  );
};
