import React from 'react';
import { ExpressionEditorContextMenuRenderProps } from './ExpressionEditorContextMenu.types';
import {
  MenuGroup,
  MenuGroupHeader,
  MenuItem,
  MenuList,
} from '@fluentui/react-components';
import { t, strings } from '@do/localization';

export const useExpressionEditorContextMenuRender: React.FC<
  ExpressionEditorContextMenuRenderProps
> = ({ state, styles }) => {
  return (
    <div className={styles.root}>
      {/* TODO: Handle state where formulaElements is null */}
      <MenuList>
        <MenuGroup>
          <MenuGroupHeader className={styles.groupHeader}>
            {t(
              strings.insights.expression_editor.context_menu.input_properties,
            )}
          </MenuGroupHeader>

          {state.inputProperties.map((property) => (
            // TODO: Add icons to menu items
            <MenuItem
              key={property.id}
              onClick={(event) => state.onMenuItemSelect(property, event)}
            >
              {property.displayName}
            </MenuItem>
          ))}
        </MenuGroup>
        <MenuGroup>
          <MenuGroupHeader className={styles.groupHeader}>
            {t(strings.insights.expression_editor.context_menu.functions)}
          </MenuGroupHeader>

          {state.functions.map((func) => (
            <MenuItem
              key={func.id}
              onClick={(event) => state.onMenuItemSelect(func, event)}
            >
              {func.displayName}
            </MenuItem>
          ))}
        </MenuGroup>
        <MenuGroup>
          <MenuGroupHeader className={styles.groupHeader}>
            {t(strings.insights.expression_editor.context_menu.expressions)}
          </MenuGroupHeader>

          {state.expressions.map((expression) => (
            <MenuItem
              key={expression.id}
              onClick={(event) => state.onMenuItemSelect(expression, event)}
            >
              {expression.displayName}
            </MenuItem>
          ))}
        </MenuGroup>
      </MenuList>
    </div>
  );
};
