import { produce } from 'immer';
import { getLogger } from '@do/core';
import {
  InsightsPageContextState,
  InsightsPageContextAction,
  InsightsPageContextActionType,
  EMPTY_FORMULA,
} from './InsightsPageContext.types';
import { strings, t } from '@do/localization';

export const debugLogging = true;
export const MODULE_NAME = 'InsightsPageContextReducer';

export const InsightsPageContextReducer: (
  draft: InsightsPageContextState,
  action: InsightsPageContextAction,
) => InsightsPageContextState = produce(
  (draft: InsightsPageContextState, action: InsightsPageContextAction) => {
    debugLogging &&
      getLogger().info(
        MODULE_NAME,
        `Updating InsightsPageContextReducer context ${action.type} with payload: `,
        (action as any).payload,
      );

    const getSelectedElement = () => {
      const formula = draft.currentFormula;
      const selectedElementId = formula.selectedElementId;
      return formula.elements.find((x) => x.id === selectedElementId) ?? null;
    };

    switch (action.type) {
      case InsightsPageContextActionType.SELECTED_NODE_SET: {
        draft.currentFormula.selectedElementId = action.payload.value;
        break;
      }
      case InsightsPageContextActionType.NAVIGATE_TO_BUILDER: {
        const { formulaId, twinId, twinDisplayName } = action.payload;
        draft.selectedTwinId = twinId;
        draft.selectedFormulaId = formulaId;
        draft.selectedTwinDisplayName = twinDisplayName;
        // should already have fetched the formula list for the selected twin
        if (draft.formulaList.state === 'success') {
          // find the formula in the collection
          const formula = draft.formulaList.data.find(
            (formula) => formula.id === formulaId,
          );
          // if found, set the current formula
          if (formula) {
            draft.currentFormula = {
              selectedElementId: null,
              selectedElement: null,
              ...formula,
            };
          } else {
            draft.currentFormula = {
              displayName: t(
                strings.insights.formula_builder.default_formula_name,
              ),
              elements: [],
              id: '',
              edges: [],
              selectedElementId: null,
              selectedElement: null,
            };
            if (formulaId) {
              getLogger().warn(
                MODULE_NAME,
                `Could not find formula with id ${formulaId}. Initializing to new formula`,
              );
            }
          }
          // navigate
          draft.page = 'builder';
        } else {
          getLogger().warn(
            MODULE_NAME,
            `Formula list not loaded. Cannot navigate to builder.`,
          );
        }
        break;
      }
      case InsightsPageContextActionType.NAVIGATE_TO_LIST: {
        draft.page = 'list';
        draft.selectedTwinId = action.payload.twinId;
        draft.selectedTwinDisplayName = action.payload.twinDisplayName;
        draft.selectedFormulaId = undefined;
        draft.currentFormula = { ...EMPTY_FORMULA };
        break;
      }
      case InsightsPageContextActionType.NAVIGATE_TO_EMPTY_STATE: {
        const { twinId } = action.payload;
        draft.page = 'empty';
        draft.selectedTwinId = twinId;
        draft.selectedFormulaId = undefined;
        draft.currentFormula = { ...EMPTY_FORMULA };
        break;
      }
      case InsightsPageContextActionType.FORMULA_LIST_LOADING: {
        draft.formulaList = {
          state: 'pending',
        };
        draft.selectedTwinId = action.payload.twinId;

        //clear current formula
        draft.currentFormula = { ...EMPTY_FORMULA };
        draft.selectedFormulaId = undefined;
        break;
      }
      case InsightsPageContextActionType.FORMULA_LIST_LOADED: {
        const { data } = action.payload;
        draft.formulaList = {
          state: 'success',
          data: data,
        };
        break;
      }
      case InsightsPageContextActionType.CURRENT_FORMULA_SET: {
        if (action.payload.formula) {
          draft.currentFormula = {
            ...action.payload.formula,
            selectedElementId: null,
            selectedElement: null,
          };
        } else {
          // If the payload formula is undefined, reset the current formula
          draft.currentFormula = { ...EMPTY_FORMULA };
        }
        draft.selectedFormulaId = action.payload.formula?.id;
        break;
      }
      case InsightsPageContextActionType.FORMULA_ADD_ELEMENT: {
        draft.currentFormula.elements.push(action.payload.element);
        break;
      }
      case InsightsPageContextActionType.FORMULA_REMOVE_ELEMENTS: {
        const formula = draft.currentFormula;
        if (action.payload.ids.length > 0) {
          formula.elements = formula.elements.filter(
            (x) => !action.payload.ids.includes(x.id),
          );
          formula.edges = formula.edges.filter(
            (x) =>
              !action.payload.ids.includes(x.source) &&
              !action.payload.ids.includes(x.target),
          );
        }
        break;
      }
      case InsightsPageContextActionType.FORMULA_UPDATE_ELEMENT: {
        const formula = draft.currentFormula;
        const element = action.payload.element;
        const elementIndex = formula.elements.findIndex(
          (x) => x.id === element.id,
        );
        if (elementIndex >= 0) {
          formula.elements[elementIndex] = element;
        } else {
          getLogger().warn(
            MODULE_NAME,
            `Cannot update element to formula. Element (id: ${element.id}) not found`,
          );
        }
        break;
      }
      case InsightsPageContextActionType.SELECTED_NODE_UPDATE_SOURCES: {
        const formula = draft.currentFormula;

        if (formula.selectedElementId) {
          const target = formula.selectedElementId;

          // Remove any old connections to this node
          const filteredEdges = formula.edges.filter(
            (x) => x.target !== target,
          );

          // Replace with the new edges
          action.payload.newSourceNodeIds.forEach((source) => {
            filteredEdges.push({
              source,
              target,
            });
          });

          formula.edges = filteredEdges;
        }
        break;
      }
      case InsightsPageContextActionType.FORMULA_SET_DISPLAY_NAME: {
        draft.currentFormula.displayName = action.payload.displayName;
        break;
      }
    }

    // update selectedElement
    draft.currentFormula.selectedElement = getSelectedElement();
  },
);
