import React from 'react';
import { ErrorBoundaryProps } from './ErrorBoundary.types';
import { useErrorBoundaryRender } from './ErrorBoundary.render';
import { useErrorBoundaryState } from './ErrorBoundary.state';

const MODULE_NAME = 'ErrorBoundary';

export const ErrorBoundary: React.FC<ErrorBoundaryProps> = (props) => {
  // get state
  const state = useErrorBoundaryState();

  // render the item
  return useErrorBoundaryRender({ state, children: props.children });
};

ErrorBoundary.displayName = MODULE_NAME;
