import React from 'react';
import { FilterRowProps } from './FilterRow.types';
import { useFilterRowRender } from './FilterRow.render';
import { useClassNames } from './FilterRow.styles';
import { useFilterRowState } from './FilterRow.state';

const MODULE_NAME = 'FilterRow';

export const FilterRow: React.FC<FilterRowProps> = (props) => {
  // get state
  const state = useFilterRowState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useFilterRowRender({ state, styles });
};

FilterRow.displayName = MODULE_NAME;
