import {
  TwinListRouteProps,
  TwinsPageRouteProps,
} from '../../components/twins';
import { DetailsPageRouteProps } from '../../components/twins/TwinDetailsPage';
import { TwinDetailsPanelNames } from '../../components/twins/TwinDetailsPage/TwinDetailsPage.types';
import { TwinPages } from '../../components/twins/TwinsPage/TwinsPage.types';
import { useExplorerRouteProps } from './useExplorerRouteProps';

export const useTwinsExplorerRouteProps = (): TwinsPageRouteProps => {
  const explorerRouteProps = useExplorerRouteProps();
  const routeProps = {
    ...explorerRouteProps,
    twinsPath: `${explorerRouteProps.explorerPath}/twins`,
  };
  if (!routeProps.pageName || routeProps.pageName.length === 0) {
    const pageName = routeProps.segments && routeProps.segments[4];
    routeProps.pageName = (pageName as TwinPages) || ('hierarchy' as TwinPages);
  }
  if (routeProps.pageName === 'details') {
    return {
      ...routeProps,
      twinId: routeProps.segments[5],
      panelName: (routeProps.segments[6] || 'details') as TwinDetailsPanelNames,
      twinDetailsPath: `${routeProps.twinsPath}/details/${routeProps.segments[5]}`,
    } as DetailsPageRouteProps;
  }
  if (routeProps.pageName === 'list') {
    return {
      ...routeProps,
      pageNumber: parseInt(routeProps.segments[5]) || 0,
    } as TwinListRouteProps;
  }
  return routeProps;
};
