import React from 'react';
import { TimeRangeInputRenderProps } from './DateTimeRangeInput.types';
import { strings, t } from '@do/localization';
import { Field, Input, Label, mergeClasses } from '@fluentui/react-components';

export const useDateTimeRangeInputRender: React.FC<
  TimeRangeInputRenderProps
> = ({ state, styles }) => {
  const inputContainerClassNames = mergeClasses(
    styles.inputContainer,
    state.layout === 'vertical' && styles.verticalLayout,
  );

  return (
    <div className={styles.root}>
      <div className={inputContainerClassNames}>
        <Field
          className={
            state.errorMessages.start ? undefined : styles.fieldWithoutError
          }
          validationMessage={
            state.startDateInputProps.disabled
              ? undefined
              : state.errorMessages.start
          }
          label={
            <Label weight='semibold'>
              {t(strings.time_range_controls.start_date)}
            </Label>
          }
          {...(state.startDateFieldProps ?? {})}
        >
          <Input type='datetime-local' {...state.startDateInputProps} />
        </Field>
        <Field
          className={
            state.errorMessages.end ? undefined : styles.fieldWithoutError
          }
          validationMessage={
            state.endDateInputProps.disabled
              ? undefined
              : state.errorMessages.end
          }
          label={
            <Label weight='semibold'>
              {t(strings.time_range_controls.end_date)}
            </Label>
          }
          {...(state.endDateFieldProps ?? {})}
        >
          <Input type='datetime-local' {...state.endDateInputProps} />
        </Field>
      </div>
    </div>
  );
};
