import React from 'react';
import { useRequiredFieldTextRender } from './RequiredFieldText.render';
import { useClassNames } from './RequiredFieldText.styles';
import { RequiredFieldTextProps } from './RequiredFieldText.types';

const MODULE_NAME = 'RequiredFieldText';

export const RequiredFieldText: React.FC<RequiredFieldTextProps> = (props) => {
  // get styles
  const styles = useClassNames();

  // render the item
  return useRequiredFieldTextRender({ ...props, styles });
};

RequiredFieldText.displayName = MODULE_NAME;
