import React from 'react';
import { ITwinDetailsNavRenderProps } from './TwinDetailsNav.types';
import { SelectTabData, Tab, TabList } from '@fluentui/react-components';
import { strings, t } from '@do/localization';

export enum TestIds {
  NavigationRoot = 'twin-details-nav',
  Details = 'twin-details-nav-details',
  TimeSeries = 'twin-details-nav-time-series',
  Events = 'twin-details-nav-events',
  RelatedTwins = 'twin-details-nav-related-twins',
}

export const useTwinDetailsNavRender: React.FC<ITwinDetailsNavRenderProps> = ({
  state,
  styles,
}) => {
  const onTabSelect = (_: any, data: SelectTabData) => {
    state.onTabChange(data.value as string);
  };
  return (
    <TabList
      data-testid={TestIds.NavigationRoot}
      selectedValue={state.selectedPanel}
      onTabSelect={onTabSelect}
      role='navigation'
      className={styles.root}
    >
      <Tab data-testid={TestIds.Details} value='details' className={styles.tab}>
        {t(strings.explorer.twin_details.nav.details)}
      </Tab>
      <Tab
        data-testid={TestIds.TimeSeries}
        value='timeSeries'
        className={styles.tab}
      >
        {t(strings.explorer.twin_details.nav.time_series)}
      </Tab>
      <Tab data-testid={TestIds.Events} value='events' className={styles.tab}>
        {t(strings.explorer.twin_details.nav.events)}
      </Tab>
      <Tab
        data-testid={TestIds.RelatedTwins}
        value='relatedTwins'
        className={styles.tab}
      >
        {t(strings.explorer.twin_details.nav.related_twins)}
      </Tab>
    </TabList>
  );
};
