import React from 'react';
import {
  DateTimeRangeInputProps,
  DateTimeRangeInputRef,
} from './DateTimeRangeInput.types';
import { useDateTimeRangeInputRender } from './DateTimeRangeInput.render';
import { useClassNames } from './DateTimeRangeInput.styles';
import { useDateTimeRangeInputState } from './DateTimeRangeInput.state';

const MODULE_NAME = 'DateTimeRangeInput';

export const DateTimeRangeInput = React.forwardRef<
  DateTimeRangeInputRef | undefined,
  DateTimeRangeInputProps
>((props, ref) => {
  // get state
  const state = useDateTimeRangeInputState(props, ref);

  // get styles
  const styles = useClassNames();

  // render the item
  return useDateTimeRangeInputRender({ state, styles });
});

DateTimeRangeInput.displayName = MODULE_NAME;
