import { useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { IconCardProps, IconCardState } from './IconCard.types';
import { resolveShorthand } from '@fluentui/react-components';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'IconCard.state';

export const useIconCardState = (props: IconCardProps): IconCardState => {
  // contexts

  // state
  const titleSlot = resolveShorthand(props.title);

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state = useMemo(() => {
    return {
      components: {
        root: 'div',
        title: props.titleAs ?? 'h3',
        image: 'div',
      },
      root: {},
      title: titleSlot,
      image: resolveShorthand(props.image),
      onClick: props.onClick,
      testId: props.testId,
    };
  }, [props.image, props.onClick, props.titleAs, titleSlot, props.testId]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state as IconCardState;
};
