import {
  RequiredFieldTextStyles,
  RequiredFieldTextClasses,
} from './RequiredFieldText.types';
import { makeStyles, shorthands, tokens } from '@fluentui/react-components';

export const useClassNames: () => RequiredFieldTextStyles =
  makeStyles<RequiredFieldTextClasses>({
    root: {
      ...shorthands.margin('8px', 0),
      display: 'inline-block',
    },
    star: {
      ...shorthands.padding(0, '3px'),
      color: tokens.colorPaletteRedForeground3, // same color as Fluent
      display: 'inline-block',
    },
    text: {
      fontWeight: tokens.fontWeightSemibold,
    },
  });
