import React from 'react';
import { PageProps } from './Page.types';
import { usePageRender } from './Page.render';
import { useClassNames } from './Page.styles';
import { usePageState } from './Page.state';

const MODULE_NAME = 'Page';

export const Page: React.FC<PageProps> = (props) => {
  // get state
  const state = usePageState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return usePageRender({
    state,
    styles,
    children: props.children,
    className: props.className,
    testId: props.testid,
  });
};

Page.displayName = MODULE_NAME;
