import { useCallback, useEffect, useMemo, useState } from 'react';
import { FeatureEvent } from '@trident/certified-events';
import {
  getLogger,
  useDebugLogging,
  useEventClient,
  TwinEvent,
  DataRequest,
} from '@do/core';
import { IEventsListProps, IEventsListState } from './EventsList.types';
import {
  ExploreTwinDetailsActivity,
  TwinVisualizationFeature,
} from '../../../../../../models/TwinVisualizationCertifiedEvents';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'EventsList.state';
const PAGE_SIZE = 10;

export const useEventsListState = (
  props: IEventsListProps,
): IEventsListState => {
  // contexts

  // state
  // TODO: change this page to an object with page# and cont. token based pagination defined by the endpoint
  const client = useEventClient(); // Don't put your hook results into state or they are never reevaluated
  const [eventsDataRequest, setEventsDataRequest] = useState<
    DataRequest<TwinEvent[]>
  >({
    state: 'pending', // TODO: SET TO UNCALLED
  });
  const [currentPage, setCurrentPage] = useState(1);
  const [pageCount, setPageCount] = useState(1);
  const [selectedEvent, setSelectedEvent] = useState<string | undefined>(
    undefined,
  );

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks
  const setEventsFromClient = useCallback(
    async (twinId: string, logConfig: Partial<FeatureEvent>) => {
      const results = await client?.getEventsByTwinId(twinId, logConfig);

      if (results && results.state === 'success') {
        setEventsDataRequest({
          state: 'success',
          data: results.data ?? [],
        });
        setPageCount(
          results.data?.length ? Math.ceil(results.data.length / PAGE_SIZE) : 1,
        );
        debugLogging &&
          getLogger().debug('Events loaded. {events}', MODULE_NAME, {
            events: results,
          });
      } else {
        setEventsDataRequest({
          state: 'error',
          errorCode: results?.errorCode || '500',
          errorMessage: results?.errorMessage || 'Unknown message',
        });
      }
    },
    [client, debugLogging],
  );

  // side effects
  useEffect(() => {
    if (client && props.twinId) {
      setEventsFromClient(props.twinId, {
        featureName: TwinVisualizationFeature.ExploreTwinDetails,
        activityName: ExploreTwinDetailsActivity.Event,
      });
    }
  }, [client, props.twinId, setEventsFromClient]);

  // result
  const state: IEventsListState = useMemo(() => {
    let shownEvents: TwinEvent[] = [];
    if (eventsDataRequest.state === 'success') {
      shownEvents = eventsDataRequest.data.slice(
        (currentPage - 1) * PAGE_SIZE,
        currentPage * PAGE_SIZE,
      );
    }

    return {
      events: shownEvents,
      eventRequestState: eventsDataRequest.state,
      selectedEvent,
      setSelectedEvent,
      page: currentPage,
      setCurrentPage,
      pageCount: pageCount,
    };
  }, [currentPage, eventsDataRequest, pageCount, selectedEvent]);

  debugLogging &&
    getLogger().debug('Render. {state}', MODULE_NAME, {
      state,
    });

  return state;
};
