import {
  TwinTimeSeriesListStyles,
  TwinTimeSeriesListClasses,
} from './TwinTimeSeriesList.types';
import { makeStyles, tokens } from '@fluentui/react-components';

export const useClassNames: () => TwinTimeSeriesListStyles =
  makeStyles<TwinTimeSeriesListClasses>({
    root: {
      width: '100%',
      height: '100%',
      display: 'grid',
    },
    columnHeader: {
      fontWeight: tokens.fontWeightSemibold,
    },
    pageFooter: {
      paddingTop: '24px',
      alignSelf: 'end',
      justifySelf: 'center',
    },
    dataGrid: {
      width: '100%',
    },
  });
