import React from 'react';
import { ITwinTimeSeriesPanelProps } from './TwinTimeSeriesPanel.types';
import { useTwinTimeSeriesPanelRender } from './TwinTimeSeriesPanel.render';
import { useClassNames } from './TwinTimeSeriesPanel.styles';
import { useTwinTimeSeriesPanelState } from './TwinTimeSeriesPanel.state';

const MODULE_NAME = 'TwinTimeSeriesPanel';

export const TwinTimeSeriesPanel: React.FC<ITwinTimeSeriesPanelProps> = (
  _props,
) => {
  // get state
  const state = useTwinTimeSeriesPanelState(_props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useTwinTimeSeriesPanelRender({ styles, state });
};

TwinTimeSeriesPanel.displayName = MODULE_NAME;
