import React from 'react';
import { LoadingRenderProps } from './Loading.types';
import {
  Skeleton,
  mergeClasses,
  SkeletonItem,
} from '@fluentui/react-components';

export enum TestIds {
  Main = 'loading-main',
}

export const useLoadingRender: React.FC<LoadingRenderProps> = ({
  state,
  styles,
}) => {
  return (
    <Skeleton
      data-testid={state.testId ?? TestIds.Main}
      aria-busy='true'
      className={mergeClasses(styles.root, state.invert ? styles.inverted : '')}
    >
      {Array.from({ length: state.count }, (_, i) => i).map((_, i) => (
        <SkeletonItem
          key={i}
          size={state.size}
          shape={state.shape}
          className={i % 2 ? styles.altItem : styles.item}
        />
      ))}
    </Skeleton>
  );
};
