export type FilterRowType =
  | 'string'
  | 'number'
  | 'boolean'
  | 'date'
  | 'timeSeries';

export type ValueType = 'String' | 'Int' | 'Double' | 'Bool' | 'DateTime';

export type FilterRowSource =
  | 'Twin'
  | 'Event'
  | 'DataPoint'
  | 'Attribute'
  | 'Label'
  | 'TimeSeries';

export const SqlTypeFilterRowTypeMapping: Record<ValueType, FilterRowType> = {
  String: 'string',
  Int: 'number',
  Double: 'number',
  Bool: 'boolean',
  DateTime: 'date',
};

// TODO: Should this extend localized option so we can localize option names?
// This would only work for the constant columns, not attributes or labels or time series data points
export interface Property {
  name: string;
  type: FilterRowType;
  valueType: ValueType;
  source?: FilterRowSource;
}

export interface LocalizedOption {
  name: string;
  localizedString: string;
}
